<template>
    <div class="row">
        <div class="col-lg-1"></div>
        <div class="col-lg-10">
            <div class="mt-2" v-if="clubs.length !== 0 || events.length !== 0 || communityEvents.length !== 0 || labs.length !== 0">
                <div v-if="clubs.length !== 0">
                    <div class="d-flex">
                        <div class="align-self-start">
                            <h2>
                                <p class="language" v-if="language === 'th'">ชุมนุมทางการ</p>
                                <p class="language" v-else>Official Clubs</p>
                            </h2>
                        </div>
                    </div>
                    <div class="row">
                        <the-card
                        v-for="info in clubs"
                        :key="info.id"
                        :id="info.id"
                        :logo="info.logo"
                        :banner="info.banner"
                        :name_th="info.name_th"
                        :name_en="info.name_en"
                        :description="info.description"
                        :status="info.status"
                        :url_id="info.url_id"
                        :language="language"
                        :comType="'club'">
                        </the-card>
                    </div>
                </div>
                <div v-if="comClubs.length !== 0">
                    <div class="d-flex">
                        <div class="align-self-start">
                            <h2>
                                <p class="language" v-if="language === 'th'">ชุมนุมไม่เป็นทางการ</p>
                                <p class="language" v-else>Unofficial Clubs</p>
                            </h2>
                        </div>
                    </div>
                    <div class="row">
                        <the-card
                        v-for="info in comClubs"
                        :key="info.id"
                        :id="info.id"
                        :logo="info.logo"
                        :banner="info.banner"
                        :name_th="info.name_th"
                        :name_en="info.name_en"
                        :description="info.description"
                        :status="info.status"
                        :url_id="info.url_id"
                        :language="language"
                        :comType="'comclub'">
                        </the-card>
                    </div>
                </div>
                <div v-if="events.length !== 0">
                    <div class="d-flex">
                        <div class="align-self-start">
                            <h2>
                                <p class="language" v-if="language === 'th'">กิจกรรม</p>
                                <p class="language" v-else>Events</p>
                            </h2>
                        </div>
                    </div>
                    <div class="row">
                        <the-card
                        v-for="info in events"
                        :key="info.id"
                        :id="info.id"
                        :logo="info.logo"
                        :banner="info.banner"
                        :name_th="info.name_th"
                        :name_en="info.name_en"
                        :description="info.description"
                        :url_id="info.url_id"
                        :accept="info.is_accepting_requests"
                        :cancel="info.is_cancelled"
                        :approve="info.is_approved"
                        :end_date="info.end_date"
                        :language="language"
                        :comType="'event'">
                        </the-card>
                    </div>
                </div>
                <div v-if="communityEvents.length !== 0">
                    <h2>
                        <p class="language" v-if="language === 'th'">กิจกรรมชุมนุม / กิจกรรมห้องปฏิบัติการ</p>
                        <p class="language" v-else>Community Events</p>
                    </h2>
                    <div class="row">
                        <the-card
                        v-for="info in communityEvents"
                        :key="info.id"
                        :id="info.id"
                        :logo="info.logo"
                        :banner="info.banner"
                        :name_th="info.name_th"
                        :name_en="info.name_en"
                        :description="info.description"
                        :url_id="info.url_id"
                        :accept="info.is_accepting_requests"
                        :create_by="info.meta.created_under_name_en"
                        :cancel="info.is_cancelled"
                        :approve="info.is_approved"
                        :end_date="info.end_date"
                        :language="language"
                        :comType="'comevent'">
                        </the-card>
                    </div>
                </div>
                <div v-if="labs.length !== 0">
                    <div class="d-flex">
                            <div class="align-self-start">
                                <h2>
                                    <p class="language" v-if="language === 'th'">ห้องปฏิบัติการ</p>
                                    <p class="language" v-else>Labs</p>
                                </h2>
                            </div>
                        </div>
                    <div class="row">
                        <the-card
                        v-for="info in labs"
                        :key="info.id"
                        :name_th="info.name_th"
                        :name_en="info.name_en"
                        :logo="info.logo"
                        :banner="info.banner"
                        :description="info.description"
                        :status="info.status"
                        :url_id="info.url_id"
                        :language="language"
                        :comType="'lab'">
                        </the-card>
                    </div>
                </div>
            </div>
            <div class="row" v-else>
                <div class="col-lg-md"></div>
                <div class="col-lg-md" v-if="loadingCheck === true">
                    <div class="d-flex justify-content-center">
                        <div class="spinner-border m-5" role="status">
                            <span class="sr-only">Loading...</span>
                        </div>
                    </div>
                    <div class="d-flex justify-content-center">
                        <h3>Loading...</h3>
                    </div>
                </div>
                <div class="col-lg-md" v-else>
                    <div class="d-flex justify-content-center mt-4">
                        <h2>{{ errorMessage }}</h2>
                    </div>
                </div>
                <div class="col-lg-md"></div>
            </div>
        </div>
        <div class="col-lg-1"></div>
    </div>
</template>

<script>
import TheCard from '../card/TheCard.vue';
import axios from 'axios';
import { $http } from '../../assets/variables.js';

export default {
    data() {
        return {
            clubs: [],
            comClubs: [],
            events: '',
            communityEvents: '',
            labs: '',
            loadingCheck: true,
            token: '',
            language: '',
            text: '',
        }
    },
    methods: {
        getData() {
            this.token = window.$cookies.get("token");
            if(this.token === null){
                this.token = window.$cookies.get("itkmitlcommunitytokenbrowser");
            }
            this.language = window.$cookies.get('itkmitlcommunitylanguage');
            this.text = this.$route.query.text;
            axios.get($http + 'api/community/club?search=' + this.text, {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                var i;
                for(i in response.data){
                    if(response.data[i].is_official){
                        this.clubs.push(response.data[i])
                    } else {
                        this.comClubs.push(response.data[i])
                    }
                }
                setTimeout(function(){this.loadingCheck = false;}, 10000);
            }).catch((err) => {
                this.loadingCheck = false;
                console.log(err);
                if(this.language === 'th'){
                    this.errorMessage = "เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่"
                } else {
                    this.errorMessage = "Data fetching error, please try again.";
                }
            });
            axios.get($http + 'api/community/event?exclude_community_events=1&search=' + this.text, {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.events = response.data
                setTimeout(function(){this.loadingCheck = false;}, 10000);
            }).catch((err) => {
                this.loadingCheck = false;
                console.log(err);
                if(this.language === 'th'){
                    this.errorMessage = "เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่"
                } else {
                    this.errorMessage = "Data fetching error, please try again.";
                }
            });
            axios.get($http + 'api/community/event/community?search=' + this.text, {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.communityEvents = response.data
                setTimeout(function(){this.loadingCheck = false;}, 10000);
            }).catch((err) => {
                this.loadingCheck = false;
                console.log(err);
                if(this.language === 'th'){
                    this.errorMessage = "เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่"
                } else {
                    this.errorMessage = "Data fetching error, please try again.";
                }
            });
            axios.get($http + 'api/community/lab?search=' + this.text, {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.labs = response.data
                setTimeout(function(){this.loadingCheck = false;}, 10000);
            }).catch((err) => {
                this.loadingCheck = false;
                console.log(err);
                if(this.language === 'th'){
                    this.errorMessage = "เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่"
                } else {
                    this.errorMessage = "Data fetching error, please try again.";
                }
            });
        }
    },
    components: {
        TheCard,
    },
    computed: {
        errorMessage() {
            if(this.clubs.length === 0 && this.events.length === 0 && this.communityEvents.length === 0 && this.labs.length === 0) {
                if(this.language === 'th'){
                    return 'ไม่มีข้อมูลที่ตรงกับที่ค้นหา';
                } else {
                    return 'No data found from what you search.';
                }
            } else {
                return '';
            }
        }
    },
    async mounted() {
        this.getData();
    },
    watch: {
        $route(){
            this.$router.go();
        }
    },
}
</script>
<style>
</style>
<style scoped src="../../assets/main.css"></style>