<template>
    <div class="row" id="main" v-if="!loading">
        <div class="col-lg"></div>
        <div class="col-lg-10 mt-3">
            <div v-if="errorMessage !== ''" class="d-flex justify-content-center mt-4">
                <h2>{{ errorMessage }}</h2>
            </div>
            <div class="d-flex ml-2 mb-2">
                <div class="align-self-start">
                    <h2>
                        <p class="language" v-if="language === 'th'">ห้องปฏิบัติการ</p>
                        <p class="language" v-else>Labs</p>
                    </h2>
                </div>
                <div class="flex-fill"></div>
                <div class="align-self-end">
                    <button v-if="checkCreateButton" @click="goLabForm" class="btn btn-warning">Create Lab</button>
                </div>
            </div>
            <div class="d-flex justify-content-center" v-if="infos.data.length === 0">
                <h2>
                    <p class="language" v-if="language === 'th'">ยังไม่มีห้องปฎิบัติการ</p>
                    <p class="language" v-else>No Labs yet</p>
                </h2>
            </div>
            <div class="row d-flex justify-content-start">
                <the-card
                v-for="info in infos.data"
                :key="info.id"
                :name_th="info.name_th"
                :name_en="info.name_en"
                :logo="info.logo"
                :banner="info.banner"
                :description="info.description"
                :status="info.status"
                :url_id="info.url_id"
                :language="language"
                :comType="'lab'">
                </the-card>
            </div>
        </div>
        <div class="col-lg"></div>
    </div>
    <div class="row" v-else>
        <div class="col-lg"></div>
        <div class="col-lg loading">
            <div class="d-flex justify-content-center">
                <div class="spinner-border m-5" role="status">
                    <span class="sr-only">Loading...</span>
                </div>
            </div>
            <div class="d-flex justify-content-center">
                <h3>Loading...</h3>
            </div>
        </div>
        <div class="col-lg"></div>
    </div>
</template>

<script>
import TheCard from '../card/TheCard.vue';
import axios from 'axios';
import { $http } from '../../assets/variables.js';

export default {
    data() {
        return {
            infos: '',
            token: '',
            language: '',
            myinfo: '',
            errorMessage: '',
            loading: true,
        }
    },
    components: {
        TheCard,
    },
    methods: {
        goLabForm() {
            this.$router.push('/labform/create');
        },
    },
    async mounted() {
        this.token = window.$cookies.get("token");
        if(this.token === null){
            this.token = window.$cookies.get("itkmitlcommunitytokenbrowser");
        }
        this.language = window.$cookies.get('itkmitlcommunitylanguage');
        axios.get($http + 'api/community/lab/', {
            headers: {
                Authorization: this.token
            }
        }).then((response) => {
            this.infos = response;
            this.loading = false;
        }).catch((err) => {
            console.log(err.response)
            if(this.language === 'th'){
                this.errorMessage = "เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่"
            } else {
                this.errorMessage = "Data fetching error, please try again.";
            }
            this.loading = false;
        });
        if (this.token !== null && this.token !== undefined){
            axios.get($http + 'api/user/user/me/', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.myinfo = response
            }).catch((err) => {
                console.log(err)
            });
        }
    },
    // watch: {
    //     $route() {
    //         this.token = window.$cookies.get("token");
    //         try {
    //             let response = axios.get($http + 'api/community/lab/', {
    //                 headers: {
    //                     Authorization: this.token
    //                 }
    //             }).then(
    //                 this.infos = response
    //             );
    //         } catch (err) {
    //             (console.log(err))
    //         }
    //         try {
    //             let response2 = axios.get($http + 'api/user/user/me/', {
    //                 headers: {
    //                     Authorization: this.token
    //                 }
    //             }).then(
    //             this.myinfo = response2
    //             );
    //         } catch (err) {
    //             (console.log(err))
    //             this.myinfo = '';
    //         }
    //     }
    // },
    computed: {
        checkCreateButton(){
            if (this.myinfo) {
                if (this.myinfo.data.user_group === "lecturer") {
                    return true;
                } else {
                    return false;
                }
            } else {
                return false;
            }
        }
    }
}
</script>
<style>
</style>
<style scoped src="../../assets/main.css"></style>