<template>
    <div>
        <div class="d-flex justify-content-center">
            <div class="spinner-border m-5" role="status">
                <span class="sr-only">Loading...</span>
            </div>
        </div>
        <div class="d-flex justify-content-center">
            <h1>
                <p class="language" v-if="language === 'th'">กำลังออกจากระบบ</p>
                <p class="language" v-else>Logging Out</p>
            </h1>
        </div>
    </div>
</template>
<script>
export default {
    data () {
        return {
            checktoken: null,
            language: '',
        };
    },
    methods: {
        loggingOut() {
            this.checktoken = window.$cookies.get('token');
            if(this.checktoken === null){
                this.checktoken = window.$cookies.get("itkmitlcommunitytokenbrowser");
            }
            this.language = window.$cookies.get('itkmitlcommunitylanguage');
            window.$cookies.remove("token")
            window.$cookies.remove("itkmitlcommunitytokenbrowser");
            this.checktoken = null;
            this.$router.push('/clubs')
        },
    },
    mounted() {
        this.loggingOut();
    },
    watch: {
        $route() {
            this.loggingOut();
        }
    },
}
</script>
<style scoped>
h1 {
    text-align: center;
    display: inline-block;
    vertical-align: middle;
}
.center {
    position: absolute;
    top: 20;
    left: 0;
    right: 0; 
    left: 0; 
    text-align: center; 
    border: 1px solid #ccc; 
    height: 100%; width: 100%; 
    vertical-align: middle;
}
</style>
