<template>
    <div class="row background">
            <div class="col-lg-3"></div>
            <div class="col-lg-6 mt-4 mb-4 not-bg">
                <!-- PC -->
                <div class="d-none d-lg-block">
                    <div class="row">
                        <div class="col d-flex justify-content-start ml-2 mb-4 mt-4">
                            <div class="d-flex">
                                <div class="form-outline align-self-start">
                                    <input v-if="language === 'th'" class="form-control" id="searchEvent" rows="4" v-model="searchEvent" placeholder="ค้นหา..." />
                                    <input v-else class="form-control" id="searchEvent" rows="4" v-model="searchEvent" placeholder="Search..." />
                                </div>
                                <div class="col-md align-self-end">
                                    <button @click="searchEventData(info.id)" class="btn btn-primary"><i class="fas fa-search"></i></button>
                                </div>
                            </div>
                        </div>
                        <div class="col d-flex justify-content-end mb-4 mt-4">
                            <router-link :to="toCreateCommunityEvent(info.id)">
                                <button v-if="info.meta.own_membership_position > 0" class="btn btn-warning">
                                    <p class="language" v-if="language === 'th'">สร้างกิจกรรมชุมนุม / ห้องปฏิบัติการ</p>
                                    <p class="language" v-else>Create Community Event</p>
                                </button>
                            </router-link>
                        </div>
                    </div>
                </div>
                <!-- Mobile -->
                <div class="d-lg-none">
                    <div class="d-flex justify-content-center ml-2 mb-4 mt-4">
                        <div class="d-flex">
                            <div class="form-outline align-self-start">
                                <input v-if="language === 'th'" class="form-control" id="searchEvent" rows="4" v-model="searchEvent" placeholder="ค้นหา..." />
                                <input v-else class="form-control" id="searchEvent" rows="4" v-model="searchEvent" placeholder="Search..." />
                            </div>
                            <div class="col-md align-self-end">
                                <button @click="searchEventData(info.id)" class="btn btn-primary"><i class="fas fa-search"></i></button>
                            </div>
                        </div>
                    </div>
                    <div class="d-flex justify-content-center mb-4 mt-4">
                        <router-link :to="toCreateCommunityEvent(info.id)">
                            <button v-if="info.meta.own_membership_position > 0" class="btn btn-warning">
                                <p class="language" v-if="language === 'th'">สร้างกิจกรรมชุมนุม / ห้องปฏิบัติการ</p>
                                <p class="language" v-else>Create Community Event</p>
                            </button>
                        </router-link>
                    </div>
                </div>
                
                <div v-if="communityevents !== 'Loading.....' && communityevents.length !== 0" class="row d-flex justify-content-center">
                    <simple-card class="mb-4"
                        v-for="event in communityevents"
                        :key="event.id"
                        :id="event.id"
                        :logo="event.logo"
                        :banner="event.banner"
                        :name_th="event.name_th"
                        :name_en="event.name_en"
                        :url_id="event.url_id"
                        :cancel="event.is_cancelled"
                        :approve="event.is_approved"
                        :end_date="event.end_date"
                        :position="'none'"
                        :comType="'comeventcheck'"
                        :create_by="event.meta.created_under_name_en"
                        :language="language"
                    ></simple-card>
                </div>
                <div class="mb-4" v-else-if="communityevents === 'Loading.....'">
                    <div class="d-flex justify-content-center">
                        <div class="spinner-border m-5" role="status">
                            <span class="sr-only">Loading...</span>
                        </div>
                    </div>
                    <div class="d-flex justify-content-center">
                        <h3>{{ communityevents }}</h3>
                    </div>
                </div>
                <div v-else class="d-flex justify-content-center mb-4">
                    {{ loadingResult }}
                </div>
            </div>
            <div class="col-lg"></div>
        </div>
</template>
<script>
import axios from 'axios';
import SimpleCard from '../../card/SimpleCard.vue';
import { $http } from '../../../assets/variables.js';

export default {
    components: {
        SimpleCard
    },
    props: ['token', 'info', 'language'],
    data() {
        return {
            communityevents: 'Loading.....',
            searchEvent: '',
            loadingResult: 'No events have been created yet.',
        }
    },
    methods: {
        searchEventData(id){
            this.communityevents = 'Loading.....';
            axios.get($http + 'api/community/event/community?created_under=' + id + '&search=' + this.searchEvent, {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.communityevents = response.data
            }).catch((err) => {
                console.log(err);
            });
            if (this.language === 'th') {
                this.loadingResult = 'ไม่มีกิจกรรมที่ตรงกับที่คุณหา';
            } else {
                this.loadingResult = 'No events match your search.';
            }
        },
        toCreateCommunityEvent(id) {
            return '/communityeventform/' + id;
        },
        getCommunityEventLink(id, url){
            if(url === null){
                return '/event/' + id;
            } else {
                return '/event/' + url;
            }
        },
        getCommunityEventsData(id) {
            axios.get($http + 'api/community/event/community?created_under=' + id, {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.communityevents = response.data
                if(this.communityevents.length === 0){
                    if (this.language === 'th') {
                        this.loadingResult = 'ยังไม่มีกิจกรรมถูกสร้างขึ้น';
                    } else {
                        this.loadingResult = 'No events have been created yet.'
                    }
                }
            }).catch((err) => {
                console.log(err);
            });
        },
    },
    async mounted(){
        this.getCommunityEventsData(this.info.id);
    },
}
</script>
<style scoped src="../../../assets/main.css"></style>
<style scoped>
.event-logo{
    margin-left: 0px;
    margin-right: 5px;;
    margin-top: 3px;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    border-width: 2px;
    overflow: hidden;
}
.event-logo img{
    margin: 0 auto;
    width: 100%;
}
.event{
    height: 50px;
    border-style: solid;
}
.event-name{
    text-decoration: none;
    color: black;
    font-size: 20px;
    margin-top: 6px;
}
a{
    text-decoration: none;
}
</style>