<template>
    <div class="row" v-if="!loading">
        <div class="col-lg"></div>
        <div class="col-lg-10 mt-2">
            <div v-if="errorMessage !== ''" class="d-flex justify-content-center mt-4">
                <h2>{{ errorMessage }}</h2>
            </div>
            <div class="d-flex mt-2 mb-3">
                <div class="align-self-start ml-2">
                    <h2 class="toggle d-flex" @click="officialToggle"
                    aria-expanded="false" aria-controls="officialclub">
                    <p class="language" v-if="language === 'th'">ชุมนุมทางการ</p>
                    <p class="language" v-else>Official Clubs</p>&nbsp;
                    <i v-if="officialtog" class="fas fa-caret-down"></i>
                    <i v-else class="fas fa-caret-right"></i></h2>
                </div>
                <div class="flex-fill"></div>
                <div class="d-flex justify-content-end">
                    <div>
                        <button v-if="checkCreateButton" @click="goClubForm" class="btn btn-warning">
                            <p class="language" v-if="language === 'th'">สร้างชุมนุม</p>
                            <p class="language" v-else>Create Club</p>
                        </button>
                    </div>
                </div>
            </div>
            <div class="d-flex justify-content-center" v-if="officialinfos.length === 0">
                <h2>
                    <p class="language" v-if="language === 'th'">ยังไม่มีชุมนุม</p>
                    <p class="language" v-else>No Clubs yet</p>
                </h2>
            </div>
            <div class="collapse show" id="officialclub">
                <div class="row d-flex justify-content-start">
                    <the-card
                    v-for="info in officialinfos"
                    :key="info.id"
                    :id="info.id"
                    :logo="info.logo"
                    :banner="info.banner"
                    :name_th="info.name_th"
                    :name_en="info.name_en"
                    :description="info.description"
                    :status="info.status"
                    :url_id="info.url_id"
                    :language="language"
                    :comType="'club'">
                    </the-card>
                </div>
            </div>
            <div v-if="unofficialinfos.length !== 0">
                <h2 class="ml-2 mb-4 toggle d-flex" @click="unofficialToggle"
                aria-expanded="false" aria-controls="unofficialclub">
                    <p class="language" v-if="language === 'th'">ชุมนุมไม่เป็นทางการ</p>
                    <p class="language" v-else>Unofficial Clubs</p>&nbsp;
                    <i v-if="unofficialtog" class="fas fa-caret-down"></i>
                    <i v-else class="fas fa-caret-right"></i>
                </h2>
                <div class="unofficial collapse show" id="unofficialclub">
                    <div class="row d-flex justify-content-start">
                        <the-card
                        v-for="info in unofficialinfos"
                        :key="info.id"
                        :id="info.id"
                        :logo="info.logo"
                        :banner="info.banner"
                        :name_th="info.name_th"
                        :name_en="info.name_en"
                        :description="info.description"
                        :status="info.status"
                        :url_id="info.url_id"
                        :language="language"
                        :comType="'comclub'">
                        </the-card>
                    </div>
                </div>
            </div>
            <div v-if="disbandedinfos.length !== 0">
                <h2 class="ml-2 mb-4 toggle d-flex" @click="disbandToggle"
                aria-expanded="false" aria-controls="disbanded">
                    <p class="language" v-if="language === 'th'">ชุมนุมที่ยกเลิก</p>
                    <p class="language" v-else>Disbanded Clubs</p>&nbsp;
                    <i v-if="disbandtog" class="fas fa-caret-down"></i>
                    <i v-else class="fas fa-caret-right"></i>
                </h2>
                <div class="collapse show" id="disbanded">
                    <div class="row d-flex justify-content-start">
                        <disbanded-card
                        v-for="info in disbandedinfos"
                        :key="info.id"
                        :id="info.id"
                        :logo="info.logo"
                        :banner="info.banner"
                        :name_th="info.name_th"
                        :name_en="info.name_en"
                        :description="info.description"
                        :status="info.status"
                        :url_id="info.url_id"
                        :language="language"
                        :comType="'club'"
                        :disband="true">
                        </disbanded-card>
                    </div>
                </div>
            </div>
        </div>
        <div class="col-lg"></div>
    </div>
    <div class="row" v-else>
        <div class="col-lg"></div>
        <div class="col-lg loading">
            <div class="d-flex justify-content-center">
                <div class="spinner-border m-5" role="status">
                    <span class="sr-only">Loading...</span>
                </div>
            </div>
            <div class="d-flex justify-content-center">
                <h3>Loading...</h3>
            </div>
        </div>
        <div class="col-lg"></div>
    </div>
</template>

<script>
import TheCard from '../card/TheCard.vue';
import DisbandedCard from '../card/DisbandedCard.vue';
import axios from 'axios';
import { $http } from '../../assets/variables.js';

export default {
    data() {
        return {
            infos: '',
            officialinfos: '',
            unofficialinfos: '',
            disbandedinfos: '',
            token: '',
            language: '',
            myinfos: '',
            errorMessage: '',
            officialtog: false,
            unofficialtog: false,
            disbandtog: false,
            loading: true,
        }
    },
    methods: {
        disbandToggle() {
            this.disbandtog = !this.disbandtog;
            window.$('#disbanded').collapse('toggle');
        },
        unofficialToggle() {
            this.unofficialtog = !this.unofficialtog;
            window.$('#unofficialclub').collapse('toggle');
        },
        officialToggle() {
            this.officialtog = !this.officialtog;
            window.$('#officialclub').collapse('toggle');
        },
        goClubForm() {
            this.$router.push('/clubform/create');
        },
        getData() {
            this.token = window.$cookies.get("token");
            if(this.token === null){
                this.token = window.$cookies.get("itkmitlcommunitytokenbrowser");
            }
            this.language = window.$cookies.get('itkmitlcommunitylanguage');
            axios.get($http + 'api/community/club/', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.infos = response
                this.officialinfos = this.infos.data.filter((info) => info.is_official === true && info.status !== 'D');
                this.unofficialinfos = this.infos.data.filter((info) => info.is_official === false && info.status !== 'D');
                this.disbandedinfos = this.infos.data.filter((info) => info.status === 'D');
                this.loading = false;
            }).catch((err) => {
                (console.log(err.response));
                if(this.language === 'th'){
                    this.errorMessage = "เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่"
                } else {
                    this.errorMessage = "Data fetching error, please try again.";
                }
                this.loading = false;
            });
            if (this.token !== null ) {
                axios.get($http + 'api/user/user/me/', {
                    headers: {
                        Authorization: this.token
                    }
                }).then((response) => {
                    this.myinfos = response
                }).catch((err) => {
                    (console.log(err.response))
                    this.myinfos = '';
                });
            }
        }
    },
    components: {
        TheCard,
        DisbandedCard,
    },
    async mounted() {
        this.getData();
    },
    watch: {
        $route(){
            this.getData();
        }
    },
    computed: {
        checkCreateButton(){
            if (this.myinfos) {
                if (this.myinfos.data.user_group === "student") {
                    return true;
                } else {
                    return false;
                }
            } else {
                return false;
            }
        }
    }
}
</script>
<style>
</style>
<style scoped src="../../assets/main.css"></style>