<template>
    <div class="collapse" id="announcement">
        <div class="ml-2 mt-2 mr-2">
            <div class="form-outline mb-4">
                <!-- <label class="form-label" for="text">Text</label> -->
                <textarea class="form-control" id="text" rows="4" v-model="text"></textarea>
                <p class="error" v-if="errorMessage.text">{{ errorMessage.text[0] }}</p>
            </div>
            
            <div style="margin-bottom: 20px;">
                <div v-if="current_image"><img class="mb-4" :src="current_image" style="width: 100%;"></div>
                <div class="input-group">
                    <div class="custom-file">
                        <input type="file" class="custom-file-input" id="inputGroupFile01" ref="annoncePic" @change="annoncePic"
                        aria-describedby="inputGroupFileAddon01" accept="image/*">
                    </div>
                </div>
                <p class="error" v-if="errorMessage.image">{{ errorMessage.image[0] }}</p>
            </div>
            

            <div class="form-check d-flex justify-content-start mb-4">
                <input
                class="form-check-input mr-2"
                type="checkbox"
                value=""
                id="allowOutside"
                v-model="is_publicly_visible"
                />
                <label class="form-check-label" for="allowOutside">
                    <p class="language" v-if="language === 'th'">ประกาศในรูปแบบสาธารณะ</p>
                    <p class="language" v-else>Post as publicly-visible announcement</p>
                </label>
            </div>
            <p class="error" v-if="errorMessage.is_publicly_visible">{{ errorMessage.is_publicly_visible[0] }}</p>

            <div class="d-flex justify-content-end"><button aria-expanded="false" aria-controls="announcement"
            @click="sendData" type="submit" class="btn btn-warning mr-2 mb-2">
                <p class="language" v-if="language === 'th'">แปะประกาศ</p>
                <p class="language" v-else>Post Announcement</p>
            </button></div>
        </div>
        <!-- Modal -->
        <the-alert :id="'alert'" :closetype="modalCloseType" :success="modalSuccess" :loading="modalLoading" :text="modalText">
            <template v-slot:header></template>
        </the-alert>
    </div>
</template>
<script>
import axios from 'axios';
import { $http } from '../../assets/variables.js';
import TheAlert from '../modal/TheAlert.vue';

export default {
    components: {
        TheAlert,
    },
    inject: ['closeForm', 'refreshAnnouncement'],
    props: ['community', 'token', 'language'],
    data() {
        return {
            text: '',
            image: null,
            current_image: null,
            is_publicly_visible: false,
            errorMessage: '',
            modalCloseType: '',
            modalSuccess: false,
            modalLoading: false,
            modalText: '',
        }
    },
    methods: {
        annoncePic(event){
            this.image = event.target.files[0];
            var reader = new FileReader();
            reader.onload = (e) => {
                this.current_image = e.target.result;
            }
            reader.readAsDataURL(this.image)
        },
        sendData() {
            // var data = {
            //     text: this.text,
            //     is_publicly_visible: this.is_publicly_visible,
            //     community: this.community
            // }
            this.modalCloseType = '';
            this.modalSuccess = false;
            this.modalLoading = true;
            this.modalText = 'Loading...';
            window.$('#alert').modal('show');
            this.errorMessage = '';
            const data = new FormData();
            data.append('text', this.text);
            if(this.image !== null){
                data.append('image', this.image);
            }
            data.append('is_publicly_visible', this.is_publicly_visible);
            data.append('community', this.community);
            axios.post($http + 'api/asset/announcement/', data, {
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                this.modalCloseType = '';
                this.modalSuccess = true;
                this.modalLoading = false;
                if (this.language === 'th') {
                    this.modalText = 'แปะประกาศเรียบร้อย';
                } else {
                    this.modalText = 'Announcement posted.';
                }
                setTimeout(() => {window.$('#alert').modal('hide');}, 3000);
                this.text = null;
                this.image = null;
                this.current_image = null;
                this.closeForm();
                this.refreshAnnouncement(this.community);
                setTimeout(() => {window.$('#announcement').collapse('hide');}, 1000);
            }).catch((err) => {
                console.log(err);
                this.modalCloseType = '';
                this.modalSuccess = false;
                this.modalLoading = false;
                if (this.language === 'th') {
                    this.modalText = 'เกิดข้อผิดพลาดระหว่างการแปะประกาศ';
                } else {
                    this.modalText = 'Failed to post announcement.';
                }
                this.errorMessage = err.response.data;
            });
        },
    },
}
</script>
<style scoped src="../../assets/main.css"></style>
<style scoped src="../../assets/form.css"></style>