<template>
    <div class="background" style="height: 100vh;">
        <div v-if="info" class="not-bg">
            <div class="row ml-0 mr-0">
                <div class="col-2"></div>
                <div class="col-8">
                    <div class="row">
                        <div class="col-1"></div>
                        <div class="col-10">
                            <div class="cover">
                                <img class="cover" :src="info.banner">
                            </div>
                        </div>
                        <div class="col-1"></div>
                    </div>
                </div>
                <div class="col"></div>
            </div>
            <div class="row ml-0 mr-0">
                <div class="col-md-3"></div>
                <div class="col-md-6">
                    <div class="container">
                        <div class="logo">
                            <img class="logo" :src="info.logo">
                        </div>
                    </div>
                    <div class="container">
                        <div class="row">
                            <div class="col-md-10">
                                <h2 class="name_en">{{ info.name_en }}</h2>
                            </div>
                            <div class="col-md d-flex justify-content-end status action">
                                <div v-if="checkStatus() === 'cancel'"><p class="badge bg-danger card-text">Cancelled</p></div>
                                <div v-else-if="checkStatus() === 'unapprove'"><p class="badge bg-dark card-text">Unapproved</p></div>
                                <div v-else-if="checkStatus() === 'end'"><p class="badge bg-dark card-text">Ended</p></div>
                                <div v-else><p class="badge bg-success card-text">Recruiting</p></div>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-md-9">
                                <h4 class="name_th">{{ info.name_th }}</h4>
                            </div>
                            <div class="col-md">
                                <div class="d-flex justify-content-end action">
                                    <button v-if="this.info.meta.own_membership_id !== null" class="btn btn-success">Joined</button>
                                    <button v-if="this.info.meta.request_ability.is_able_to_send_request"
                                        class="btn btn-success"
                                        @click="joinCommunity">Join Event</button>
                                    <div v-else-if="info.meta.request_ability.code === 'restricted'">
                                        <div class="d-flex justify-content-end">
                                            <button class="btn btn-success" disabled
                                            >Join Event</button>
                                        </div>
                                        <p style="font-size: 10px; margin-bottom: -2px;">{{ info.meta.request_ability.message }}</p>
                                    </div>
                                    <div v-else-if="info.meta.request_ability.code === 'pending_invitation'">
                                        <div class="d-flex justify-content-end">
                                            <button class="btn btn-primary"
                                            @click="acceptInvitation(invite.id)">Accept Invitation</button>
                                            <button class="btn btn-danger"
                                            @click="declineInvitation(invite.id)">Decline Invitation</button>
                                        </div>
                                        <p style="font-size: 10px; margin-bottom: -2px;">{{ info.meta.request_ability.message }}</p>
                                    </div>
                                    <button @click="cancelRequest" class="btn btn-danger" v-if="this.pending.length !== 0">Cancel Join Request</button>
                                    <div v-if="this.info.meta.available_actions.length !== 0 && !(this.info.meta.available_actions.length === 1 && this.info.meta.available_actions[0] === 'comment')" class="dropdown ml-2">
                                        <button class="btn btn-primary dropdown-toggle"
                                            type="button"
                                            id="dropdownMenuButton"
                                            data-toggle="dropdown"
                                            aria-expanded="false"
                                        >...</button>
                                        <ul class="dropdown-menu dropdown-menu-right" aria-labelledby="dropdownMenuButton">
                                            <li>
                                                <button v-if="this.info.meta.available_actions.find((action) => action === 'edit') === 'edit'" class="dropdown-item" @click="goEventForm(this.info.id)">Edit Event</button>
                                            </li>
                                            <li>
                                                <button v-if="this.info.meta.available_actions.find((action) => action === 'send-approval-request') === 'send-approval-request'" class="dropdown-item" @click="goSendApproval(this.info.id)">Send Approval Request</button>
                                            </li>
                                            <li>
                                                <button v-if="this.info.meta.available_actions.find((action) => action === 'cancel-approval-request') === 'cancel-approval-request'" class="dropdown-item" @click="cancelApproval(this.info.id)">Cancel Approval Request</button>
                                            </li>
                                            <li>
                                                <button v-if="this.info.meta.available_actions.find((action) => action === 'retire') === 'retire'" class="dropdown-item" @click="retireFromEvent(this.info.meta.own_membership_id)">Retire</button>
                                            </li>
                                            <li>
                                                <button v-if="this.info.meta.available_actions.find((action) => action === 'active') === 'active'" class="dropdown-item" @click="backToDuty(this.info.meta.own_membership_id)">Rejoin</button>
                                            </li>
                                            <li>
                                                <button v-if="this.info.meta.available_actions.find((action) => action === 'leave') === 'leave'" class="dropdown-item" @click="leaveEvent(this.info.meta.own_membership_id)">Leave Event</button>
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-md-3"></div>
            </div>
            <div class="row the-rest ml-0 mr-0">
                <div class="col-md-3"></div>
                <div class="col-md-6">
                    <div class="row tab-size">
                            <router-link class="col-md test-tab text-center" :to="'/event/' + eventurl + '/'"><p class="tab">Announcements</p></router-link>
                            <router-link class="col-md test-tab text-center" :to="'/event/' + eventurl + '/album'"><p class="tab">Albums</p></router-link>
                            <router-link class="col-md test-tab text-center" :to="'/event/' + eventurl + '/comment'"><p class="tab">Comments</p></router-link>
                            <router-link class="col-md test-tab text-center" :to="'/event/' + eventurl + '/member'"><p class="tab">Members</p></router-link>
                            <router-link v-if="this.info.meta.own_membership_id !== null" class="col-md test-tab text-center" :to="'/event/' + eventurl + '/request'"><p class="tab">Requests</p></router-link>
                            <router-link v-if="this.info.meta.own_membership_id > 1" class="col-md test-tab text-center" :to="'/event/' + eventurl + '/qrcode'"><p class="tab">QR Code</p></router-link>
                    </div>
                </div>
                <div class="col-md-3"></div>
            </div>
            <router-view class="ml-0 mr-0"
            :info = info
            :token = token
            :eventtype = eventtype
            :eventseries = eventseries></router-view>
        </div>
        <div class="row" v-else>
            <div class="col-md"></div>
            <div v-if="message !== ''" class="col-md">
                <h3 style="color: red;">{{ message }}</h3>
            </div>
            <div v-else class="col-md">
                <div class="d-flex justify-content-center">
                    <div class="spinner-border m-5" role="status">
                        <span class="sr-only">Loading...</span>
                    </div>
                </div>
                <div class="d-flex justify-content-center">
                    <h1>Loading...</h1>
                </div>
            </div>
            <div class="col-md"></div>
        </div>
    </div>
</template>
<script>
import axios from 'axios';

export default {
    props: ['eventurl'],
    data () {
        return {
            token: '',
            info: '',
            message: '',
            pending: '',
            myinfo: '',
            eventtype: '',
            eventseries: '',
            invite: '',
            date: '',
        };
    },
    methods: {
        checkStatus(){
            this.date = new Date().toJSON().slice(0,10);
            if(this.info.is_cancelled){
                return 'cancel';
            } else if (!this.info.is_approved){
                return 'unapprove';
            } else if (this.date > this.info.end_date){
                return 'end';
            } else {
                return 'recruiting';
            }
        },
        declineInvitation(id){
            axios.patch('http://127.0.0.1:8000/api/membership/invitation/' + id + '/', {
                status: "D"
            }, {
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                alert('Invitation Declined.')
                this.$router.go();
            });
        },
        acceptInvitation(id){
            axios.patch('http://127.0.0.1:8000/api/membership/invitation/' + id + '/', {
                status: "A"
            }, {
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                alert('Invitation Accepted.')
                this.$router.go();
            });
        },
        getInvitation(id){
            axios.get('http://127.0.0.1:8000/api/user/user/me', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.myinfo = response.data
                axios.get('http://127.0.0.1:8000/api/membership/invitation?invitee='+ this.myinfo.id +'&community=' + id +'&status=W',{
                    headers: {
                        Authorization: this.token
                    }
                }).then((response) => {
                    this.invite = response.data[0];
                    console.log(this.invite)
                })
            })
        },
        getPending(id){
            axios.get('http://127.0.0.1:8000/api/user/user/me', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.myinfo = response.data
                axios.get('http://127.0.0.1:8000/api/membership/request?user=' + this.myinfo.id + '&community=' + id + '&status=W', {
                    headers: {
                        Authorization: this.token
                    }
                })
                .then((response) => {
                    this.pending = response.data
                    console.log(response.data)
                });
            })
            
        },
        getEventSeriesName(id){
            axios.get('http://127.0.0.1:8000/api/category/event-series/' + id)
            .then((response) => {
                this.eventseries = response.data.title_en
            }).catch(() => {
                this.eventseries = "-"
            })
        },
        getEventTypeName(id){
            axios.get('http://127.0.0.1:8000/api/category/event-type/' + id)
            .then((response) => {
                this.eventtype = response.data.title_en
            }).catch(() => {
                this.eventtype = "-"
            })
        },
        cancelApproval(id){
            axios.get('http://127.0.0.1:8000/api/membership/approval-request?community=' + id, {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                axios.delete('http://127.0.0.1:8000/api/membership/approval-request/' + response.data[0].id + '/', {
                    headers: {
                        Authorization: this.token
                    }
                }).then(() => {
                    alert('Approval request is cancelled successfully.')
                    this.$router.go();
                }).catch(() => {
                    alert('Error cancelling approval request, please try again.')
                })
            }).catch(() => {
                alert('Error getting approval request data, please try again.')
            })
        },
        goSendApproval(id){
            this.$router.push('/approve/' + id);
        },
        getdata() {
            this.token = window.$cookies.get('token');
            axios.get('http://127.0.0.1:8000/api/community/event/?url_id=' + this.eventurl, {
                headers: {
                    Authorization: this.token
                }
            })
            .then((response) => {
                this.info = response.data;
                this.getEventTypeName(response.data.event_type);
                this.getEventSeriesName(response.data.event_series);
                this.getPending(response.data.id);
                this.checkStatus();
                if(response.data.meta.request_ability.code === 'pending_invitation'){
                    this.getInvitation(response.data.id);
                }
                if(this.info === undefined){
                    this.message = 'Oops no data found.';
                }
            }).catch((err) => {
                if(err.response.status === 404){
                    axios.get('http://127.0.0.1:8000/api/community/event/' + this.eventurl, {
                        headers: {
                            Authorization: this.token
                        }
                    })
                    .then((response) => {
                        this.info = response.data;
                        this.getEventTypeName(response.data.event_type);
                        this.getEventSeriesName(response.data.event_series);
                        this.getPending(response.data.id);
                        this.checkStatus();
                        if(response.data.meta.request_ability.code === 'pending_invitation'){
                            this.getInvitation(response.data.id);
                        }
                        if(this.info === undefined){
                            this.message = 'Oops no data found.';
                        }
                    }).catch((err) => {
                        (console.log(err))
                        this.info = '';
                        this.message = 'Oops there is an error on fetching data.';
                    });
                }
            });
        },
        goEventForm(id) {
            this.$router.push('/eventform/' + id);
        },
        joinCommunity() {
            try {
                var data = {
                    community: this.info.id
                };
                axios.post('http://127.0.0.1:8000/api/membership/request/', data, {
                    headers: {
                        Authorization: this.token
                    },
                }).then(
                    alert("Join request is sent successfully."),
                    this.$router.go()
                );
            } catch (err) {
                console.log(err);
            }
        },
        cancelRequest() {
            var check = confirm("Are you sure you want to cancel the join request?");
            if(check) {
                try{
                    axios.delete('http://127.0.0.1:8000/api/membership/request/' + this.pending[0].id + '/', {
                        headers: {
                            Authorization: this.token
                        }
                    }).then(
                        this.$router.go()
                    );
                } catch (err) {
                    console.log(err);
                }
            }
        },
        leaveEvent(id) {
            var check = confirm("Are you sure you want to leave the event?");
            if(check) {
                try {
                    axios.get('http://127.0.0.1:8000/api/membership/membership/' + id + '/', {
                        headers: {
                            Authorization: this.token
                        }
                    }).then((response) => {
                        axios.put('http://127.0.0.1:8000/api/membership/membership/' + id + '/', {
                            position: response.data.position,
                            status: "L"
                        }, {
                            headers: {
                                Authorization: this.token
                            }
                        }).then(
                            this.$router.go()
                        )
                    });
                    
                } catch (err) {
                    console.log(err);
                }
            }
        },
        retireFromEvent(id) {
            var check = confirm("Are you sure you want to retire?");
            if(check) {
                try {
                    axios.get('http://127.0.0.1:8000/api/membership/membership/' + id + '/', {
                        headers: {
                            Authorization: this.token
                        }
                    }).then((response) => {
                        axios.put('http://127.0.0.1:8000/api/membership/membership/' + id + '/', {
                            position: response.data.position,
                            status: "R"
                        }, {
                            headers: {
                                Authorization: this.token
                            }
                        }).then(
                            this.$router.go()
                        )
                    });
                    
                } catch (err) {
                    console.log(err);
                }
            }
        },
        backToDuty(id) {
            alert("Welcome back to the event.");
            try {
                axios.get('http://127.0.0.1:8000/api/membership/membership/' + id + '/', {
                    headers: {
                        Authorization: this.token
                    }
                }).then((response) => {
                    axios.put('http://127.0.0.1:8000/api/membership/membership/' + id + '/', {
                        position: response.data.position,
                        status: "A"
                    }, {
                        headers: {
                            Authorization: this.token
                        }
                    }).then(
                        this.$router.go()
                    )
                });
            } catch (err) {
                console.log(err);
            }
        },
    },
    async mounted() {
        this.getdata();
    },
    watch: {
        $route() {
            this.getdata();
        }
    },
}
</script>
<style scoped>
img.cover{
    width: 100%;
}
img.logo{
    width: 20%;
    height: 20%;
}
</style>
<style scoped src="../../../assets/main.css"></style>
<style scoped src="../../../assets/community.css"></style>