<template>
    <div class="row background">
        <div class="col-md-3"></div>
        <div class="col-md-6">
            <div class="row">
                <!-- {{info}} -->
                <div class="col-xl-4 mt-4 mb-4">
                    <div class="not-bg info-box">
                        <div class="ml-1 mt-1 mb-1 mr-1">
                            <div class="container info-box-title">
                                <b><p class="language" v-if="language === 'th'">เกี่ยวกับ</p>
                                <p class="language" v-else>About</p>
                            </b>
                            </div>
                            
                            <!-- {{info}} -->
                            <div v-if="info.description" class="container">
                                <div>
                                    <b><p class="language" v-if="language === 'th'">คำอธิบาย</p>
                                    <p class="language" v-else>Description</p></b>
                                </div>
                                <div>
                                    <div v-if="info.description === null || info.description === ''">
                                        <p class="language" v-if="language === 'th'">ยังไม่มีอะไร</p>
                                        <p class="language" v-else>Nothing here yet</p>
                                    </div>
                                    <p style="word-wrap: break-word;" v-else>{{ info.description }}</p>
                                </div>
                            </div>
                            <div v-if="info.room" class="container">
                                <div>
                                    <b><p class="language" v-if="language === 'th'">ห้อง</p>
                                    <p class="language" v-else>Room</p></b>
                                </div>
                                <div>
                                    <div v-if="info.room === null || info.room === ''">
                                        <p class="language" v-if="language === 'th'">ยังไม่มีอะไร</p>
                                        <p class="language" v-else>Nothing here yet</p>
                                    </div>
                                    <p v-else>{{ info.room }}</p>
                                </div>
                            </div>
                            <div v-if="info.location" class="container">
                                <div>
                                    <b><p class="language" v-if="language === 'th'">สถานที่</p>
                                    <p class="language" v-else>Location</p></b>
                                </div>
                                <div>
                                    <div v-if="info.location === null || info.location === ''">
                                        <p class="language" v-if="language === 'th'">ยังไม่มีอะไร</p>
                                        <p class="language" v-else>Nothing here yet</p>
                                    </div>
                                    <p v-else>{{ info.location }}</p>
                                </div>
                            </div>
                            <div class="collapse" id="about">
                                <div v-if="clubtype" class="container">
                                    <div>
                                        <b><p class="language" v-if="language === 'th'">ประเภทชุมนุม</p>
                                        <p class="language" v-else>Club Type</p></b>
                                    </div>
                                    <div>
                                        <div v-if="clubtype === null || clubtype === ''">
                                            <p class="language" v-if="language === 'th'">ยังไม่มีอะไร</p>
                                            <p class="language" v-else>Nothing here yet</p>
                                        </div>
                                        <p v-else>{{ clubtype }}</p>
                                    </div>
                                </div>
                                <div v-if="eventtype" class="container">
                                    <div>
                                        <b><p class="language" v-if="language === 'th'">ประเภทกิจกรรม</p>
                                        <p class="language" v-else>Event Type</p></b>
                                    </div>
                                    <div>
                                        <div v-if="eventtype === null || eventtype === ''">
                                            <p class="language" v-if="language === 'th'">ยังไม่มีอะไร</p>
                                            <p class="language" v-else>Nothing here yet</p>
                                        </div>
                                        <p v-else>{{ eventtype }}</p>
                                    </div>
                                </div>
                                <div v-if="eventseries" class="container">
                                    <div>
                                        <b><p class="language" v-if="language === 'th'">กลุ่มกิจกรรม</p>
                                        <p class="language" v-else>Event Series</p></b>
                                    </div>
                                    <div>
                                        <div v-if="eventseries === null || eventseries === ''">
                                            <p class="language" v-if="language === 'th'">ยังไม่มีอะไร</p>
                                            <p class="language" v-else>Nothing here yet</p>
                                        </div>
                                        <p v-else>{{ eventseries }}</p>
                                    </div>
                                </div>
                                <div v-if="info.start_date" class="container">
                                    <div>
                                        <b><p class="language" v-if="language === 'th'">วันที่เริ่ม</p>
                                        <p class="language" v-else>Start Date</p></b>
                                    </div>
                                    <div>
                                        <div v-if="info.start_date === null || info.start_date === ''">
                                            <p class="language" v-if="language === 'th'">ยังไม่มีอะไร</p>
                                            <p class="language" v-else>Nothing here yet</p>
                                        </div>
                                        <p v-else>{{ info.start_date }} {{ info.start_time }}</p>
                                    </div>
                                </div>
                                <div v-if="info.end_date" class="container">
                                    <div>
                                        <b><p class="language" v-if="language === 'th'">วันที่สิ้นสุด</p>
                                        <p class="language" v-else>End Date</p></b>
                                    </div>
                                    <div>
                                        <div v-if="info.end_date === null || info.end_date === ''">
                                            <p class="language" v-if="language === 'th'">ยังไม่มีอะไร</p>
                                            <p class="language" v-else>Nothing here yet</p>
                                        </div>
                                        <p v-else>{{ info.end_date }} {{ info.end_time }}</p>
                                    </div>
                                </div>
                                <div v-if="info.founded_date" class="container">
                                    <div>
                                        <b><p class="language" v-if="language === 'th'">วันที่ก่อตั้ง</p>
                                        <p class="language" v-else>Founded Date</p></b>
                                    </div>
                                    <div>
                                        <div v-if="info.founded_date === null || info.founded_date === ''">
                                            <p class="language" v-if="language === 'th'">ยังไม่มีอะไร</p>
                                            <p class="language" v-else>Nothing here yet</p>
                                        </div>
                                        <p v-else>{{ info.founded_date }}</p>
                                    </div>
                                </div>
                                <div v-if="info.tags" class="container">
                                    <div>
                                        <b><p class="language" v-if="language === 'th'">ที่เกี่ยวข้อง</p>
                                        <p class="language" v-else>Tags</p></b>
                                    </div>
                                    <div>
                                        <div v-if="info.tags === null || info.tags === ''">
                                            <p class="language" v-if="language === 'th'">ยังไม่มีอะไร</p>
                                            <p class="language" v-else>Nothing here yet</p>
                                        </div>
                                        <p style="word-wrap: break-word;" v-else>{{ info.tags }}</p>
                                    </div>
                                </div>
                                <div v-if="advisor" class="container">
                                    <div v-if="advisor.length !== 0">
                                        <div>
                                            <b><p class="language" v-if="language === 'th'">ที่ปรึกษา</p>
                                            <p class="language" v-else>Advisor</p></b>
                                        </div>
                                        <div v-for="advise in advisor" :key="advise.id">
                                            <p>{{ advise }}</p>
                                        </div>
                                    </div>
                                </div>
                                <div class="container">
                                    <div>
                                        <b><p class="language" v-if="language === 'th'">ลิ้งค์</p>
                                        <p class="language" v-else>External Links</p></b>
                                    </div>
                                    <div>
                                        <p v-if="info.external_links === null || info.external_links === ''">-</p>
                                        <a v-for="link in externalLink" :key="link" class="external_links" :href="link" style="word-wrap: break-word;">{{ removeHTTP(link) }}</a>
                                    </div>
                                </div>
                            </div>
                            <b><p @click="toggleAbout" class="text-center toggle-data"
                            aria-expanded="false" aria-controls="about" style="margin-top: 16px;">{{ toggleText }}</p></b>
                        </div>
                    </div>
                </div>
                <!-- Announcement -->
                <div class="col-xl-8 mt-4" v-if="error !== ''">
                    <h3 class="error d-flex justify-content-center">{{ error }}</h3>
                </div>
                <div class="col-xl-8 mt-4 mb-4" v-else>
                    <div class="info-box not-bg mb-2 top-announcement">
                        <!-- PC -->
                        <div class="d-none d-lg-block">
                            <div class="row ml-2 mr-2">
                                <div class="col d-flex mt-2">
                                    <div class="mb-2">
                                        <input v-if="language === 'th'" class="form-control" id="searchAnnounce" rows="4" v-model="searchAnnounce" placeholder="ค้นหา" />
                                        <input v-else class="form-control" id="searchAnnounce" rows="4" v-model="searchAnnounce" placeholder="Search..." />
                                    </div>
                                    <div class="mb-2">
                                        <button @click="searchAnnouncement(info.id)" class="btn btn-primary"><i class="fas fa-search"></i></button>
                                    </div>
                                </div>
                                <div class="col mt-2 mb-2 d-flex justify-content-end">
                                    <button v-if="info.meta.own_membership_position > 0" class="btn btn-warning" @click="checkAnnouncement(); toggleForm();"
                                    aria-expanded="false" aria-controls="announcement">{{ createAnnouncementMessage }}</button>
                                </div>
                            </div>
                        </div>
                        <!-- Mobile -->
                        <div class="d-flex d-lg-none justify-content-center">
                            <div class="mb-2">
                                <input v-if="language === 'th'" class="form-control" id="searchAnnounce" rows="4" v-model="searchAnnounce" placeholder="ค้นหา..." />
                                <input v-else class="form-control" id="searchAnnounce" rows="4" v-model="searchAnnounce" placeholder="Search..." />
                            </div>
                            <div class="mb-2">
                                <button @click="searchAnnouncement(info.id)" class="btn btn-primary"><i class="fas fa-search"></i></button>
                            </div>
                        </div>
                        <div class="d-lg-none mt-2 mb-2 d-flex justify-content-center">
                            <button v-if="info.meta.own_membership_position > 0" class="btn btn-warning" @click="checkAnnouncement(); toggleForm();"
                            aria-expanded="false" aria-controls="announcement">{{ createAnnouncementMessage }}</button>
                        </div>
                        <announcement-form style="padding: 10px;"
                        :community = info.id
                        :token = token
                        :language= language></announcement-form>
                    </div>
                    <!-- announcement list -->
                    <div v-for="announcement in announcements[0].slice().reverse()" 
                    :key="announcement.id">
                        <div class="not-bg info-box mt-4" v-if="announcement === 'Loading...'"
                        style="min-height: 80px; overflow: hidden;"> 
                            <div class="d-flex justify-content-center">
                                <div class="spinner-border m-5" role="status">
                                    <span class="sr-only">Loading</span>
                                </div>
                            </div>
                            <div class="d-flex justify-content-center">
                                <h3>Loading...</h3>
                            </div>
                        </div>
                        <div v-else-if="announcementEmpty">
                            <p class="mt-4 d-flex justify-content-center">{{ announcement }}</p>
                        </div>
                        <div v-else>
                        <announcement-form-edit
                        :language = language
                        :announceid = announcement.id
                        :text = announcement.text
                        :image = announcement.image
                        :public = announcement.is_publicly_visible
                        :token = token
                        :community = info.id
                        :editable = announcement.meta.is_able_to_edit></announcement-form-edit>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="col-md-3"></div>
    </div>
</template>
<script>
import AnnouncementForm from '../../form/AnnouncementForm.vue';
import AnnouncementFormEdit from '../../form/AnnouncementFormEdit.vue';
import axios from 'axios';
import { $http } from '../../../assets/variables.js';

export default {
    props: ['token', 'info', 'clubtype', 'eventtype', 'eventseries', 'advisor', 'language'],
    provide() {
        return {
            closeForm: this.checkAnnouncement,
            refreshAnnouncement: this.getAnnoucementData
        }
    },
    data() {
        return {
            announcements: [['Loading...']],
            announcementMessage: '',
            announcementLoading: true,
            announcementEmpty: false,
            createAnnouncementCheck: false,
            searchAnnounce: '',
            error: '',
            toggleText: '',
            toggleState: false,
            externalLink: [],
        }
    },
    components: {
        AnnouncementForm,
        AnnouncementFormEdit,
    },
    computed: {
        createAnnouncementMessage() {
            if(this.createAnnouncementCheck){
                if(this.language === 'th') {
                    return 'ปิด'
                } else {
                    return 'Close';
                }
            } else {
                if(this.language === 'th') {
                    return 'สร้างประกาศ'
                } else {
                    return 'Create Announcement';
                }
            }
        },
    },
    methods: {
        toggleForm(){
            window.$('#announcement').collapse('toggle');
        },
        removeHTTP(text){
            var removed = text.split("https://www.")
            return removed[1];
        },
        getExternalLink(){
            var keep = this.info.external_links;
            this.externalLink = keep.split("\n");
            this.externalLink = keep.split(" ");
        },
        toggleAbout(){
            if(this.toggleState){
                if(this.language === 'th') {
                    this.toggleText = 'เพิ่มเติม';
                } else {
                    this.toggleText = 'See more';
                }
            } else {
                if(this.language === 'th') {
                    this.toggleText = 'ลดลง'
                } else {
                    this.toggleText = 'See less';
                }
            }
            window.$('#about').collapse('toggle');
            this.toggleState = !this.toggleState;
        },
        searchAnnouncement(id) {
            this.announcementLoading = true;
            axios.get($http + 'api/asset/announcement?community=' + id + '&search=' + this.searchAnnounce, {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.announcements.shift()
                if (response.data.length === 0){
                    this.announcementEmpty = true;
                    if(this.language === 'th') {
                        this.announcements.push(['ไม่มีประกาศที่ตรงกับที่คุณค้นหา'])
                    } else {
                        this.announcements.push(['No announcements match your search.'])
                    }
                } else {
                    this.announcementEmpty = false;
                    this.announcements.push(response.data)
                }
                this.announcementLoading = false;
            }).catch(()=>{
                this.announcementLoading = false;
            });
        },
        getAnnoucementData(id) {
            this.announcementLoading = true;
            if(this.announcements[0] !== undefined){
                    this.announcements.shift();
                    this.announcements.push(['Loading...'])
            }
            setTimeout(() => {
            if(id !== undefined && id !== null){
                this.announcements.shift();
                axios.get($http + 'api/asset/announcement?community=' + id, {
                    headers: {
                        Authorization: this.token
                    }
                }).then((response) => {
                    this.announcements.push(response.data)
                    this.announcementLoading = false;
                    this.announcementEmpty = false;
                    if(this.announcements[0].length === 0){
                        this.announcements.shift()
                        if(this.language === 'th') {
                            this.announcements.push(['ยังไม่มีประกาศ'])
                        } else {
                            this.announcements.push(['No announcements have been made yet.'])
                        }
                        this.announcementLoading = false;
                        this.announcementEmpty = true;
                    }
                }).catch((err) => {
                    this.announcementLoading = false;
                    console.log(err.response);
                    if(this.language === 'th') {
                        this.error = "ไม่สามารถดึงข้อมูลประกาศได้";
                    } else {
                        this.error = "Can't get announcement data."
                    }
                });
                
            }
            }, 2000);
        },
        checkAnnouncement() {
            this.createAnnouncementCheck = !this.createAnnouncementCheck;
        },
    },
    async mounted() {
        this.getAnnoucementData(this.info.id);
        this.getExternalLink();
        if(this.language === 'th') {
            this.toggleText = 'เพิ่มเติม'
        } else {
            this.toggleText = 'See more';
        }
    }
}
</script>
<style scoped>
h3.error{
    color: red;
}
</style>
<style scoped src="../../../assets/main.css"></style>
<style scoped src="../../../assets/community.css">
</style>
<style scoped>
.top-announcement{

}
.external_links:hover{
    color: #1c86f2;
}
</style>