<template>
    <div class="collapse row not-bg form-padding" id="comment">
        <div class="form-outline mb-4">
            <label class="form-label" for="text">
                <p class="language" v-if="language === 'th'">ความคิดเห็น</p>
                <p class="language" v-else>Comment</p>
            </label>
            <textarea class="form-control" id="text" rows="4" v-model="text"></textarea>
            <p class="error" v-if="errorMessage.text">{{ errorMessage.text[0] }}</p>
        </div>

        <div class="form-outline mb-4">
            <label class="form-label" for="written_by">
                <p class="language" v-if="language === 'th'">ชื่อ</p>
                <p class="language" v-else>Name</p>
            </label>
            <input v-if="language === 'th'" class="form-control" id="written_by" rows="4" v-model="written_by" placeholder="หากไม่ประสงค์ที่จะแสดงตัวตนไม่ต้องใส่อะไร" />
            <input v-else class="form-control" id="written_by" rows="4" v-model="written_by" placeholder="leave blank if you don't have intend to show your identity" />
        </div>

        <div class="d-flex justify-content-end">
            <button aria-expanded="false" aria-controls="comment"
            @click="sendData" type="submit" class="btn btn-warning">
                <p class="language" v-if="language === 'th'">ส่ง</p>
                <p class="language" v-else>Submit</p>
            </button>
        </div>
        <!-- Modal -->
        <the-alert :id="'alert'" :closetype="modalCloseType" :success="modalSuccess" :loading="modalLoading" :text="modalText">
            <template v-slot:header></template>
        </the-alert>
    </div>
</template>
<script>
import axios from 'axios';
import { $http } from '../../assets/variables.js';
import TheAlert from '../modal/TheAlert.vue';

export default {
    components: {
        TheAlert
    },
    props: ['event', 'token', 'language'],
    inject: ['closeComment', 'refreshComment'],
    data() {
        return {
            text: '',
            written_by: '',
            errorMessage: '',
            modalCloseType: '',
            modalSuccess: false,
            modalLoading: false,
            modalText: '',
        }
    },
    methods: {
        sendData() {
            this.modalCloseType = '';
            this.modalSuccess = false;
            this.modalLoading = true;
            this.modalText = 'Loading...';
            window.$('#alert').modal('show');
            this.errorMessage = '';
            if(this.written_by === '' || this.written_by === null || this.written_by === undefined){
                this.written_by = 'Anonymous'
            }
            var data = {
                text: this.text,
                written_by: this.written_by,
                event: this.event
            }
            axios.post($http + 'api/asset/comment/', data, {
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                this.modalCloseType = '';
                this.modalSuccess = true;
                this.modalLoading = false;
                if (this.language === 'th') {
                    this.modalText = 'ส่งความคิดเห็นเรียบร้อย'
                } else {
                    this.modalText = 'Comment posted.';
                }
                setTimeout(() => {window.$('#alert').modal('hide');}, 3000);
                this.text = null;
                this.written_by = null;
                this.closeComment();
                this.refreshComment(this.event)
                setTimeout(() => {window.$('#comment').collapse('hide');}, 1000);
            }).catch((error) => {
                this.closeComment();
                this.modalCloseType = '';
                this.modalSuccess = false;
                this.modalLoading = false;
                if (this.language === 'th') {
                    this.modalText = 'เกิดข้อผิดพลาดระหว่างส่งความคิดเห็น';
                } else {
                    this.modalText = 'Failed to post comment.';
                }
                setTimeout(() => {window.$('#alert').modal('hide');}, 3000);
                this.errorMessage = error.response.data;
            });
        },
    }
}
</script>
<style scoped src="../../assets/main.css"></style>
<style scoped src="../../assets/form.css"></style>
<style scoped>
.form-padding{
    padding-top: 12px;
}
</style>