<template>
    <div class="row">
        <div class="col-md-3"></div>
        <div class="col-md-6 not-bg mt-2">
            <h1 class="d-flex justify-content-center m-4">
                <p class="language" v-if="language === 'th'">คำถามที่พบบ่อย</p>
                <p class="language" v-else>Frequently Asked Questions (FAQ)</p>
            </h1>
            <div style="margin-top: 50px;">
                <div v-for="question in faq" :key="question.id" class="mb-2 faqbox">
                    <div @click="toggleQuestion('question' + question.id)" class="question-box d-flex row" :id="'question' + question.id" data-toggle="collapse" :data-target="'#answer' + question.id"
                        aria-expanded="false" :aria-controls="'answer' + question.id">
                        <div class="m-2 mb-4 col-9">Q: 
                            <p class="language" v-if="language === 'th'">{{ question.question_th }}</p>
                            <p class="language" v-else>{{ question.question }}</p>
                        </div>
                        <i class="col d-flex justify-content-end fas fa-angle-down fa-2x"
                        style="margin-top: 6px;"></i>
                    </div>
                    <div class="collapse answer" :id="'answer' + question.id">
                        <div class="m-2" style="white-space: pre-line;">
                            <p class="language" v-if="language === 'th'">{{ question.answer_th }}</p>
                            <p class="language" v-else>{{ question.answer }}</p><br> &nbsp;
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="col-md-3"></div>
    </div>
</template>
<script>
import axios from 'axios';
import { $http } from '../../assets/variables.js';

export default {
    data() {
        return {
            token: '',
            language: '',
            faq: '',
        }
    },
    methods: {
        toggleQuestion(id){
            var element = document.getElementById(id);
            element.classList.toggle('question-box-active');
        },
        getFAQ(){
            axios.get($http + 'api/misc/faq', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.faq = response.data
            })
        },
    },
    async mounted() {
        this.token = window.$cookies.get('token');
        if(this.token === null){
            this.token = window.$cookies.get("itkmitlcommunitytokenbrowser");
        }
        this.language = window.$cookies.get('itkmitlcommunitylanguage');
        this.getFAQ();
    },
}
</script>
<style scoped src="../../assets/main.css"></style>
<style scoped>
.faqbox{
    border-bottom: solid;
    border-width: 1px;
    border-color: lightgrey;
    cursor: pointer
}
.question-box{
    font-size: 16px;
}
.question-box:hover{
    background: rgb(238, 238, 238)
}
