<template>
    <div class="row mb-4">
        <div class="col-md">
            <img src="../../assets/back.svg" @click="this.$router.back()" class="mt-4 ml-3 d-none d-lg-block back-button">
        </div>
        <div class="col-md mt-4 not-bg form-padding">
            <h2 class="d-none d-lg-block">
                <p class="language" v-if="language === 'th'">แบบฟอร์มขอการอนุมัติ</p>
                <p class="language" v-else>Approval Request Form</p>
            </h2>

            <div class="d-lg-none row">
                <h2 class="mb-2 col-9">
                    <p class="language" v-if="language === 'th'">แบบฟอร์มขอการอนุมัติ</p>
                    <p class="language" v-else>Approval Request Form</p>
                </h2>
                <i @click="this.$router.back()" style="cursor: pointer;" class="fas fa-times-circle fa-2x col d-flex justify-content-end mt-1"></i>
            </div>
            <div class="form-outline mt-4 mb-4">
                <label class="form-label" for="message">
                    <p class="language" v-if="language === 'th'">ข้อความ</p>
                    <p class="language" v-else>Message</p>
                </label>
                <textarea v-if="language === 'th'" class="form-control" id="message" rows="4" v-model="message" placeholder="ข้อความถึงสโมสรนักศึกษา"></textarea>
                <textarea v-else class="form-control" id="message" rows="4" v-model="message" placeholder="Message for the student committee."></textarea>
                <p v-if="error.message" class="error">{{ error.message[0] }}</p>
            </div>
            <label class="mb-2">
                <p class="language" v-if="language === 'th'">เอกสารการขออนุมัติ</p>
                <p class="language" v-else>Approval document</p>
            </label>
            <div class="input-group mb-4">
                <div class="custom-file">
                    <input type="file" class="custom-file-input" id="inputGroupFile01" ref="newFile" @change="newFile"
                    aria-describedby="inputGroupFileAddon01">
                </div>
            </div>
            <button @click="sendApproval" class="btn btn-primary btn-block mt-4">
                <p class="language" v-if="language === 'th'">ส่งคำขออนุมัติ</p>
                <p class="language" v-else>Send Approval Request</p>
            </button>
        </div>
        <div class="col-md"></div>
        <!-- Modal -->
        <the-alert :id="'alert'" :closetype="modalCloseType" :success="modalSuccess" :loading="modalLoading" :text="modalText">
            <template v-slot:header></template>
        </the-alert>
    </div>
</template>
<script>
import axios from 'axios';
import { $http } from '../../assets/variables.js';
import TheAlert from '../modal/TheAlert.vue';

export default {
    components: {
        TheAlert
    },
    props: ['comid'],
    data() {
        return {
            token: '',
            message: '',
            file: null,
            error: '',
            modalCloseType: '',
            modalSuccess: false,
            modalLoading: false,
            modalText: '',
            language: '',
        }
    },
    methods: {
        sendApproval(){
            this.modalText = 'Loading...';
            this.modalLoading = true;
            this.modalSuccess = false;
            this.modalCloseType = '';
            window.$('#alert').modal('show');
            this.token = window.$cookies.get('token');
            if(this.token === null){
                this.token = window.$cookies.get("itkmitlcommunitytokenbrowser");
            }
            const data = new FormData();
            data.append('community', this.comid);
            data.append('message', this.message);
            data.append('file', this.file);
            axios.post($http + 'api/membership/approval-request/', data, {
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                if (this.language === 'th') {
                    this.modalText = 'คำขออนุมัติถูกส่งเรียบร้อย';
                } else {
                    this.modalText = 'Approval request sended.';
                }
                this.modalLoading = false;
                this.modalSuccess = true;
                this.modalCloseType = 'close';
            }).catch((err) => {
                if (this.language === 'th') {
                    this.modalText = 'เกิดข้อผิดพลาดระหว่างการส่งคำขออนุมัติ';
                } else {
                    this.modalText = 'Failed to send approval request.';
                }
                this.modalLoading = false;
                this.modalSuccess = false;
                this.modalCloseType = '';
                this.error = err.response.data
            })
        },
        newFile(event){
            this.file = event.target.files[0]
        }
    },
    async mounted() {
        this.language = window.$cookies.get('itkmitlcommunitylanguage');
    },
}
</script>
<style scoped src="../../assets/main.css"></style>
<style scoped src="../../assets/form.css"></style>