<template>
    <div class="row">
        <div class="col-md-3">
            <img src="../../assets/back.svg" @click="this.$router.back()" class="mt-4 ml-3 d-none d-md-block back-button">
        </div>
        <div class="col-md-6 mt-4">
            <div class="not-bg" style="padding: 30px;">
                <!-- PC -->
                <div class="row d-none d-lg-block">
                    <div class="col">
                        <h2>
                            <p class="language" v-if="language === 'th'">โหวตที่ได้รับ</p>
                            <p class="language" v-else>My Received Votes</p>
                        </h2>
                    </div>
                    <div class="col d-flex justify-content-end">
                        <div class="form-outline align-self-start">
                            <input v-if="language === 'th'" class="form-control" id="searchMem" rows="4" v-model="searchMem" placeholder="ค้นหา..." />
                            <input v-else class="form-control" id="searchMem" rows="4" v-model="searchMem" placeholder="Search..." />
                        </div>
                        <div class="align-self-end">
                            <button @click="searchMember(info.id)" class="btn btn-primary">
                                <div v-if="memberSearching" class="spinner-border spinner-border-sm" role="status">
                                    <span class="sr-only">Loading...</span>
                                </div>
                                <i v-else class="fas fa-search"></i>
                            </button>
                        </div>
                    </div>
                </div>
                <!-- Mobile -->
                <div class="row d-lg-none">
                    <div>
                        <i @click="this.$router.back()" style="cursor: pointer;" class="fas fa-times-circle fa-2x col d-flex justify-content-end mt-3 x-button d-md-none"></i>
                    </div>
                    <div class="d-flex justify-content-center mb-2">
                        <h2>
                            <p class="language" v-if="language === 'th'">โหวตที่ได้รับ</p>
                            <p class="language" v-else>My Received Votes</p>
                        </h2>
                    </div>
                    <div class="d-flex justify-content-center">
                        <div class="form-outline align-self-start">
                            <input v-if="language === 'th'" class="form-control" id="searchMem" rows="4" v-model="searchMem" placeholder="ค้นหา..." />
                            <input v-else class="form-control" id="searchMem" rows="4" v-model="searchMem" placeholder="Search..." />
                        </div>
                        <div class="align-self-end">
                            <button @click="searchMember(info.id)" class="btn btn-primary">
                                <div v-if="memberSearching" class="spinner-border spinner-border-sm" role="status">
                                    <span class="sr-only">Loading...</span>
                                </div>
                                <i v-else class="fas fa-search"></i>
                            </button>
                        </div>
                    </div>
                </div>
                <!-- End -->
                <div>
                    <div class="form-outline mt-2 mb-2">
                        <label class="form-label">
                            <p class="language" v-if="language === 'th'">กิจกรรม</p>
                            <p class="language" v-else>Event</p>
                        </label>
                        <select id="status" class="form-control select" v-model="selectedEvent">
                            <option value="none" selected>
                                <p class="language" v-if="language === 'th'">ทุกกิจกรรม</p>
                                <p class="language" v-else>All events</p>
                            </option>
                            <option v-for="event in eventList" :key="event.id" :value="event.id">
                                <p class="language" v-if="language === 'th'">{{ event.name_th }}</p>
                                <p class="language" v-else>{{ event.name_en }}</p>
                            </option>
                        </select>
                    </div>
                </div>
                <div>
                    <div class="d-flex">
                        <p class="language" v-if="language === 'th'">จำนวนโหวตทั้งหมด</p>
                        <p class="language" v-else>Total Votes</p>: {{ voteCount }}
                    </div>
                </div>
            </div>
            <div v-if="!loadingVote" class="row">
                <div v-for="vote in voteData" :key="vote.id" class="col-lg-4 mt-4" style="padding: 0 12px 0 12px;">
                    <div class="not-bg" style="padding: 20px;">
                        <p>{{ vote.comment }}</p>
                    </div>
                </div>
            </div>
            <div v-else>
                <div class="d-flex justify-content-center">
                    <div class="spinner-border m-5" role="status">
                        <span class="sr-only">Loading...</span>
                    </div>
                </div>
                <div class="d-flex justify-content-center">
                    <h3>Loading...</h3>
                </div>
            </div>
        </div>
        <div class="col-md"></div>
    </div>
</template>
<script>
import axios from 'axios';
import { $http } from '../../assets/variables.js';

export default {
    data() {
        return {
            selectedEvent: 'none',
            eventList: [],
            token: '',
            language: '',
            error: '',
            voteData: [],
            voteCount: '',
            loadingVote: false,
        }
    },
    methods: {
        getData() {
            this.loadingVote = true;
            this.token = window.$cookies.get('token');
            if(this.token === null){
                this.token = window.$cookies.get("itkmitlcommunitytokenbrowser");
            }
            if (this.token === null || this.token === '' || this.token === undefined) {
                this.$route.push('/login');
            }
            this.language = window.$cookies.get('itkmitlcommunitylanguage');
            axios.get($http + 'api/misc/vote/me', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.voteCount = response.data.length
                var vote;
                for(vote in response.data){
                    if(response.data[vote].comment === null || response.data[vote].comment === ''){
                        continue
                    } else {
                        this.voteData.push(response.data[vote])
                    }
                }
                var event;
                var check = [];
                for(event in response.data){
                    var keep = response.data[event];
                    if(check.find((eventing) => eventing === keep.meta.event) !== undefined){
                        continue
                    } else {
                        check.push(keep.meta.event)
                    }
                }
                for(event in check){
                    axios.get($http + 'api/community/community/' + check[event], {
                        headers: {
                            Authorization: this.token
                        }
                    }).then((response) => {
                        var newEvent = {
                            id: response.data.id,
                            name_th: response.data.name_th,
                            name_en: response.data.name_en
                        };
                        this.eventList.push(newEvent);
                    })
                }
                this.loadingVote = false;
            }).catch(() => {
                if (this.language === 'th') {
                    this.error = 'เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่';
                } else {
                    this.error = 'Data fetching error, please try again.';
                }
            })
        },
    },
    async mounted() {
        this.getData();
    },
    watch: {
        $route() {
            this.getData();
        },
        selectedEvent() {
            this.loadingVote = true;
            this.voteData = [];
            if(this.selectedEvent === 'none'){
                axios.get($http + 'api/misc/vote/me', {
                    headers: {
                        Authorization: this.token
                    }
                }).then((response) => {
                    var keep = response.data
                    var vote;
                    for(vote in keep){
                        if(keep[vote].comment === null || keep[vote].comment === ''){
                            continue
                        } else {
                            this.voteData.push(keep[vote])
                        }
                    }
                    this.voteCount = keep.length;
                    this.loadingVote = false;
                })
            } else {
                axios.get($http + 'api/misc/vote/me', {
                    headers: {
                        Authorization: this.token
                    }
                }).then((response) => {
                    var keep = response.data
                    var vote;
                    var eventVote = keep.filter((eventing) => eventing.meta.event === this.selectedEvent);
                    for(vote in eventVote){
                        if(eventVote[vote].comment === null || eventVote[vote].comment === ''){
                            continue
                        } else {
                            this.voteData.push(eventVote[vote])
                        }
                    }
                    this.voteCount = eventVote.length;
                    this.loadingVote = false;
                })
            }
        }
    }
}
</script>
<style scoped src="../../assets/main.css"></style>
<style scoped src="../../assets/form.css"></style>
<style scoped>
h2{
    margin: 0;
}

p{
    padding: 0;
    margin: 0;
}
.x-button{
    position: absolute;
    right: 20px;
    top: 80px;
}
</style>