<template>
    <div class="row mb-4">
        <div class="col-md">
            <img src="../../assets/back.svg" @click="this.$router.back()" class="mt-4 ml-3 d-none d-lg-block back-button">
        </div>
        <div class="col-md mt-4 not-bg form-padding">
            <h2 class="d-none d-lg-block">
                <p class="language" v-if="language === 'th'">เอกสารสำหรับการขออนุมัติ</p>
                <p class="language" v-else>Document for approval</p>
            </h2>

            <div class="d-lg-none row">
                <h2 class="mb-2 col-9">
                    <p class="language" v-if="language === 'th'">เอกสารสำหรับการขออนุมัติ</p>
                    <p class="language" v-else>Document for approval</p>
                </h2>
                <i @click="this.$router.back()" style="cursor: pointer;" class="fas fa-times-circle fa-2x col d-flex justify-content-end mt-2"></i>
            </div>

            <p class="language" v-if="language === 'th'">ใส่ข้อมูลทุกช่องให้ครบถ้วนจากนั้นตัวเว็บไซต์จะสร้างเอกสารขึ้นมาสำหรับดาวน์โหลด คุณยังต้องนำเอกสารตัวนี้ไปให้อาจารย์ที่ปรึกษาเซ็นรับรองด้วยตัวเอง</p>
            <p class="language" v-else>Input all the data needed and the website will create a document for you to download for requesting an approval. You still have to take this to your advisor for his/her signature.</p>
            <p style="color: red; font-weight: bold;" v-if="language !== 'th'">The Document is in thai so please input everything in thai.</p>
            <div class="form-outline mt-4">
                <label class="form-label" for="objective">
                    <p class="language" v-if="language === 'th'">วัตถุประสงค์</p>
                    <p class="language" v-else>Objective</p>
                </label>
                <textarea v-if="language === 'th'" class="form-control" id="objective" rows="4" v-model="objective" placeholder="วัตถุประสงค์ของชุมนุมนี้คืออะไร?"></textarea>
                <textarea v-else class="form-control" id="objective" rows="4" v-model="objective" placeholder="What is this club objective?"></textarea>
                <p v-if="error.objective" class="error">{{ error.objective[0] }}</p>
            </div>

            <div class="form-outline mt-4">
                <label class="form-label" for="objective_list">
                    <p class="language" v-if="language === 'th'">รายการวัตถุประสงค์</p>
                    <p class="language" v-else>Objective List</p>
                </label>
                <textarea v-if="language === 'th'" class="form-control" id="objective_list" rows="4" v-model="objective_list" placeholder="รายการวัตถุประสงค์ ไม่ต้องเขียนเลขหรืออะไรนำหน้าให้คั่นด้วยปุ่ม enter"></textarea>
                <textarea v-else class="form-control" id="objective_list" rows="4" v-model="objective_list" placeholder="List of objective. No need to put 1. 2. just use enter."></textarea>
                <p v-if="error.objective_list" class="error">{{ error.objective_list[0] }}</p>
            </div>

            <div class="form-outline mt-4">
                <label class="form-label" for="room">
                    <p class="language" v-if="language === 'th'">สถานที่ตั้ง</p>
                    <p class="language" v-else>Location</p>
                </label>
                <input v-if="language === 'th'" type="text" id="room" class="form-control" v-model="room" placeholder="ห้องที่ชุมนุมของคุณวางแผนจะใช้งาน" />
                <input v-else type="text" id="room" class="form-control" v-model="room" placeholder="The Club's room you plan to use." />
                <p v-if="error.room" class="error">{{ error.room[0] }}</p>
            </div>

            <div class="form-outline mt-4">
                <label class="form-label" for="schedule">
                    <p class="language" v-if="language === 'th'">ช่วงเวลาดำเนินการ</p>
                    <p class="language" v-else>Schedule</p>
                </label>
                <input type="text" id="schedule" class="form-control" v-model="schedule" placeholder="ex: จันทร์-ศุกร์ 16.00-17.00น" />
                <p v-if="error.schedule" class="error">{{ error.schedule[0] }}</p>
            </div>

            <div class="form-outline mt-4">
                <label class="form-label" for="plan_list">
                    <p class="language" v-if="language === 'th'">แผนการดำเนินงาน</p>
                    <p class="language" v-else>Plan List</p>
                </label>
                <textarea v-if="language === 'th'" class="form-control" id="plan_list" rows="4" v-model="plan_list" placeholder="รายการแผนการดำเนินงาน ไม่ต้องเขียนเลขหรืออะไรนำหน้าให้คั่นด้วย enter"></textarea>
                <textarea v-else class="form-control" id="plan_list" rows="4" v-model="plan_list" placeholder="List of plan. No need to put 1. 2. just use enter."></textarea>
                <p v-if="error.plan_list" class="error">{{ error.plan_list[0] }}</p>
            </div>

            <div class="form-outline mt-4">
                <label class="form-label" for="merit">
                    <p class="language" v-if="language === 'th'">ผลที่คาดว่าจะได้รับ</p>
                    <p class="language" v-else>Merit</p>
                </label>
                <textarea v-if="language === 'th'" class="form-control" id="merit" rows="4" v-model="merit" placeholder="สิ่งที่คุณคาดว่าจะได้รับจากการเข้าร่วมชุมนุมนี้"></textarea>
                <textarea v-else class="form-control" id="merit" rows="4" v-model="merit" placeholder="Things you hope to get from joining this club."></textarea>
                <p v-if="error.merit" class="error">{{ error.merit[0] }}</p>
            </div>

            <div class="form-outline mt-4">
                <label class="form-label">
                    <p class="language" v-if="language === 'th'">อาจารย์ที่ปรึกษา</p>
                    <p class="language" v-else>Club Advisor</p>
                </label>
                <select class="form-control select" v-model="advisor">
                    <option v-if="advisor_list.length === 0" value="none">
                        <p class="language" v-if="language === 'th'">กำลังโหลดรายชื่ออาจารย์</p>
                        <p class="language" v-else>Loading advisor list</p>
                    </option>
                    <option v-for="advice in advisor_list" :key="advice.id" :value="advice.id">{{ advice.name }}</option>
                </select>
                <p v-if="error.advisor" class="error">{{ error.advisor[0] }}</p>
            </div>

            <div class="form-outline mt-4">
                <button v-if="!doc_exist" @click="createDoc" class="btn btn-primary btn-block">
                    <p class="language" v-if="language === 'th'">สร้างเอกสาร</p>
                    <p class="language" v-else>Generate Document</p>
                </button>
                <button v-if="doc_exist" @click="editDoc(info.id)" class="btn btn-primary btn-block">
                    <p class="language" v-if="language === 'th'">บันทึกการเปลี่ยนแปลง</p>
                    <p class="language" v-else>Save Change</p>
                </button>
                <button v-if="doc_exist" @click="downloadDoc()" class="btn btn-primary btn-block">
                    <p class="language" v-if="language === 'th'">ดาวน์โหลดเอกสาร</p>
                    <p class="language" v-else>Download Document</p>
                </button>
                <button v-if="doc_exist" @click="deleteDoc(info.id)" class="btn btn-danger btn-block">
                    <p class="language" v-if="language === 'th'"></p>
                    <p class="language" v-else>Delete Document</p>
                </button>
            </div>
        </div>
        <div class="col-md"></div>
        <!-- Modal -->
        <the-alert :id="'alert'" :closetype="modalCloseType" :success="modalSuccess" :loading="modalLoading" :text="modalText" :url="modalUrl">
            <template v-slot:header></template>
        </the-alert>
    </div>
</template>
<script>
import axios from 'axios';
import { $http } from '../../assets/variables.js';
import TheAlert from '../modal/TheAlert.vue';

export default {
    components: {
        TheAlert
    },
    props: ['clubid'],
    data() {
        return {
            token: '',
            info: '',
            objective: '',
            objective_list: '',
            room: '',
            schedule: '',
            plan_list: '',
            merit: '',
            advisor: 'none',
            advisor_list: [],
            doc_exist: false,
            error: '',
            modalCloseType: '',
            modalSuccess: false,
            modalLoading: false,
            modalText: '',
            language: '',
        }
    },
    methods: {
        createDoc(){
            this.modalCloseType = '';
            this.modalSuccess = false;
            this.modalLoading = true;
            this.modalText = 'Loading...';
            window.$('#alert').modal('show');
            axios.post($http + 'api/generator/docx/', {
                objective: this.objective,
                objective_list: this.objective_list,
                room: this.room,
                schedule: this.schedule,
                plan_list: this.plan_list,
                merit: this.merit,
                club: this.clubid,
                advisor: this.advisor
            }, {
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                this.modalCloseType = 'refresh';
                this.modalSuccess = true;
                this.modalLoading = false;
                if (this.language === 'th') {
                    this.modalText = 'เอกสารถูกสร้างขึ้นมาเรียบร้อย';
                } else {
                    this.modalText = 'Document created.';
                }
            }).catch((err) => {
                this.modalCloseType = '';
                this.modalSuccess = false;
                this.modalLoading = false;
                if (this.language === 'th') {
                    this.modalText = 'เกิดข้อผิดพลาดระหว่างสร้างเอกสาร';
                } else {
                    this.modalText = 'Failed to create document.';
                }
                this.error = err.response.data;
            })
        },
        downloadDoc(){
            window.location.replace(this.info.document)
        },
        editDoc(id){
            this.modalCloseType = '';
            this.modalSuccess = false;
            this.modalLoading = true;
            this.modalText = 'Loading...';
            window.$('#alert').modal('show');
            axios.patch($http + 'api/generator/docx/' + id + '/', {
                objective: this.objective,
                objective_list: this.objective_list,
                room: this.room,
                schedule: this.schedule,
                plan_list: this.plan_list,
                merit: this.merit,
                advisor: this.advisor
            }, {
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                this.modalCloseType = 'refresh';
                this.modalSuccess = true;
                this.modalLoading = false;
                if (this.language === 'th') {
                    this.modalText = 'แก้ไขเอกสารเรียบร้อย';
                } else {
                    this.modalText = 'Document edited.';
                }
            }).catch((err) => {
                this.modalCloseType = '';
                this.modalSuccess = false;
                this.modalLoading = false;
                if (this.language === 'th') {
                    this.modalText = 'เกิดข้อผิดพลาดระหว่างแก้ไขเอกสาร';
                } else {
                    this.modalText = 'Failed to edit document.';
                }
                this.error = err.response.data;
            })
        },
        deleteDoc(id){
            this.modalCloseType = '';
            this.modalSuccess = false;
            this.modalLoading = true;
            this.modalText = 'Loading...';
            window.$('#alert').modal('show');
            axios.delete($http + 'api/generator/docx/' + id + '/', {
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                this.modalCloseType = 'refresh';
                this.modalSuccess = true;
                this.modalLoading = false;
                if (this.language === 'th') {
                    this.modalText = 'เอกสารถูกลบเรียบร้อย';
                } else {
                    this.modalText = 'Document deleted.';
                }
            }).catch(() => {
                this.modalCloseType = '';
                this.modalSuccess = false;
                this.modalLoading = false;
                if (this.language === 'th') {
                    this.modalText = 'เกิดข้อผิดพลาดระหว่างลบเอกสาร';
                } else {
                    this.modalText = 'Failed to delete document please try again later.';
                }
            })
        },
        getdata(){
            this.token = window.$cookies.get('token');
            if(this.token === null){
                this.token = window.$cookies.get("itkmitlcommunitytokenbrowser");
            }
            axios.get($http + 'api/user/user?user_group=lecturer', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.advisor_list = response.data;
            }).catch(() => {
                this.modalCloseType = '';
                this.modalSuccess = false;
                this.modalLoading = false;
                if (this.language === 'th') {
                    this.modalText = "เกิดข้อผิดพลาดระหว่างดึงข้อมูลอาจารย์ที่ปรึกษา";
                } else {
                    this.modalText = "Data fetching error. Can't get advisor data.";
                }
                window.$('#alert').modal('show');
            })
            axios.get($http + 'api/generator/docx?club=' + this.clubid, {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.doc_exist = true;
                this.info = response.data[0];
                this.objective = this.info.objective;
                this.objective_list = this.info.objective_list;
                this.room = this.info.room;
                this.schedule = this.info.schedule;
                this.plan_list = this.info.plan_list;
                this.merit = this.info.merit;
                this.advisor = this.info.advisor;
            }).catch(() => {
                this.doc_exist = false;
            })
        },
    },
    async mounted() {
        this.language = window.$cookies.get('itkmitlcommunitylanguage');
        this.getdata();
    },
}
</script>
<style scoped src="../../assets/main.css"></style>
<style scoped src="../../assets/form.css"></style>
<style scoped>
p.error{
    color: red;
}
</style>