// Library import
import { createApp } from 'vue';
import { createRouter, createWebHistory } from 'vue-router';
import VueCookies from 'vue-cookies';
import axios from 'axios';
import VueAxios from 'vue-axios';
import App from './App.vue';
// Directory import
import TheClubs from './components/ui/TheClubs.vue';
import LoginPage from './components/ui/LoginPage.vue';
import LogOut from './components/nav/LogOut.vue';
import TheEvents from './components/ui/TheEvents.vue';
import TheLabs from './components/ui/TheLabs.vue';
import MyProfile from './components/ui/MyProfile.vue';
import Community from './components/ui/Community.vue';
// import OfficialClubInfo from './components/ui/official/OfficialClubInfo.vue';
// import UnofficialClubInfo from './components/ui/unofficial/UnofficialClubInfo.vue';
import EventInfo from './components/ui/official/EventInfo.vue';
// import EventUnapproved from './components/ui/unofficial/EventUnapproved.vue';
import LabInfo from './components/ui/official/LabInfo.vue';
import ComEventInfo from './components/ui/official/ComEventInfo.vue';
import ClubForm from './components/form/ClubForm.vue';
import EventForm from './components/form/EventForm.vue';
import LabForm from './components/form/LabForm.vue';
import ProfileForm from './components/form/ProfileForm.vue';
import NotFound from './components/nav/NotFound.vue';
import CommunityEventForm from './components/form/CommunityEventForm.vue';
import SearchPage from './components/ui/SearchPage.vue';
import StudentCommittee from './components/ui/StudentCommittee.vue';
import ToCommunity from './components/nav/ToCommunity.vue';
import Invitation from './components/ui/Invitation.vue';
import Notification from './components/ui/Notification.vue';
import JoinQR from './components/nav/JoinQR.vue';
// import AboutTab from './components/ui/tabs/AboutTab.vue';
import AnnouncementTab from './components/ui/tabs/AnnouncementTab.vue';
import AlbumTab from './components/ui/tabs/AlbumTab.vue';
import EventTab from './components/ui/tabs/EventTab.vue';
import MemberTab from './components/ui/tabs/MemberTab.vue';
import RequestTab from './components/ui/tabs/RequestTab.vue';
import CommentTab from './components/ui/tabs/CommentTab.vue';
import QRCodeTab from './components/ui/tabs/QRCodeTab.vue';
import ViewAlbum from './components/album/ViewAlbum.vue';
import AlbumForm from './components/form/AlbumForm.vue';
import ClubDocument from './components/form/ClubDocument.vue';
import ApprovalForm from './components/form/ApprovalForm.vue';
import ComEventFormEdit from './components/form/ComEventFormEdit.vue';
import TheFAQ from './components/ui/TheFAQ.vue';
import TheVotes from './components/ui/TheVotes.vue';
import EmailPreference from './components/ui/EmailPreference.vue';
import TheUnsubscribe from './components/ui/TheUnsubscribe.vue';
// style import
// import 'mdb-ui-kit';
// import 'mdb-ui-kit/css/mdb.min.css';
import './assets/main.css'
import 'bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

const router = createRouter({
    history: createWebHistory(),
    routes: [
        { path: '/', redirect: '/clubs' },
        { path: '/clubs', component: TheClubs },
        { path: '/community/:comtype/:comurl', component: Community, props: true,
        children: [
            { path: '', component: AnnouncementTab },
            { path: 'album', component: AlbumTab },
            { path: 'event', component: EventTab },
            { path: 'member', component: MemberTab },
            { path: 'request', component: RequestTab },
            { path: 'comment', component: CommentTab },
            { path: 'qrcode', component: QRCodeTab },
          ]
        },
        { path: '/club/:cluburl', component: Community, props: true,
        children: [
            { path: '', component: AnnouncementTab },
            { path: 'album', component: AlbumTab },
            { path: 'event', component: EventTab },
            { path: 'member', component: MemberTab },
            { path: 'request', component: RequestTab },
          ]
        },
        // { path: '/officialclub/:cluburl',  component: OfficialClubInfo, props: true },
        // { path: '/unofficialclub/:clubid',  component: UnofficialClubInfo, props: true },
        { path: '/clubform/:clubid', component: ClubForm, props: true},
        { path: '/qrlogin/:qr', component: LoginPage, props: true},
        { path: '/login', component: LoginPage },
        { path: '/logout', component: LogOut },
        { path: '/events', component: TheEvents},
        { path: '/event/:eventurl', component: EventInfo, props: true,
        children: [
            { path: '', component: AnnouncementTab },
            { path: 'album', component: AlbumTab },
            { path: 'announcement', component: AnnouncementTab },
            { path: 'comment', component: CommentTab },
            { path: 'member', component: MemberTab },
            { path: 'request', component: RequestTab },
            { path: 'qrcode', component: QRCodeTab },
        ]
        },
        { path: '/communityevent/:eventurl', component: ComEventInfo, props: true,
        children: [
            { path: '', component: AnnouncementTab },
            { path: 'album', component: AlbumTab },
            { path: 'announcement', component: AnnouncementTab },
            { path: 'comment', component: CommentTab },
            { path: 'member', component: MemberTab },
            { path: 'request', component: RequestTab },
            { path: 'qrcode', component: QRCodeTab },
        ]
        },
        // { path: '/eventunapprove/:eventid', component: EventUnapproved, props: true },
        { path: '/eventform/:eventid', component: EventForm, props: true},
        { path: '/comeventform/:eventid', component: ComEventFormEdit, props: true},
        { path: '/labs', component: TheLabs },
        { path: '/FAQ', component: TheFAQ },
        { path: '/votes', component: TheVotes },
        { path: '/email', component: EmailPreference },
        { path: '/unsubscribe', component: TheUnsubscribe },
        { path: '/lab/:laburl', component: LabInfo, props: true, 
        children: [
            { path: '', component: AnnouncementTab },
            { path: 'album', component: AlbumTab },
            { path: 'announcement', component: AnnouncementTab },
            { path: 'event', component: EventTab },
            { path: 'member', component: MemberTab },
            { path: 'request', component: RequestTab },
        ]
        },
        { path: '/labform/:labid', component: LabForm, props: true},
        { path: '/profile/:userId', component: MyProfile, props: true },
        { path: '/profileform/:profileid', component: ProfileForm, props: true},
        { path: '/communityeventform/:communityid', component: CommunityEventForm, props: true },
        { path: '/search', component: SearchPage},
        { path: '/studentcommittee', component: StudentCommittee },
        { path: '/tocommunity/:id', component: ToCommunity, props: true },
        { path: '/invitation/:id', component: Invitation, props: true },
        { path: '/notification', component: Notification },
        { path: '/join/:keys', component: JoinQR, props: true },
        { path: '/album/:comid/:albumid', component: ViewAlbum, props: true},
        { path: '/albumform/:comid/:albumid/:event/:comevent', component: AlbumForm, props: true },
        { path: '/clubdocument/:clubid', component: ClubDocument, props: true },
        { path: '/approve/:comid', component: ApprovalForm, props: true },
        { path: '/:notFound(.*)', component: NotFound }
    ],
});

const app = createApp(App);

app.use(router, axios, VueAxios, VueCookies);

app.mount('#app');
