<template>
    <div class="row mb-4">
        <div class="col-md">
            <img src="../../assets/back.svg" @click="this.$router.back()" class="mt-4 ml-3 d-none d-lg-block back-button">
        </div>
        <div class="col-md not-bg mt-4 form-padding">
            <!-- <div v-if="info">
                {{ info }}
            </div> -->
            <h2 class="mb-2 d-none d-lg-block">
                <p class="language" v-if="language === 'th'">แก้ไขกิจกรรมชุมนุม / ห้องปฏิบัติการ</p>
                <p class="language" v-else>Edit Community Event</p>
            </h2>

            <div class="d-lg-none row">
                <h2 class="mb-2 col-9">
                    <p class="language" v-if="language === 'th'">แก้ไขกิจกรรมชุมนุม / ห้องปฏิบัติการ</p>
                    <p class="language" v-else>Edit Community Event</p>
                </h2>
                <i @click="this.$router.back()" style="cursor: pointer;" class="fas fa-times-circle fa-2x col d-flex justify-content-end mt-2"></i>
            </div>

            <label class="custom-file-label mb-4" for="inputGroupFile01">
                <p class="language" v-if="language === 'th'">โลโก้</p>
                <p class="language" v-else>Logo</p>
            </label>
            <div class="form-image mb-4" v-if="current_logo !== null">
                <img class="image" ref="image" :src="current_logo">
            </div>
            <div v-if="preview !== null" class="mb-4">
                <p class="language" v-if="language === 'th'">ตัวอย่าง</p>
                <p class="language" v-else>Preview</p>
                <img class="mb-4" :src="preview" style="width: 175px; height: 175px;"><br>
                <button @click="cancelPicture">
                    <p class="language" v-if="language === 'th'">เอารูปออก</p>
                    <p class="language" v-else>Remove Image</p>
                </button><br>
            </div>
            <div v-if="preview === null" class="input-group mb-4">
                <div class="custom-file">
                    <input type="file" class="custom-file-input" id="inputGroupFile01" ref="logoPic" @change="logoPic"
                    aria-describedby="inputGroupFileAddon01" accept="image/*">
                </div>
            <p class="error" v-if="errorMessage.logo">{{ errorMessage.logo[0] }}</p>
            </div>
            
            <label class="custom-file-label mb-4" for="inputGroupFile02">
                <p class="language" v-if="language === 'th'">รูปปก</p>
                <p class="language" v-else>Banner</p>
            </label>
            <img v-if="current_banner !== null" class="banner mb-4" :src="current_banner">
            <button v-if="cBanner" class="mb-4" @click="cancelBanner()">
                <p class="language" v-if="language === 'th'">เอารูปออก</p>
                <p class="language" v-else>Remove Image</p>
            </button>
            <div v-else class="input-group mb-4">
                <div class="custom-file">
                    <input type="file" class="custom-file-input" id="inputGroupFile02" ref="bannerPic" @change="bannerPic"
                    aria-describedby="inputGroupFileAddon01" accept="image/*">
                </div>
            <p class="error" v-if="errorMessage.banner">{{ errorMessage.banner[0] }}</p>
            </div>

           <div class="row">
                <div class="col">
                <div class="form-outline mb-4">
                    <label class="form-label" for="name_th">
                        <p class="language" v-if="language === 'th'">ชื่อกิจกรรม (ไทย)</p>
                        <p class="language" v-else>Event Name (Thai)</p>
                    </label>
                    <input type="text" id="name_th" class="form-control" v-model="name_th" />
                    <p class="error" v-if="errorMessage.name_th">{{ errorMessage.name_th[0] }}</p>
                </div>
                </div>
                <div class="col">
                <div class="form-outline mb-4">
                    <label class="form-label" for="name_en">
                        <p class="language" v-if="language === 'th'">ชื่อกิจกรรม (อังกฤษ)</p>
                        <p class="language" v-else>Event Name (English)</p>
                    </label>
                    <input type="text" id="name_en" class="form-control" v-model="name_en" />
                    <p class="error" v-if="errorMessage.name_en">{{ errorMessage.name_en[0] }}</p>
                </div>
                </div>
            </div>

            <div class="form-outline mb-4">
                <label class="form-label" for="description">
                    <p class="language" v-if="language === 'th'">รายละเอียดกิจกรรม</p>
                    <p class="language" v-else>Event Description</p>
                </label>
                <textarea class="form-control" id="description" rows="4" v-model="description"></textarea>
                <p class="error" v-if="errorMessage.description">{{ errorMessage.description[0] }}</p>
            </div>

            <div class="form-outline mb-4">
                <label class="form-label" for="externallinks">
                    <p class="language" v-if="language === 'th'">ลิ้งค์อิ่น ๆ ที่เกี่ยวข้อง</p>
                    <p class="language" v-else>External Links</p>
                </label>
                <textarea class="form-control" id="external_links" rows="4" v-model="external_links"></textarea>
                <p class="error" v-if="errorMessage.external_links">{{ errorMessage.external_links[0] }}</p>
            </div>

            <div v-if="info.is_approved === true" class="form-outline mb-4">
                <label class="form-label" for="url_id">URL ID</label>
                <input type="text" id="url_id" class="form-control" v-model="url_id" />
                <p class="error" v-if="errorMessage.url_id">{{ errorMessage.url_id[0] }}</p>
            </div>

            <div class="form-outline mb-4">
                <label class="form-label" for="location">
                    <p class="language" v-if="language === 'th'">สถานที่จัด</p>
                    <p class="language" v-else>Location</p>
                </label>
                <input type="text" id="location" class="form-control" v-model="location" />
                <p class="error" v-if="errorMessage.location">{{ errorMessage.location[0] }}</p>
            </div>

            <div class="row">
                <div class="col-lg">
                    <div class="form-outline mb-4">
                        <label class="form-label" for="startdate">
                            <p class="language" v-if="language === 'th'">วันเริ่มกิจกรรม</p>
                            <p class="language" v-else>Start Date</p>
                        </label>
                        <input type="date" id="start_date" class="form-control" v-model="start_date" placeholder="dd-mmm-yyyy" />
                        <p class="error" v-if="errorMessage.start_date">{{ errorMessage.start_date[0] }}</p>
                    </div>
                </div>

                <div class="col-lg">
                    <div class="form-outline mb-4">
                        <label class="form-label" for="enddate">
                            <p class="language" v-if="language === 'th'">วันสิ้นสุดกิจกรรม</p>
                            <p class="language" v-else>End Date</p>
                        </label>
                        <input type="date" id="end_date" class="form-control" v-model="end_date" placeholder="dd-mmm-yyyy" />
                        <p class="error" v-if="errorMessage.end_date">{{ errorMessage.end_date[0] }}</p>
                    </div>
                </div>
            </div>

            <div class="row">
                <div class="col-xl">
                    <div class="form-outline mb-4">
                        <label class="form-label" for="starttime">
                            <p class="language" v-if="language === 'th'">เวลาเริ่มกิจกรรม</p>
                            <p class="language" v-else>Start Time</p>
                        </label>
                        <input type="time" id="start_time" class="form-control" v-model="start_time" placeholder="dd-mmm-yyyy" />
                        <p class="error" v-if="errorMessage.start_time">{{ errorMessage.start_time[0] }}</p>
                    </div>
                </div>

                <div class="col-xl">
                    <div class="form-outline mb-4">
                        <label class="form-label" for="endtime">
                            <p class="language" v-if="language === 'th'">เวลาสิ้นสุดกิจกรรม</p>
                            <p class="language" v-else>End Time</p>
                        </label>
                        <input type="time" id="end_time" class="form-control" v-model="end_time" placeholder="dd-mmm-yyyy" />
                        <p class="error" v-if="errorMessage.end_time">{{ errorMessage.end_time[0] }}</p>
                    </div>
                </div>
            </div>

            <div class="form-check d-flex justify-content-start mb-4">
                <input
                class="form-check-input mr-2"
                type="checkbox"
                value=""
                id="is_accepting_requests"
                v-model="is_accepting_requests"
                />
                <label class="form-check-label" for="is_accepting_requests">
                    <p class="language" v-if="language === 'th'">กำลังรับคำขอ?</p>
                    <p class="language" v-else>Is accepting request?</p>
                </label>
            </div>
            <p class="error" v-if="errorMessage.is_accepting_requests">{{ errorMessage.is_accepting_requests[0] }}</p>


            <div class="form-check d-flex justify-content-start mb-4">
                <input
                class="form-check-input mr-2"
                type="checkbox"
                value=""
                id="is_cancelled"
                v-model="is_cancelled"
                />
                <label class="form-check-label" for="is_cancelled">
                    <p class="language" v-if="language === 'th'">กิจกรรมถูกยกเลิก?</p>
                    <p class="language" v-else>Is cancelled?</p>
                </label>
                <p class="error" v-if="errorMessage.is_cancelled">{{ errorMessage.is_cancelled[0] }}</p>
            </div>

            <div v-if="info.is_approved === true" class="form-check d-flex justify-content-start mb-4">
                <input
                class="form-check-input mr-2"
                type="checkbox"
                value=""
                id="is_publicly_visible"
                v-model="is_publicly_visible"
                />
                <label class="form-check-label" for="is_publicly_visible">
                    <p class="language" v-if="language === 'th'">สามารถมองเห็นได้อย่างสาธารณะ</p>
                    <p class="language" v-else>Is publicly visible?</p>
                </label>
                <p class="error" v-if="errorMessage.is_publicly_visible">{{ errorMessage.is_publicly_visible[0] }}</p>
            </div>

            <div v-if="allows_outside_participators !== null" class="form-check d-flex justify-content-start mb-4">
                <input
                class="form-check-input mr-2"
                type="checkbox"
                value=""
                id="allows_outside_participators"
                v-model="allows_outside_participators"
                />
                <label class="form-check-label" for="allows_outside_participators">
                    <p class="language" v-if="language === 'th'">อนุญาติให้บุคคลภายนอกเข้าร่วมได้?</p>
                    <p class="language" v-else>allows outside participator?</p>
                </label>
                <p class="error" v-if="errorMessage.allows_outside_participators">{{ errorMessage.allows_outside_participators[0] }}</p>
            </div>

            <div class="form-outline mb-4">
                <label class="form-label">
                    <p class="language" v-if="language === 'th'">ประเภทกิจกรรม</p>
                    <p class="language" v-else>Event Type</p>
                </label>
                <select id="event_type" class="form-control select" v-model="event_type">
                    <option v-for="eventtyp in eventtype" :key="eventtyp.id" :value="eventtyp.id">
                        <p class="language" v-if="language === 'th'">{{ eventtyp.title_th }}</p>
                        <p class="language" v-else>{{ eventtyp.title_en }}</p>
                    </option>
                </select>
                <p class="error" v-if="errorMessage.event_type">{{ errorMessage.event_type[0] }}</p>
            </div>
            
            <div class="form-outline mb-4">
                <label class="form-label">
                    <p class="language" v-if="language === 'th'">กลุ่มกิจกรรม</p>
                    <p class="language" v-else>Event Series</p>
                </label>
                <select id="event_series" class="form-control select" v-model="event_series">
                    <option v-for="eventserie in eventseries" :key="eventserie.id" :value="eventserie.id">
                        <p class="language" v-if="language === 'th'">{{ eventserie.title_th }}</p>
                        <p class="language" v-else>{{ eventserie.title_en }}</p>
                    </option>
                </select>
                <p class="error" v-if="errorMessage.event_series">{{ errorMessage.event_series[0] }}</p>
            </div>
            

            <!-- Submit button -->
            <div>
                <p class="error" v-if="errorMessage.non_field_errors">{{ errorMessage.non_field_errors }}</p>
                <button @click="sendData" type="submit" class="btn btn-primary btn-block" v-if="checkEdit">
                    <p class="language" v-if="language === 'th'">บันทึกการแก้ไข</p>
                    <p class="language" v-else>Save Changes</p>
                </button>
                <button @click="sendData" type="submit" class="btn btn-primary btn-block" v-if="!checkDelete && !checkEdit">
                    <p class="language" v-if="language === 'th'">สร้างกิจกรรม</p>
                    <p class="language" v-else>Create Event</p>
                </button>
                <div class="d-flex justify-content-center seperator"></div>
                <button @click="check1deleteData" type="submit" class="btn btn-outline-danger btn-block mt-4" v-if="checkDelete">
                    <p class="language" v-if="language === 'th'">ลบกิจกรรม</p>
                    <p class="language" v-else>Delete Event</p>
                </button>
                <button type="submit" class="btn btn-outline-secondary btn-block mt-4" v-else disabled>
                    <p class="language" v-if="language === 'th'">ลบกิจกรรม</p>
                    <p class="language" v-else>Delete Event</p>
                </button>
            </div>
        </div>
        <div class="col-md"></div>
        <!-- Modal -->
        <the-alert :id="'alert'" :closetype="modalCloseType" :success="modalSuccess" :loading="modalLoading" :text="modalText" :url="modalUrl">
            <template v-slot:header></template>
        </the-alert>

        <the-confirm :id="'check1'">
            <h4 class="d-flex justify-content-center">
                <p class="language" v-if="language === 'th'">ต้องการลบกิจกรรม?</p>
                <p class="language" v-else>Delete the event?</p>
            </h4>
            <template v-slot:footer>
                <button data-dismiss="modal" class="btn btn-danger mr-4" style="width: 75px;" @click="check2deleteData()">
                    <p class="language" v-if="language === 'th'">ใช่</p>
                    <p class="language" v-else>Yes</p>
                </button>
                <button data-dismiss="modal" class="btn btn-outline-secondary ml-4" style="width: 75px;">
                    <p class="language" v-if="language === 'th'">ไม่</p>
                    <p class="language" v-else>No</p>
                </button>
            </template>
        </the-confirm>
        
        <the-confirm :id="'check2'">
            <h4 class="align-self-center">
                <p class="language" v-if="language === 'th'">การลบกิจกรรมจะส่งผลให้ลบข้อมูลอื่น ๆ ที่เกี่ยวข้องกับกิจกรรม ผู้เข้าร่วมกิจกรรมอาจต้องการเก็บข้อมูลนั้นไว้</p>
                <p class="language" v-else>Delete the event will delete all the event and other things related. Your event participator might want to keep the data.</p>
            </h4>
            <template v-slot:footer>
                <button data-dismiss="modal" class="btn btn-danger mr-4" style="width: 125px;" @click="check3deleteData()">
                    <p class="language" v-if="language === 'th'">ใช่ ลบมันซะ</p>
                    <p class="language" v-else>Yes, delete it.</p>
                </button>
                <button data-dismiss="modal" class="btn btn-outline-secondary ml-4" style="width: 125px;">
                    <p class="language" v-if="language === 'th'">ไม่</p>
                    <p class="language" v-else>No</p>
                </button>
            </template>
        </the-confirm>
        
        <the-confirm :id="'check3'">
            <h4 class="d-flex justify-content-center">
                <p class="language" v-if="language === 'th'">เช็ครอบสุดท้ายคุณแน่ใจว่าต้องการลบจริง ๆ ใช่ไหม คุณตั้งสถานะยกเลิกแทนที่จะลบได้นะ</p>
                <p class="language" v-else>Last check are you sure you want to delete it? You can just set to Cancelled.</p>
            </h4>
            <template v-slot:footer>
                <button data-dismiss="modal" class="btn btn-danger mr-4" style="width: 170px;" @click="deleteData()">
                    <p class="language" v-if="language === 'th'">ใช่ ฉันแน่ใจแล้ว</p>
                    <p class="language" v-else>Yes, I'm really sure.</p>
                </button>
                <button data-dismiss="modal" class="btn btn-outline-secondary ml-4" style="width: 170px;">
                    <p class="language" v-if="language === 'th'">ไม่</p>
                    <p class="language" v-else>No</p>
                </button>
            </template>
        </the-confirm>
    </div>
</template>
<script>
import Cropper from 'cropperjs';
import axios from 'axios';
import { $http } from '../../assets/variables.js';
import TheAlert from '../modal/TheAlert.vue';
import TheConfirm from '../modal/TheConfirm.vue';

export default {
    components: {
        TheAlert,
        TheConfirm
    },
    props: ['eventid'],
    data () {
        return {
            cropper: {},
            image: {},
            preview: null,
            token: '',
            info: '',
            current_logo: null,
            current_banner: null,
            name_th: '',
            name_en: '',
            description: '',
            external_links: '',
            url_id: '',
            logo: null,
            banner: null,
            is_accepting_requests: false,
            location: '',
            start_date: '',
            end_date: '',
            start_time: '',
            end_time: '',
            is_cancelled: false,
            is_publicly_visible: false,
            allows_outside_participators: null,
            event_type: null,
            event_series: null,
            eventtype: [],
            eventseries: [],
            errorMessage: '',
            createButtonText: 'Create Event',
            editButtonText: 'Edit Event',
            errorTexted: [],
            cBanner: false,
            modalCloseType: '',
            modalLoading: false,
            modalSuccess: false,
            modalText: '',
            modalUrl: ''
        }
    },
    methods: {
        errorText(){
            var error;
            for(error in this.errorMessage){
                console.log(error);
                var element = document.getElementById(error);
                element.classList.toggle('error-box');
            }
            this.errorTexted = this.errorMessage;
        },
        cancelError(){
            var error;
            for(error in this.errorTexted){
                console.log(error);
                var element = document.getElementById(error);
                element.classList.toggle('error-box');
            }
            this.errorMessage = [];
        },
        cancelPicture(){
            this.logo = null;
            this.current_logo = null;
            this.preview = null;
        },
        cancelBanner(){
            this.banner = null;
            this.current_banner = null;
            this.cBanner = false;
        },
        bannerPic(event){
            this.banner = event.target.files[0];
            var reader = new FileReader();
            reader.onload = (e) => {
                this.current_banner = e.target.result;
            }
            reader.readAsDataURL(this.banner)
            this.cBanner = true;
        },
        logoPic(event){
            this.logo = event.target.files[0];
            var reader = new FileReader();
            reader.onload = (e) => {
                this.current_logo = e.target.result;
            }
            reader.readAsDataURL(this.logo);
            setTimeout(() => {
                this.image = this.$refs.image;
                this.cropper = new Cropper(this.image, {
                    zoomable: false,
                    scalable: false,
                    aspectRatio: 1,
                    crop: () => {
                        const canvas = this.cropper.getCroppedCanvas();
                        this.preview = canvas.toDataURL("image/png");
                        canvas.toBlob((blob) => {
                            this.logo = blob
                        })
                        // this.logo = canvas.toDataURL("image/png");
                    }
                })
            }, 1000);
        },
        check1deleteData(){
            window.$('#check1').modal('show');
        },
        check2deleteData(){
            setTimeout(() => { window.$('#check2').modal('show'); }, 1000);
        },
        check3deleteData(){
            setTimeout(() => { window.$('#check3').modal('show'); }, 1000);
        },
        deleteData() {
            this.modalCloseType = '';
            this.modalLoading = true;
            this.modalSuccess = false;
            this.modalText = '';
            this.modalUrl = '';
            window.$('#alert').modal('show');
            axios.delete($http + 'api/community/event/community/' + this.eventid, {
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                setTimeout(() => {
                    this.modalCloseType = 'redirect';
                    this.modalLoading = false;
                    this.modalSuccess = true;
                    if (this.language === 'th') {
                        this.modalText = 'ลบกิจกรรมเรียบร้อยและข้อมูลทุกอย่างสูญหายไปตลอดกาล';
                    } else {
                        this.modalText = 'The event is deleted and all the data is lost forever.';
                    }
                    this.modalUrl = '/events';
                }, 3000);
            }
            ).catch(() => {
                this.modalCloseType = '';
                this.modalLoading = false;
                this.modalSuccess = false;
                if (this.language === 'th') {
                    this.modalText = 'เกิดข้อผิดพลาดระหว่างลบกิจกรรม';
                } else {
                    this.modalText = "Failed to delete event.";
                }
                this.modalUrl = '';
            });
        },
        sendData() {
            this.modalCloseType = '';
            this.modalLoading = true;
            this.modalSuccess = false;
            this.modalText = 'Loading...';
            this.modalUrl = '';
            window.$('#alert').modal('show');
            this.cancelError();
            this.token = window.$cookies.get('token');
            if(this.token === null){
                this.token = window.$cookies.get("itkmitlcommunitytokenbrowser");
            }
            const data = new FormData();
            data.append('name_th', this.name_th);
            data.append('name_en', this.name_en);
            data.append('description', this.description);
            data.append('external_links', this.external_links);
            if(this.url_id !== null){
                data.append('url_id', this.url_id);
            }
            if(this.logo !== null){
                data.append('logo', this.logo, 'logo.png');
            }
            if(this.banner !== null){
                data.append('banner', this.banner);
            }
            data.append('is_accepting_requests', this.is_accepting_requests);
            data.append('location', this.location);
            data.append('start_date', this.start_date);
            data.append('end_date', this.end_date);
            data.append('start_time', this.start_time);
            data.append('end_time', this.end_time);
            data.append('is_cancelled', this.is_cancelled);
            data.append('is_publicly_visible', this.is_publicly_visible);
            data.append('allows_outside_participators', this.allows_outside_participators);
            if(this.event_type !== null){
                data.append('event_type', this.event_type);
            }
            if(this.event_series !== null){
                data.append('event_series', this.event_series);
            }
            // var data = {
            //     name_th: this.name_th,
            //     name_en: this.name_en,
            //     description: this.description,
            //     external_links: this.external_links,
            //     url_id: this.url_id,
            //     logo: this.logo,
            //     banner: this.banner,
            //     is_accepting_requests: this.is_accepting_requests,
            //     location: this.location,
            //     start_date: this.start_date,
            //     end_date: this.end_date,
            //     start_time: this.start_time,
            //     end_time: this.end_time,
            //     is_cancelled: this.is_cancelled,
            //     is_publicly_visible: this.is_publicly_visible,
            //     allows_outside_participators: this.allows_outside_participators,
            //     event_type: this.event_type,
            //     event_series: this.event_series
            // };
            if(this.eventid !== 'create'){
                this.editButtonText = 'Loading.....'
                axios.patch($http + 'api/community/event/community/' + this.eventid + '/', data, {
                    headers: {
                        Authorization: this.token
                    }
                }).then(() => {
                    this.modalCloseType = 'redirect';
                    this.modalLoading = false;
                    this.modalSuccess = true;
                    if (this.language === 'th') {
                        this.modalText = 'แก้ไขกิจกรรมเรียบร้อย';
                    } else {
                        this.modalText = 'Event edited successfully.';
                    }
                    this.modalUrl = '/community/communityevent/' + this.info.id;
                }).catch((err) => {
                    this.modalCloseType = '';
                    this.modalLoading = false;
                    this.modalSuccess = false;
                    if (this.language === 'th') {
                        this.modalText = 'เกิดข้อผิดพลาดระหว่างแก้ไขกิจกรรม'
                    } else {
                        this.modalText = 'Failed to edit event.';
                    }
                    this.modalUrl = '';
                    this.errorMessage = err.response.data;
                    console.log(err);
                    this.editButtonText = 'Edit Event';
                    this.errorText();
                });
            } else {
                this.createButtonText = 'Loading.....'
                axios.post($http + 'api/community/event/community/', data, {
                    headers: {
                        Authorization: this.token
                    }
                }).then(() => {
                    this.modalCloseType = 'redirect';
                    this.modalLoading = false;
                    this.modalSuccess = true;
                    if (this.language === 'th') {
                            this.modalText = 'สร้างกิจกรรมเรียบร้อย';
                        } else {
                            this.modalText = 'Event created successfully.';
                        }
                    this.modalUrl = '/events';
                }).catch((err) => {
                    this.modalCloseType = '';
                    this.modalLoading = false;
                    this.modalSuccess = false;
                    if (this.language === 'th') {
                        this.modalText = 'เกิดข้อผิดพลาดระหว่างสร้างกิจกรรม';
                    } else {
                        this.modalText = 'Failed to create event.';
                    }
                    this.modalUrl = '';
                    this.errorMessage = err.response.data;
                    console.log(err);
                    this.createButtonText = 'Create Event';
                    this.errorText();
                });
            }
        },
        getData() {
            this.language = window.$cookies.get('itkmitlcommunitylanguage');
            this.token = window.$cookies.get('token');
            if(this.token === null){
                this.token = window.$cookies.get("itkmitlcommunitytokenbrowser");
            }
            if(this.eventid !== 'create'){
                axios.get($http + 'api/community/event/community/' + this.eventid, {
                    headers: {
                        Authorization: this.token
                    }
                }).then((response) => {
                    this.info = response.data
                    if(this.info.meta.available_actions.find((action) => action === 'edit') !== 'edit'){
                        this.$router.push('/event/' + this.info.id)
                    } else {
                        this.name_th = this.info.name_th
                        this.name_en = this.info.name_en
                        this.description = this.info.description
                        this.external_links = this.info.external_links
                        this.url_id = this.info.url_id
                        this.current_logo = this.info.logo
                        this.current_banner = this.info.banner
                        this.is_accepting_requests = this.info.is_accepting_requests
                        this.location = this.info.location
                        this.start_date = this.info.start_date
                        this.end_date = this.info.end_date
                        this.start_time = this.info.start_time
                        this.end_time = this.info.end_time
                        this.is_cancelled = this.info.is_cancelled
                        this.is_publicly_visible = this.info.is_publicly_visible
                        this.allows_outside_participators = this.info.allows_outside_participators
                        this.event_type = this.info.event_type
                        this.event_series = this.info.event_series
                    }
                }).catch(() => {
                    this.modalCloseType = 'close';
                    this.modalLoading = false;
                    this.modalSuccess = false;
                    if (this.language === 'th') {
                        this.modalText = 'เกิดข้อผิดพลาดระหว่างดึงข้อมูลกิจกรรมปัจจุบัน'
                    } else {
                        this.modalText = 'Failed to get current event data.';
                    }
                    this.modalUrl = '';
                    window.$('#alert').modal('show');
                });
            } else if (this.token === null || this.token === undefined) {
                this.$router.back()
            }
            try {
                axios.get($http + 'api/category/event-type', {
                    headers: {
                        Authorization: this.token
                    }
                }).then((response) => {
                    this.eventtype = response.data
                });
                axios.get($http + 'api/category/event-series', {
                    headers: {
                        Authorization: this.token
                    }
                }).then((response) => {
                    this.eventseries = response.data
                });
            } catch (err) {
                (console.log(err))
            }
        }
    },
    async mounted(){
        this.getData();
    },
    computed: {
        checkDelete() {
            if(this.info.meta === undefined){
                return null
            }
            if(this.info.meta.available_actions === undefined) {
                return false
            } else {
                if(this.info.meta.available_actions.find((action) => action === 'delete') === 'delete') {
                    return true;
                } else {
                    return false;
                }
            }
        },
        checkEdit() {
            if(this.info.meta === undefined){
                return null
            }
            if(this.info.meta.available_actions === undefined) {
                return false
            } else {
                if(this.info.meta.available_actions.find((action) => action === 'edit') === 'edit') {
                    return true;
                } else {
                    return false;
                }
            }
        },
    }
}
</script>
<style scoped>
img{
    max-width: 100%;
}
p.error{
    color: red;
}
</style>
