                                    <button @click="voting(member.id, member.user)" data-target="#voting" data-toggle="modal" v-if="member.meta.is_able_to_vote && info.meta.is_ended" class="btn btn-success">
                                        <p class="language" v-if="language === 'th'">โหวต</p>
                                        <p class="language" v-else>Vote</p>
                                    </button>
                                    <button v-else-if="!member.meta.is_able_to_vote && info.meta.is_ended" class="btn btn-outline-secondary" disabled>
                                        <p class="language" v-if="language === 'th'">โหวต</p>
                                        <p class="language" v-else>Vote</p>
                                    </button>
                                    </div>
                                    <div class="dropdown">
                                        <button
                                            v-if="member.meta.is_able_to_assign.find((mem) => mem.is_assignable === true)"
                                            class="btn btn-primary"
                                            type="button"
                                            id="dropdownMenuButton"
                                            data-toggle="dropdown"
                                            aria-expanded="false"
                                        >
                                            <p class="language" v-if="language === 'th'">เพิ่ม/ลดตำแหน่ง</p><p class="language" v-else>Promote/Demote</p>
                                        </button>
                                        <ul class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                                            <li v-for="position in member.meta.is_able_to_assign" :key="position">
                                                <button v-if="position.is_assignable" @click="selectThePosition(member.id, position.position)" class="dropdown-item">{{ generatePosition(position.position) }}</button>
                                                <button v-else class="dropdown-item disabled-position">{{ generatePosition(position.position) }}</button>
                                            </li>
                                        </ul>
                                    </div>
                                    <div>
                                    <button
                                    v-if="member.meta.is_able_to_remove"
                                    @click="selectRemoveMember(member.id)"
                                    class="btn btn-danger">
                                        <p class="language" v-if="language === 'th'">เชิญออก</p>
                                        <p class="language" v-else>Remove</p>
                                    </button>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div v-if="members[0] !== undefined">
                            <div class="not-bg mt-2 row" v-for="member in members" :key="member.user">
                                <div class="col-lg d-flex mt-4">
                                    <router-link :to="getProfileLink(member.user)">
                                        <div class="d-flex">
                                            <div class="image">
                                                <img v-if="getProfilePhoto(member.user)" class="mr-2" :src="getProfilePhoto(member.user)" style="width: 100%;">
                                                <img v-else src="../../../assets/user_placeholder.jpg" class="mr-2" style="width: 100%;">
                                            </div>
                                            <div>
                                                <p class="profile-name">{{ getProfileName(member.user) }}</p>
                                                <p class="position">{{ generatePosition(member.position) }}</p>
                                            </div>
                                        </div>  
                                    </router-link>
                                </div>
                                <!-- {{ member }} -->
                                <div v-if="member.meta.is_able_to_assign.find((mem) => mem.is_assignable === true) || member.meta.is_able_to_remove || (info.meta.is_ended && info.is_approved)" class="col-lg mb-4 d-flex justify-content-center">
                                    <div>
                                    <button @click="voting(member.id, member.user)" data-target="#voting" data-toggle="modal" v-if="member.meta.is_able_to_vote && info.meta.is_ended" class="btn btn-success">
                                        <p class="language" v-if="language === 'th'">โหวต</p>
                                        <p class="language" v-else>Vote</p>
                                    </button>
                                    <button v-else-if="!member.meta.is_able_to_vote && info.meta.is_ended" class="btn btn-outline-secondary" disabled>
                                        <p class="language" v-if="language === 'th'">โหวต</p>
                                        <p class="language" v-else>Vote</p>
                                    </button>
                                    </div>
                                    <div class="dropdown">
                                        <button
                                            v-if="member.meta.is_able_to_assign.find((mem) => mem.is_assignable === true)"
                                            class="btn btn-primary"
                                            type="button"
                                            id="dropdownMenuButton"
                                            data-toggle="dropdown"
                                            aria-expanded="false"
                                        >
                                            <p class="language" v-if="language === 'th'">เพิ่ม/ลดตำแหน่ง</p><p class="language" v-else>Promote/Demote</p>
                                        </button>
                                        <ul class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                                            <li v-for="position in member.meta.is_able_to_assign" :key="position">
                                                <button v-if="position.is_assignable" @click="selectThePosition(member.id, position.position)" class="dropdown-item">{{ generatePosition(position.position) }}</button>
                                                <button v-else class="dropdown-item disabled-position">{{ generatePosition(position.position) }}</button>
                                            </li>
                                        </ul>
                                    </div>
                                    <div>
                                    <button
                                    v-if="member.meta.is_able_to_remove"
                                    @click="selectRemoveMember(member.id)"
                                    class="btn btn-danger">
                                        <p class="language" v-if="language === 'th'">เชิญออก</p>
                                        <p class="language" v-else>Remove</p>
                                    </button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <!-- PC -->
                <div class="d-none d-lg-block">
                    <div v-if="memberSortR.length !== 0" class="mt-4">
                        <div class="row not-bg">
                            <div class="mt-2 ml-2">
                                <h4>Retired Members</h4>
                            </div>
                        </div>
                        <div class="not-bg row mt-2" v-for="member in memberSortR" :key="member.user">
                            <div class="col-md d-flex mt-2 mb-2">
                                <router-link :to="getProfileLink(member.user)">
                                    <div class="d-flex">
                                        <div class="image">
                                            <img v-if="getProfilePhoto(member.user)" class="mr-2" :src="getProfilePhoto(member.user)" style="width: 100%;">
                                            <img v-else src="../../../assets/user_placeholder.jpg" class="mr-2" style="width: 100%;">
                                        </div>
                                    <p class="profile-name">{{ getProfileName(member.user) }}</p>
                                    </div>  
                                </router-link>
                            </div>
                            <div class="col-md d-flex justify-content-end">
                                <div>
                                <button style="margin-top: 16px;"
                                v-if="member.meta.is_able_to_remove"
                                @click="selectRemoveMember(member.id)"
                                class="btn btn-danger">
                                    <p class="language" v-if="language === 'th'">เชิญออก</p>
                                    <p class="language" v-else>Remove</p>
                                </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <!-- Mobile -->
                <div class="d-lg-none">
                    <div v-if="memberSortR.length !== 0" class="mt-4">
                        <div class="row not-bg mb-2">
                            <div class="mb-2">
                                <div class="mt-2">
                                    <h4>Retired Members</h4>
                                </div>
                            </div>
                        </div>
                        <div class="not-bg row" v-for="member in memberSortR" :key="member.user">
                            <div class="col-lg d-flex mt-4">
                                <router-link :to="getProfileLink(member.user)">
                                    <div class="d-flex">
                                        <div class="image">
                                            <img v-if="getProfilePhoto(member.user)" class="mr-2" :src="getProfilePhoto(member.user)" style="width: 100%;">
                                            <img v-else src="../../../assets/user_placeholder.jpg" class="mr-2" style="width: 100%;">
                                        </div>
                                    <p class="profile-name">{{ getProfileName(member.user) }}</p>
                                    </div>  
                                </router-link>
                            </div>
                            <div class="col-lg mb-4 d-flex justify-content-center">
                                <div>
                                <button
                                v-if="member.meta.is_able_to_remove"
                                @click="selectRemoveMember(member.id)"
                                class="btn btn-danger">
                                    <p class="language" v-if="language === 'th'">เชิญออก</p>
                                    <p class="language" v-else>Remove</p>
                                </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="col-md"></div>
            <!-- Modal -->
            <div
            class="modal fade"
            id="voting"
            ref="voting"
            tabindex="-1"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
            >
                <div class="modal-dialog">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title" id="exampleModalLabel">
                                <p class="language" v-if="language === 'th'">โหวต</p>
                                <p class="language" v-else>Vote</p>
                            </h5>
                            <button
                            type="button"
                            class="btn-close"
                            data-dismiss="modal"
                            aria-label="Close"
                            ></button>
                        </div>
                        <div class="modal-body">
                            <div class="row">
                                <div class="form-outline mb-4">
                                    <label class="form-label" for="name">
                                        <p class="language" v-if="language === 'th'">ชื่อ</p>
                                        <p class="language" v-else>Name</p>
                                    </label>
                                    <input type="text" class="form-control" id="name" disabled :value="votedName"/>
                                    <p class="error" v-if="errorMessage.voted_for">{{ errorMessage.voted_for[0] }}</p>
                                </div>
                                <div class="form-outline mb-4">
                                    <label class="form-label" for="comment">
                                        <p class="language" v-if="language === 'th'">คำชม</p>
                                        <p class="language" v-else>Comment</p>
                                    </label>
                                    <textarea class="form-control" id="comment" rows="4" v-model="comment"></textarea>
                                    <p class="error" v-if="errorMessage.comment">{{ errorMessage.comment[0] }}</p>
                                </div>
                            </div>
                        </div>
                        <div class="modal-footer">
                            <button @click="sendVote()" type="button" class="btn btn-success">
                                <p class="language" v-if="language === 'th'">โหวต</p>
                                <p class="language" v-else>Vote</p>
                            </button>
                            <button type="button" class="btn btn-light" data-dismiss="modal">
                                <p class="language" v-if="language === 'th'">ปิด</p>
                                <p class="language" v-else>Close</p>
                            </button>
                        </div>
                    </div>
                </div>
            </div>

            <the-alert :id="'alert'" :closetype="modalCloseType" :success="modalSuccess" :loading="modalLoading" :text="modalText">
                <template v-slot:header></template>
            </the-alert>

            <the-confirm :id="'demotepromote'">
                <h4 v-if="selectPosition !== 3" class="d-flex justify-content-center">
                    <p class="language" v-if="language === 'th'">เปลี่ยนตำแหน่งสมาชิกคนนี้เป็น</p>
                    <p class="language" v-else>Change this member to</p>&nbsp;{{ generatePosition(selectPosition) }}?
                </h4>
                <div v-else>
                    <h4 class="d-flex justify-content-center">
                        <p class="language" v-if="language === 'th'">เปลี่ยนตำแหน่งสมาชิกคนนี้เป็น</p>
                        <p class="language" v-else>Change this member to</p>&nbsp;{{ generatePosition(selectPosition) }}?
                    </h4>
                    <h5 class="d-flex justify-content-center">
                        <p class="language" v-if="language === 'th'">คุณจะถูกลดตำแหน่งเป็น</p>
                        <p class="language" v-else>You will be demote to</p>&nbsp;{{ generatePosition(2) }}
                    </h5>
                </div>
                <template v-slot:footer>
                    <button data-dismiss="modal" class="btn btn-primary mr-4" style="width: 75px;" @click="promoteDemote(selectUser, selectPosition)">
                        <p class="language" v-if="language === 'th'">ใช่</p>
                        <p class="language" v-else>Yes</p>
                    </button>
                    <button data-dismiss="modal" class="btn btn-outline-secondary ml-4" style="width: 75px;">
                        <p class="language" v-if="language === 'th'">ไม่</p>
                        <p class="language" v-else>No</p>
                    </button>
                </template>
            </the-confirm>

            <the-confirm :id="'remove'">
                <h4 class="d-flex justify-content-center">
                    <p class="language" v-if="language === 'th'">เชิญสมาชิกคนนี้ออก?</p>
                    <p class="language" v-else>Remove this member?</p>
                </h4>
                <template v-slot:footer>
                    <button data-dismiss="modal" class="btn btn-danger mr-4" style="width: 75px;" @click="removeMember(selectRemove)">
                        <p class="language" v-if="language === 'th'">ใช่</p>
                        <p class="language" v-else>Yes</p>
                    </button>
                    <button data-dismiss="modal" class="btn btn-outline-secondary ml-4" style="width: 75px;">
                        <p class="language" v-if="language === 'th'">ไม่</p>
                        <p class="language" v-else>No</p>
                    </button>
                </template>
            </the-confirm>
        </div>
</template>
<script>
import axios from 'axios';
import { $http } from '../../../assets/variables.js';
import TheAlert from '../../modal/TheAlert.vue';
import TheConfirm from '../../modal/TheConfirm.vue';

export default {
    components: {
        TheAlert,
        TheConfirm,
    },
    props: ['token', 'info', 'eventStatus', 'language'],
    data() {
        return {
            memberList: [],
            memberSort: '',
            president: '',
            vicePresident: '',
            staff: '',
            members: '',
            memberSortR: '',
            searchMem: '',
            memberSearching: false,
            errorMessage: '',
            comment: '',
            voted_for: '',
            votedName: '',
            modalCloseType: '',
            modalSuccess: false,
            modalLoading: false,
            modalText: '',
            selectPosition: '',
            selectUser: '',
            selectRemove: '',
            memberLoading: false,
            maxVote: '',
        }
    },
    methods: {
        getMaximumVote(){
            axios.get($http + 'api/misc/vote/info/', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.maxVote = response.data;
            })
        },
        selectRemoveMember(user){
            this.selectRemove = user;
            window.$('#remove').modal('show');
        },
        selectThePosition(user, position){
            this.selectPosition = position;
            this.selectUser = user;
            window.$('#demotepromote').modal('show');
        },
        sendVote(){
            this.modalCloseType = '';
            this.modalSuccess = false;
            this.modalLoading = true;
            this.modalText = 'Loading...';
            window.$('#alert').modal('show');
            axios.post($http + 'api/misc/vote/', {
                comment: this.comment,
                voted_for: this.voted_for
            }, {
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                this.modalCloseType = 'refresh';
                this.modalSuccess = true;
                this.modalLoading = false;
                if (this.language === 'th') {
                    this.modalText = 'โหวตของคุณถูกส่งแล้ว'
                } else {
                    this.modalText = 'Your vote have been submitted.';
                }
            }).catch((err) => {
                this.modalCloseType = '';
                this.modalSuccess = false;
                this.modalLoading = false;
                if (this.language === 'th') {
                    this.modalText = 'เกิดข้อผิดพลาดระหว่างส่งโหวต'
                } else {
                    this.modalText = 'Failed to submit your vote';
                }
                console.log(err.response.data)
                this.errorMessage = err.response.data;
            })
        },
        voting(id, user){
            this.voted_for = '';
            this.comment = '';
            this.voted_for = id;
            this.votedName = this.getProfileName(user);
        },
        searchMember(id){
            this.memberSort = '';
            axios.get($http + 'api/membership/membership/?search=' + this.searchMem + '&community=' + id,{
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.memberSort = response.data.filter((mem) => mem.status === 'A')
                this.president = response.data.filter((mem) => mem.position === 3);
                this.vicePresident = response.data.filter((mem) => mem.position === 2);
                this.staff = response.data.filter((mem) => mem.position === 1);
                this.members = response.data.filter((mem) => mem.position === 0);
                var members
                for (members in response.data) {
                    var member = response.data[members]
                    axios.get($http + 'api/user/user/'+ member.user, {
                        headers: {
                            Authorization: this.token
                        }
                    }).then((response) => {
                        this.memberList.push(response)
                    })
                    if(parseInt(members) === response.data.length-1){
                        this.memberSearching = false;
                    }
                }
            });
        },
        getProfilePhoto(id){
            var member = this.memberList.find((mem) => mem.data.id === id);
            if (member !== undefined) {
                return member.data.profile_picture;
            }
        },
        removeMember(id) {
            axios.patch($http + 'api/membership/membership/' + id + '/' ,{
                status: 'X'
            }, {
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                this.getMemberData(this.info.id);
            }).catch(() => {
                this.modalCloseType = '';
                this.modalSuccess = false;
                this.modalLoading = false;
                if (this.language === 'th') {
                    this.modalText = "เกิดข้อผิดพลาดระหว่างเชิญสมาชิกออก"
                } else {
                    this.modalText = "Failed to remove member."
                }
                window.$('#alert').modal('show');
            });
        },
        promoteDemote(id, position) {
            axios.patch($http + 'api/membership/membership/' + id + '/', {
                position: position
            }, 
            {
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                this.getMemberData(this.info.id);
            }).catch(() => {
                this.modalCloseType = '';
                this.modalSuccess = false;
                this.modalLoading = false;
                if (this.language === 'th') {
                    this.modalText = 'เกิดข้อผิดพลาดระหว่างเพิ่มลดตำแหน่งสมาชิก'
                } else {
                    this.modalText = 'Fail to promote/demote member.';
                }
                window.$('#alert').modal('show');
            });
        },
        generatePosition(num) {
            if(this.language === 'th') {
                if(this.info.meta.community_type == 'club') {
                    if (num === 0) {
                        return 'สมาชิก';
                    } else if (num === 1) {
                        return 'ทีมงาน';
                    } else if (num === 2) {
                        return 'รองประธาน'
                    } else if (num === 3) {
                        return 'ประธาน'
                    } else {
                        return 'error'
                    }
                } else if(this.info.meta.community_type == 'event') {
                    if (num === 0) {
                        return 'ผู้เข้าร่วม';
                    } else if (num === 1) {
                        return 'ทีมงาน';
                    } else if (num === 2) {
                        return 'รองประธาน'
                    } else if (num === 3) {
                        return 'ประธาน'
                    } else {
                        return 'error'
                    }
                } else if(this.info.meta.community_type == 'community_event') {
                    if (num === 0) {
                        return 'ผู้เข้าร่วม';
                    } else if (num === 1) {
                        return 'ทีมงาน';
                    } else if (num === 2) {
                        return 'ผู้ร่วมสร้างกิจกรรม'
                    } else if (num === 3) {
                        return 'ผู้สร้างกิจกรรม'
                    } else {
                        return 'error'
                    }
                } else if(this.info.meta.community_type == 'lab') {
                    if (num === 0) {
                        return 'สมาชิกห้องปฏิบัติการ';
                    } else if (num === 1) {
                        return 'ผู้ช่วยดูแลห้องปฏิบัติการ';
                    } else if (num === 2) {
                        return 'อาจารย์ผู้ร่วมดูแลห้องปฏิบัติการ'
                    } else if (num === 3) {
                        return 'อาจารย์ผู้ดูแลห้องปฏิบัติการ'
                    } else {
                        return 'error'
                    }
                }
            } else {
                if(this.info.meta.community_type == 'club') {
                    if (num === 0) {
                        return 'Member';
                    } else if (num === 1) {
                        return 'Staff';
                    } else if (num === 2) {
                        return 'Vice President'
                    } else if (num === 3) {
                        return 'President'
                    } else {
                        return 'error'
                    }
                } else if(this.info.meta.community_type == 'event') {
                    if (num === 0) {
                        return 'Participator';
                    } else if (num === 1) {
                        return 'Staff';
                    } else if (num === 2) {
                        return 'Vice President'
                    } else if (num === 3) {
                        return 'President'
                    } else {
                        return 'error'
                    }
                } else if(this.info.meta.community_type == 'community_event') {
                    if (num === 0) {
                        return 'Participator';
                    } else if (num === 1) {
                        return 'Staff';
                    } else if (num === 2) {
                        return 'Event Co-Creator'
                    } else if (num === 3) {
                        return 'Event Creator'
                    } else {
                        return 'error'
                    }
                } else if(this.info.meta.community_type == 'lab') {
                    if (num === 0) {
                        return 'Lab Member';
                    } else if (num === 1) {
                        return 'Lab Helper';
                    } else if (num === 2) {
                        return 'Lab Co-Supervisor'
                    } else if (num === 3) {
                        return 'Lab Supervisor'
                    } else {
                        return 'error'
                    }
                }
            }
        },
        getMemberData(id){
            this.getMaximumVote();
            this.memberLoading = true;
            this.memberSort = '';
            this.memberList = [];
            axios.get($http + 'api/membership/membership?community='+ id +'&status=A', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.memberSort = response.data
                this.president = response.data.filter((mem) => mem.position === 3);
                this.vicePresident = response.data.filter((mem) => mem.position === 2);
                this.staff = response.data.filter((mem) => mem.position === 1);
                this.members = response.data.filter((mem) => mem.position === 0);
                var members
                for (members in response.data) {
                    var member = response.data[members]
                    axios.get($http + 'api/user/user/'+ member.user, {
                        headers: {
                            Authorization: this.token
                        }
                    }).then((response) => {
                        this.memberList.push(response)
                    })
                }
                this.memberLoading = false;
            })
            axios.get($http + 'api/membership/membership?community='+ this.info.id +'&status=R', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.memberSortR = response.data
                var members
                for (members in response.data) {
                    var member = response.data[members]
                    axios.get($http + 'api/user/user/'+ member.user, {
                        headers: {
                            Authorization: this.token
                        }
                    }).then((response) => {
                        this.memberList.push(response)
                    })
                }
            })
        },
        getProfileLink(id) {
            return '/profile/' + id;
        },
        getProfileName(id) {
            var member = this.memberList.find((mem) => mem.data.id === id);
            if (member !== undefined) {
                return member.data.name;
            }
        },
    },
    async mounted() {
        this.getMemberData(this.info.id);
    },
}
</script>
<style scoped src="../../../assets/main.css"></style>
