<template>
    <div class="row">
        <div class="col"></div>
        <div class="col">
            <div class="d-flex justify-content-center">
                <div class="spinner-border m-5" role="status">
                    <span class="sr-only">Loading...</span>
                </div>
            </div>
            <div class="d-flex justify-content-center">
                <h3>Loading...</h3>
            </div>
        </div>
        <div class="col"></div>
        <!-- Modal -->
        <the-alert :id="'alert'" :closetype="modalCloseType" :success="modalSuccess" :loading="modalLoading" :text="modalText">
            <template v-slot:header></template>
        </the-alert>
    </div>
</template>
<script>
import axios from 'axios';
import { $http } from '../../assets/variables.js';
import TheAlert from '../modal/TheAlert.vue';

export default {
    data() {
        return {
            token: '',
            modalCloseType: '',
            modalSuccess: false,
            modalLoading: false,
            modalText: '',
            language: '',
        }
    },
    components: {
        TheAlert,
    },
    props:['id'],
    async mounted() {
        this.token = window.$cookies.get('token');
        if(this.token === null){
            this.token = window.$cookies.get("itkmitlcommunitytokenbrowser");
        }
        this.language = window.$cookies.get('itkmitlcommunitylanguage');
        axios.get($http + 'api/community/community/' + this.id, {
            headers: {
                Authorization: this.token
            }
        }).then((response) => {
            if(response.data.meta.community_type === 'club'){
                this.$router.push('/community/club/' + this.id);
            } else if(response.data.meta.community_type === 'event'){
                this.$router.push('/community/event/' + this.id);
            } else if(response.data.meta.community_type === 'community_event'){
                this.$router.push('/community/communityevent/' + this.id);
            } else if(response.data.meta.community_type === 'lab'){
                this.$router.push('/community/lab/' + response.data.url_id);
            }
        }).catch(() => {
            this.modalLoading = false;
            this.modalSuccess = false;
            this.modalCloseType = 'close';
            if(this.language === 'th') {
                this.modalText = "ไม่สามารถดึงข้อมูลชุมชนได้"
            } else {
                this.modalText = "Can't get community data."
            }
            window.$('#alert').modal('show');
        })
    }
}
</script>
<style scoped src="../../assets/main.css"></style>