<template>
    <div id="announcement-edit" class="mt-4 ml-2 info-box" style="min-height: 50px; width: 97.5%; padding-top: 0; padding-bottom: 0;">
        <div v-if="!editAnnouncementCheck" class="mb-2">
            <div class="row not-bg" style="min-height: 50px;">
                <div class="d-flex">
                    <div class="d-flex justify-content-start">
                        <p class="mt-3" style="padding-left: 12px;">{{ text }}</p>
                    </div>
                    <div class="flex-fill"></div>
                    <div v-if="editable" class="">
                        <div class="dropdown d-flex justify-content-end">
                            <button
                                class="btn btn-white"
                                type="button"
                                id="dropdownMenuButton"
                                data-toggle="dropdown"
                                aria-expanded="false"
                            >
                                ...
                            </button>
                            <ul class="dropdown-menu dropdown-menu-right" aria-labelledby="dropdownMenuButton">
                                <li>
                                    <button @click="editAnnouncement()" class="dropdown-item">
                                        <p class="language" v-if="language === 'th'">แก้ไข</p>
                                        <p class="language" v-else>Edit</p>
                                    </button>
                                </li>
                                <li>
                                    <button @click="checkDeleteAnnouncement(announceid, community)" class="dropdown-item">
                                        <p class="language" v-if="language === 'th'">ลบ</p>
                                        <p class="language" v-else>Delete</p>
                                    </button>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
            <div v-if="image" class="row not-bg mt-4" :data-target="'#picture' + announceid" data-toggle="modal">
                <div class="d-flex justify-content-center announcement-image">
                    <img :src="image" style="height: auto; width: 100%; max-width: 100%;">
                </div>
            </div>
            <!-- Modal -->
                <div
                class="modal fade"
                :id="'picture' + announceid"
                tabindex="-1"
                aria-labelledby="exampleModalLabel"
                aria-hidden="true"
                >
                <div class="x" data-dismiss="modal">
                    <img src="../../assets/X.svg" class="ximg">
                </div>
                    <div class="modal-dialog modal-dialog-centered modal-xl">
                        <div class="modal-content" style="background-color: transparent; border-style: none;">
                            <div class="modal-body" style="padding: 0;">
                                <div class="d-flex justify-content-center">
                                    <div class="display d-flex justify-content-center">
                                        <img :src="image">
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            <!-- Modal -->
        </div>
        <div v-if="editAnnouncementCheck" id="form" class="mt-4 row not-bg form-padding">
            <div class="form-outline mb-4 mt-2">
                <!-- <label class="form-label" for="textting">Text</label> -->
                <textarea class="form-control" id="textting" rows="4" v-model="texting"></textarea>
                <p class="error" v-if="errorMessage.text">{{ errorMessage.text[0] }}</p>
            </div>
            
            <img v-if="current_image" :src="current_image" style="max-height: 2000px; max-width: 97%; object-fit: cover;" class="mb-4">
            <div class="input-group">
                <div class="custom-file">
                    <input type="file" class="custom-file-input" id="inputGroupFile01" ref="annoncePic" @change="annoncePic"
                    aria-describedby="inputGroupFileAddon01" accept="image/*">
                </div>
            </div>

            <div class="form-check d-flex justify-content-start mb-4 mt-4" style="margin-left: 12px;">
                <input
                class="form-check-input mr-2"
                type="checkbox"
                value=""
                id="allowOutside"
                v-model="is_publicly_visibly"
                />
                <label class="form-check-label" for="allowOutside">
                    <p class="language" v-if="language === 'th'">ประกาศในรูปแบบสาธารณะ</p>
                    <p class="language" v-else>Post as publicly-visible announcement</p>
                </label>
            </div>

            <div class="d-flex justify-content-end">
                <button @click="sendData" type="submit" class="btn btn-warning">
                    <p class="language" v-if="language === 'th'">บันทึกการเปลี่ยนแปลง</p>
                    <p class="language" v-else>Save Change</p>
                </button>
                <button @click="editAnnouncement()" class="btn btn-light">
                    <p class="language" v-if="language === 'th'">ยกเลิก</p>
                    <p class="language" v-else>Cancel</p>
                </button>
            </div>
        </div>
        <!-- Modal -->
        <the-alert :id="'edit'" :closetype="modalCloseType" :success="modalSuccess" :loading="modalLoading" :text="modalText" :url="modalUrl">
            <template v-slot:header></template>
        </the-alert>
        <the-confirm :id="'deleteAnnouncement'">
            <h4 class="d-flex justify-content-center">
                <p class="language" v-if="language === 'th'">ต้องการลบประกาศนี้?</p>
                <p class="language" v-else>Delete this announcement?</p>
            </h4>
            <template v-slot:footer>
                <button data-dismiss="modal" class="btn btn-danger mr-4" style="width: 75px;" @click="deleteAnnouncement(selectannounce, selectcom)">
                    <p class="language" v-if="language === 'th'">ใช่</p>
                    <p class="language" v-else>Yes</p>
                </button>
                <button data-dismiss="modal" class="btn btn-outline-secondary ml-4" style="width: 75px;">
                    <p class="language" v-if="language === 'th'">ไม่</p>
                    <p class="language" v-else>No</p>
                </button>
            </template>
        </the-confirm>
    </div>
</template>
<script>
import axios from 'axios';
import { $http } from '../../assets/variables.js';
import TheAlert from '../modal/TheAlert.vue';
import TheConfirm from '../modal/TheConfirm.vue';

export default {
    components: {
        TheAlert,
        TheConfirm
    },
    inject: ['refreshAnnouncement'],
    props: ['community', 'token', 'text', 'image', 'editable', 'announceid', 'public', 'language'],
    data() {
        return {
            texting: this.text,
            imaging: null,
            current_image: this.image,
            is_publicly_visibly: this.public,
            editAnnouncementCheck: false,
            errorMessage: '',
            modalCloseType: '',
            modalSuccess: false,
            modalLoading: false,
            modalText: '',
            selectannounce: '',
            selectcom: '',
        }
    },
    computed: {
        editAnnouncementButtonText() {
            if(this.editAnnouncementCheck){
                return 'Close'
            } else {
                return 'Edit'
            }
        }
    },
    methods: {
        checkDeleteAnnouncement(announce, com){
            this.selectannounce = announce;
            this.selectcom = com;
            window.$('#deleteAnnouncement').modal('show');
        },
        annoncePic(event){
            this.imaging = event.target.files[0];
            var reader = new FileReader();
            reader.onload = (e) => {
                this.current_image = e.target.result;
            }
            reader.readAsDataURL(this.imaging)
        },
        editAnnouncement(){
            this.editAnnouncementCheck = !this.editAnnouncementCheck;
        },
        deleteAnnouncement(id, community) {
            axios.delete($http + 'api/asset/announcement/' + id + '/', {
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                this.refreshAnnouncement(community)
            }).catch((err) => {
                console.log(err);
                this.modalCloseType = '';
                this.modalSuccess = false;
                this.modalLoading = false;
                if (this.language === 'th') {
                    this.modalText = 'เกิดข้อผิดพลาดระหว่างลบประกาศ';
                } else {
                    this.modalText = 'Failed to delete announcement.';
                }
                window.$('edit').modal('show');
            });
        },
        sendData() {
            this.modalCloseType = '';
            this.modalSuccess = false;
            this.modalLoading = true;
            this.modalText = 'Loading...';
            window.$('#edit').modal('show');
            this.errorMessage = '';
            var data = {
                text: this.texting,
                is_publicly_visible: this.is_publicly_visibly
            }
            axios.patch($http + 'api/asset/announcement/' + this.announceid + '/', data, {
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                if(this.imaging !== null) {
                    const picture = new FormData();
                    picture.append('image', this.imaging)
                    axios.patch($http + 'api/asset/announcement/' + this.announceid + '/', picture, {
                        headers: {
                            Authorization: this.token
                        }
                    }).then((response) => {
                        if(response.status === 200){
                            this.modalCloseType = '';
                            this.modalSuccess = true;
                            this.modalLoading = false;
                            if (this.language === 'th') {
                                this.modalText = 'ประกาศถูกแก้ไขเรียบร้อย';
                            } else {
                                this.modalText = 'Announcement edited.';
                            }
                            this.editAnnouncement();
                            setTimeout(() => {
                                window.$('#edit').modal('hide');
                                this.refreshAnnouncement(this.community);
                            }, 1000);
                        }
                    })
                } else {
                    this.modalCloseType = '';
                    this.modalSuccess = true;
                    this.modalLoading = false;
                    if (this.language === 'th') {
                        this.modalText = 'ประกาศถูกแก้ไขเรียบร้อย';
                    } else {
                        this.modalText = 'Announcement edited.';
                    }
                    this.editAnnouncement();
                    setTimeout(() => {
                        window.$('#edit').modal('hide');
                        this.refreshAnnouncement(this.community);
                    }, 1000);
                }
            }).catch((err) => {
                this.modalCloseType = '';
                this.modalSuccess = false;
                this.modalLoading = false;
                if (this.language === 'th') {
                    this.modalText = 'เกิดข้อผิดพลาดระหว่างแก้ไขประกาศ';
                } else {
                    this.modalText = 'Failed to edit announcement.';
                }
                console.log(err);
                this.errorMessage = err.response.data;
            });
        },
    },
}
</script>
<style scoped src="../../assets/main.css"></style>
<style scoped src="../../assets/form.css"></style>
<style scoped>
img{
    object-fit: cover;
    max-width: 100%;
    max-height: 100%;
}
.display{
    max-width: 100%;
    max-height: 80vh;
    border-radius: 5px;
}
.form-padding{
    padding: 10px;
}
.announcement-image{
    cursor: pointer;
    padding: 0px;
    border-radius: 5px;
    overflow: hidden;
}
.x{
    height: 30px;
    width: 30px;
    position: fixed;
    left: 25px;
    top: 90px;
    cursor: pointer;
}
</style>
