<template>
    <div class="row mb-4">
        <div class="col-lg d-none d-lg-block">
            <img src="../../assets/back.svg" @click="this.$router.back()" class="mt-4 ml-3 d-none d-lg-block back-button">
        </div>
        <div class="col-lg-6 not-bg mt-4">
            <div v-if="error !== ''">
                {{ error }}
            </div>
            <div v-else-if="info === ''">
                <div class="d-flex justify-content-center">
                    <div class="spinner-border m-5" role="status">
                        <span class="sr-only">Loading...</span>
                    </div>
                </div>
                <div class="d-flex justify-content-center">
                    <h3>Loading...</h3>
                </div>
            </div>
            <div class="row d-flex justify-content-center" v-else>
                <!-- PC -->
                <div class="d-none d-lg-block">
                    <div class="row m-4">
                        <div class="col-md">
                            <h2>{{ info.name }}</h2>
                        </div>
                        <div class="col-md d-flex justify-content-end">
                            <button v-if="checkEdit()" @click="editAlbum()" class="btn btn-primary">
                                <p class="language" v-if="language === 'th'">แก้ไขอัลบั้ม</p>
                                <p class="language" v-else>Edit Album</p>
                            </button>
                        </div>
                    </div>
                </div>
                <!-- Mobile -->
                <div class="d-lg-none">
                    <div class="d-flex justify-content-center mt-2">
                        <h2>{{ info.name }}</h2>
                    </div>
                    <div class="d-flex justify-content-center mb-2">
                        <button v-if="checkEdit()" @click="editAlbum()" class="btn btn-primary">
                            <p class="language" v-if="language === 'th'">แก้ไขอัลบั้ม</p
                            ><p class="language" v-else>Edit Album</p>
                        </button>
                    </div>
                </div>
                <div v-if="pictures.length === 0">
                    <div class="d-flex justify-content-center">
                        <div class="spinner-border m-5" role="status">
                            <span class="sr-only">Loading...</span>
                        </div>
                    </div>
                    <div class="d-flex justify-content-center">
                        <h3>Loading...</h3>
                    </div>
                </div>
                <div class="d-flex justify-content-center m-4" v-else-if="pictureError">{{ pictures }}</div>
                <div class="row d-flex justify-content-center" v-else>
                    <the-picture v-for="picture in pictures" :key="picture.id"
                    :image="picture.image"
                    :id="picture.id">
                    </the-picture>
                </div>
                <!-- Modal -->
                <div
                class="modal fade"
                id="picture"
                tabindex="-1"
                aria-labelledby="exampleModalLabel"
                aria-hidden="true"
                >
                <img src="../../assets/arrow_left.svg" @click="changePicture(-1)" class="left d-none d-lg-block">
                <img src="../../assets/arrow_right.svg" @click="changePicture(1)" class="right d-none d-lg-block">
                <img src="../../assets/arrow_left.svg" @click="changePicture(-1)" class="left d-lg-none">
                <img src="../../assets/arrow_right.svg" @click="changePicture(1)" class="right d-lg-none">
                    <div class="modal-dialog modal-dialog-centered modal-xl">
                        <div class="modal-content" style="background-color: transparent; border-style: none;">
                            <div class="modal-body" style="padding: 0;">
                                <div class="d-flex justify-content-center" data-dismiss="modal">
                                    <img :src="pictures[selectedPicture].image" class="image">
                                </div>
                                <div class="d-flex justify-content-center">
                                    <div class="d-flex justify-content-end">
                                        {{ selectedPicture+1 }}&nbsp;
                                        <p class="language" v-if="language === 'th'">จาก</p>
                                        <p class="language" v-else>of</p>&nbsp;
                                        {{ pictures.length }}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="col-lg"></div>
        <!-- Modal -->
        <the-alert :id="'alert'" :closetype="modalCloseType" :success="modalSuccess" :loading="modalLoading" :text="alertText">
            <template v-slot:header></template>
        </the-alert>
    </div>
</template>
<script>
import ThePicture from './ThePicture.vue';
import axios from 'axios';
import { $http } from '../../assets/variables.js';
import TheAlert from '../modal/TheAlert.vue';

export default {
    components: {
        ThePicture,
        TheAlert,
    },
    props: ['albumid', 'comid'],
    data () {
        return {
            token: '',
            info: '',
            error: '',
            pictures: [],
            community: '',
            selectedPicture: 0,
            modalCloseType: '',
            modalSuccess: false,
            modalLoading: false,
            modalText: '',
            pictureError: false,
            language: '',
        }
    },
    methods: {
        changePicture(num){
            var save = this.selectedPicture
            save += num;
            if(save < 0){
                save = this.pictures.length - 1
            }else if(save >= this.pictures.length){
                save = 0
            }
            this.selectedPicture = save;
        },
        selectPicture(id) {
            var target = this.pictures.find((pic) => pic.id === id);
            var index = this.pictures.indexOf(target);
            this.selectedPicture = index;
        },
        getCommunityData(id) {
            axios.get($http + 'api/community/community/' + id, {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                if (response.data.meta.community_type === 'community_event') {
                    axios.get($http + 'api/community/event/community/' + id, {
                        headers: {
                            Authorization: this.token
                        }
                    }).then((response) => {
                        this.community = response.data
                    })
                } else {
                    this.community = response.data
                }
            }).catch(() => {
                this.modalCloseType = '';
                this.modalSuccess = false;
                this.modalLoading = false;
                if(this.language === 'th') {
                    this.modalText = 'เกิดข้อผิดพลาดระหว่างดึงข้อมูล'
                } else {
                    this.modalText = 'Error getting community details.';
                }
            })
        },
        getdata() {
            this.token = window.$cookies.get('token');
            if(this.token === null){
                this.token = window.$cookies.get("itkmitlcommunitytokenbrowser");
            }
            this.language = window.$cookies.get('itkmitlcommunitylanguage');
            axios.get($http + 'api/asset/album/' + this.albumid, {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.info = response.data;
                axios.get($http + 'api/asset/album/image?album=' + this.albumid, {
                    headers: {
                        Authorization: this.token
                    }
                }).then((response) => {
                    if(response.data.length !== 0){
                        var i;
                        for(i in response.data){
                            this.pictures.push(response.data[i]);
                        }
                    } else {
                        this.pictureError = true;
                        if(this.language === 'th') {
                            this.pictures = 'ยังไม่มีรูปภาพ'
                        } else {
                            this.pictures = 'No picture yet.';
                        }
                    }
                }).catch((err) => {
                    this.error = err.response.data
                })
            }).catch((err) => {
                this.error = err.response.data
            })
        },
        checkEdit(){
            if(this.info.meta !== undefined){
                return this.info.meta.is_able_to_edit;
            }
        },
        editAlbum(){
            if(this.community.meta !== undefined){
                if(this.community.meta.community_type === 'event'){
                    this.$router.push('/albumform/' + this.comid + '/' + this.albumid + '/yes/none');
                } else if(this.community.meta.community_type === 'community_event'){
                    this.$router.push('/albumform/' + this.community.created_under + '/' + this.albumid + '/no/none');
                } else {
                    this.$router.push('/albumform/' + this.comid + '/' + this.albumid + '/no/none');
                }
            }
        },
    },
    async mounted() {
        this.getdata();
        this.getCommunityData(this.comid);
    },
}
</script>
<style scoped src="../../assets/main.css"></style>
