<template>
    <div class="row background">
        <div class="col-md-3"></div>
        <div class="col-md-6 mt-3 mb-4">
            <!-- PC -->
            <div class="d-none d-lg-block">
                <div class="mt-2 not-bg row" style="padding: 28px 22px 28px 22px;">
                    <div class="row">
                        <div class="col-md">
                            <!-- <h2>Comments</h2> -->
                            <div class="d-flex">
                                <div class="form-outline align-self-start">
                                    <input v-if="language === 'th'" class="form-control" id="searchComment" rows="4" v-model="searchComment" placeholder="ค้นหา..." />
                                    <input v-else class="form-control" id="searchComment" rows="4" v-model="searchComment" placeholder="Search..." />
                                </div>
                                <div class="col-md align-self-end">
                                    <button @click="searchfromComment(info.id)" class="btn btn-primary"><i class="fas fa-search"></i></button>
                                </div>
                            </div>
                        </div>
                        <div class="col-md d-flex justify-content-end" style="margin-right: -38px;">
                            <button class="btn btn-warning" @click="checkComment(); toggleForm();" aria-expanded="false" aria-controls="comment">{{ createCommentMessage }}</button>
                        </div>
                    </div>
                </div>
            </div>
            <!-- Mobile -->
            <div class="d-lg-none not-bg row" style="padding-bottom: 5px;">
                <div class="d-flex justify-content-center mt-2">
                    <!-- <h2>Comments</h2> -->
                </div>
                <div class="d-flex justify-content-center mt-2">
                    <button class="btn btn-warning" @click="checkComment(); toggleForm();" aria-expanded="false" aria-controls="comment">{{ createCommentMessage }}</button>
                </div>
                <div class="d-flex justify-content-center mt-2 mb-2">
                    <div class="form-outline">
                        <input v-if="language === 'th'" class="form-control" id="searchComment" rows="4" v-model="searchComment" placeholder="ค้นหา..." />
                                    <input v-else class="form-control" id="searchComment" rows="4" v-model="searchComment" placeholder="Search..." />
                    </div>
                    <div class="">
                        <button @click="searchfromComment(info.id)" class="btn btn-primary"><i class="fas fa-search"></i></button>
                    </div>
                </div>
            </div>
            <comment-form
            :event="info.id"
            :token="token"
            :language="language"></comment-form>
            <div v-if="comments[0].length !== 0">
                <div class="row d-flex justify-content-center">
                    <div class="col-lg-4 comment-card mt-4" v-for="comment in comments[0].slice().reverse()" 
                    :key="comment.id">
                        <div>
                            <div v-if="commentLoading"
                            style="margin-top: 30px;">
                                <div class="d-flex justify-content-center">
                                    <div class="spinner-border m-5" role="status">
                                        <span class="sr-only">Loading...</span>
                                    </div>
                                </div>
                                <div class="d-flex justify-content-center">
                                    <h3>Loading...</h3>
                                </div>
                            </div>
                            <div class="d-flex justify-content-center" v-else-if="commentEmpty">
                                <p class="mt-4">{{ comment }}</p>
                            </div>
                            <div class="not-bg" style="padding: 20px;" v-else>
                                <div class="row">
                                    <p class="col-11">{{ comment.text }}</p>
                                    <i v-if="comment.meta.is_able_to_delete" @click="selectDeleteComment(comment.id)" style="cursor: pointer; padding: 0;" class="col fas fa-times fa-1x d-flex justify-content-end x-button"></i>
                                </div>
                                <b class="d-flex"><i><p class="language" v-if="language === 'th'">โดย {{ comment.written_by }}</p>
                                <p class="language" v-else>By {{ comment.written_by }}</p></i></b>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <!-- End -->
        </div>
        <div class="col-md"></div>
        <!-- Modal -->
        <the-alert :id="'alert'" :closetype="modalCloseType" :success="modalSuccess" :loading="modalLoading" :text="modalText">
            <template v-slot:header></template>
        </the-alert>
        <the-confirm :id="'deleteComment'">
            <h4 class="d-flex justify-content-center">
                <p class="language" v-if="language === 'th'">ลบความคิดเห็นนี้?</p>
                <p class="language" v-else>Delete this comment?</p>
            </h4>
            <template v-slot:footer>
                <button data-dismiss="modal" class="btn btn-danger mr-4" style="width: 75px;" @click="deleteComment(selectComment)">
                    <p class="language" v-if="language === 'th'">ใช่</p>
                    <p class="language" v-else>Yes</p>
                </button>
                <button data-dismiss="modal" class="btn btn-outline-secondary ml-4" style="width: 75px;">
                    <p class="language" v-if="language === 'th'">ไม่</p>
                    <p class="language" v-else>No</p>
                </button>
            </template>
        </the-confirm>
    </div>
</template>
<script>
import axios from 'axios'
import CommentForm from '../../form/CommentForm.vue';
import { $http } from '../../../assets/variables.js';
import TheAlert from '../../modal/TheAlert.vue';
import TheConfirm from '../../modal/TheConfirm.vue';

export default {
    components: {
        CommentForm,
        TheAlert,
        TheConfirm,
    },
    props: ['token', 'info', 'language'],
    provide() {
        return {
            closeComment: this.checkComment,
            refreshComment: this.getComment,
        }
    },
    data() {
        return {
            searchComment: '',
            comments: [['Loading.....']],
            createCommentCheck: false,
            commentLoading: true,
            commentEmpty: false,
            modalCloseType: '',
            modalSuccess: false,
            modalLoading: false,
            modalText: '',
            selectComment: '',
        }
    },
    computed: {
        createCommentMessage() {
            if (this.language === 'th') {
                if(this.createCommentCheck){
                    return 'ปิด';
                } else {
                    return 'แสดงความคิดเห็น';
                }
            } else {
                if(this.createCommentCheck){
                    return 'Close';
                } else {
                    return 'Comment';
                }
            }
        }
    },
    methods: {
        toggleForm(){
            window.$('#comment').collapse('toggle');
        },
        selectDeleteComment(id){
            this.selectComment = id;
            window.$('#deleteComment').modal('show');
        },
        deleteComment(id){
            axios.delete($http + 'api/asset/comment/' + id + '/', {
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                this.modalCloseType = '';
                this.modalSuccess = true;
                this.modalLoading = false;
                if (this.language === 'th') {
                    this.modalText = 'ความคิดเห็นถูกลบเรียบร้อย';
                } else {
                    this.modalText = 'Comment deleted.';
                }
                window.$('#alert').modal('show');
                this.getComment(this.info.id);
            }).catch(() => {
                this.modalCloseType = '';
                this.modalSuccess = false;
                this.modalLoading = false;
                if (this.language === 'th') {
                    this.modalText = 'เกิดข้อผิดพลาดระหว่างลบความคิดเห็น';
                } else {
                    this.modalText = 'Failed to delete comment.';
                }
                window.$('#alert').modal('show');
            })
        },
        searchfromComment(id) {
            this.commentLoading = true;
            axios.get($http + 'api/asset/comment?event=' + id + '&search=' + this.searchComment, {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.comments.shift()
                if (response.data.length === 0){
                    if (this.language === 'th') {
                        this.comments.push(['ไม่มีความคิดเห็นที่ตรงกับที่คุณหา'])
                    } else {
                        this.comments.push(['No comments match your search.'])
                    }
                    this.commentEmpty = true
                    this.commentLoading = false;
                } else {
                    this.comments.push(response.data)
                    this.commentLoading = false
                    this.commentEmpty = false;
                }
            });
        },
        getComment(id) {
            if(this.comments[0] !== undefined){
                    this.comments.shift();
                    this.comments.push(['Loading.....'])
                    this.commentLoading = true;
            }
            setTimeout(() => {
            if(id !== undefined && id !== null){
                this.comments.shift();
                axios.get($http + 'api/asset/comment?event=' + id, {
                    headers: {
                        Authorization: this.token
                    }
                }).then((response) => {
                    this.comments.push(response.data)
                    if(this.comments[0].length === 0){
                        this.comments.shift()
                        if (this.language === 'th') {
                            this.comments.push(['ยังไม่มีความคิดเห็นถูกสร้างขึ้น'])
                        } else {
                            this.comments.push(['No comments have been made yet.'])
                        }
                        this.commentEmpty = true;
                    } else {
                        this.commentEmpty = false;
                    }
                    this.commentLoading = false;
                }).catch((err) => {
                    console.log(err);
                    this.commentLoading = false;
                });
            }
            }, 2000);
        },
        checkComment() {
            this.createCommentCheck = !this.createCommentCheck;
        },
    },
    async mounted() {
        this.getComment(this.info.id);
    },
}
</script>
<style scoped src="../../../assets/main.css"></style>
<style scoped>
@media only screen and (max-width: 992px) {
    .comment-card{
        padding: 0;
    }
}
.x-button{
    margin-right: 5px;
}
</style>