<template>
    <div v-if="this.info.meta.own_membership_id !== null" class="row background">
            <div class="col-md-3"></div>
            <div class="col-md-6 mt-4 mb-4">
                <!-- Joining -->
                <div class="row">
                    <div class="d-none d-lg-block not-bg" style="height: 60px;">
                        <div class="d-flex" style="margin-top: 16px;">
                            <h4 class="ml-2">
                                <p class="language" v-if="language === 'th'">คำขอเข้าร่วม</p>
                                <p class="language" v-else>Join Requests</p>
                            </h4>
                        </div>
                    </div>
                    <div class="d-flex not-bg justify-content-center d-lg-none">
                        <h4 class="mt-1">
                            <p class="language" v-if="language === 'th'">คำขอเข้าร่วม</p>
                            <p class="language" v-else>Join Requests</p>
                        </h4>
                    </div>
                </div>
                <div v-if="request.length !== 0" class="mt-2">
                    <!-- PC -->
                    <div class="d-none d-lg-block">
                        <div class="row not-bg member mt-2" v-for="req in request" :key="req.id">
                            <div class="col-md d-flex">
                                <router-link :to="getProfileLink(req.user)">
                                    <div class="d-flex">
                                        <div class="picture">
                                            <img v-if="getRequestProfilePhoto(req.user)" class="mr-2" :src="getRequestProfilePhoto(req.user)">
                                            <img v-else src="../../../assets/user_placeholder.jpg" class="mr-2">
                                        </div>
                                        <p class="name">{{ getRequestUserName(req.user) }}</p>
                                    </div>
                                </router-link>
                            </div>
                            <div class="col-md d-flex justify-content-end">
                                <div class="button">
                                    <button @click="acceptRequest(req.id)" class="btn btn-success">
                                        <p class="language" v-if="language === 'th'">ยอมรับ</p>
                                        <p class="language" v-else>Accept</p>
                                    </button>
                                    <button @click="declineRequest(req.id)" class="btn btn-danger">
                                        <p class="language" v-if="language === 'th'">ปฏิเสธ</p>
                                        <p class="language" v-else>Decline</p>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <!-- Mobile -->
                    <div class="d-lg-none">
                        <div class="mt-2 not-bg" v-for="req in request" :key="req.id">
                            <div class="d-flex justify-content-center">
                                <router-link :to="getProfileLink(req.user)">
                                    <div class="d-flex">
                                        <div class="picture">
                                            <img v-if="getRequestProfilePhoto(req.user)" class="mr-2" :src="getRequestProfilePhoto(req.user)">
                                            <img v-else src="../../../assets/user_placeholder.jpg" class="mr-2">
                                        </div>
                                        <p class="name">{{ getRequestUserName(req.user) }}</p>
                                    </div>
                                </router-link>
                            </div>
                            <div class="d-flex justify-content-center">
                                <div class="button mb-2">
                                    <button @click="acceptRequest(req.id)" class="btn btn-success">
                                        <p class="language" v-if="language === 'th'">ยอมรับ</p>
                                        <p class="language" v-else>Accept</p>
                                    </button>
                                    <button @click="declineRequest(req.id)" class="btn btn-danger">
                                        <p class="language" v-if="language === 'th'">ปฏิเสธ</p>
                                        <p class="language" v-else>Decline</p>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <!-- end -->
                </div>
                <div v-else-if="joinLoading">
                    <div class="row">
                        <div class="d-flex justify-content-center">
                            <div class="spinner-border m-5" role="status">
                                <span class="sr-only">Loading...</span>
                            </div>
                        </div>
                        <div class="d-flex justify-content-center mb-4">
                            <h3>Loading...</h3>
                        </div>
                    </div>
                </div>
                <div v-else>
                    <div class="row mt-2">
                        <div class="d-none d-lg-block not-bg">
                            <div class="ml-2 mt-2 mb-2">
                                <p class="language" v-if="language === 'th'">ไม่มีคำขอเข้าร่วม</p>
                                <p class="language" v-else>No join request.</p>
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="d-lg-none not-bg d-flex justify-content-center">
                            <div class="mt-2 mb-2">
                                <p class="language" v-if="language === 'th'">ไม่มีคำขอเข้าร่วม</p>
                                <p class="language" v-else>No join request.</p>
                            </div>
                        </div>
                    </div>
                </div>
                <!-- Invitation -->
                <div class="mt-4">
                    <div class="row">
                        <!-- PC -->
                        <div class="d-none d-lg-block not-bg">
                            <div class="d-flex" style="height: 60px;">
                                <div class="col-md">
                                    <h4 class="ml-2 mt-3 mb-3">
                                        <p class="language" v-if="language === 'th'">คำเชิญเข้าร่วม</p>
                                        <p class="language" v-else>Invitations</p>
                                    </h4>
                                </div>
                                <div class="col-md d-flex justify-content-end m-2">
                                    <button data-target="#invite" data-toggle="modal" v-if="info.meta.own_membership_position > 0" class="btn btn-success">
                                        <p class="language" v-if="language === 'th'">เชิญ</p>
                                        <p class="language" v-else>Invite</p>
                                    </button>
                                </div>
                            </div>
                        </div>
                        <!-- Mobile -->
                        <div class="d-lg-none not-bg">
                            <div class="d-flex justify-content-center">
                                <h4 class="mt-1">
                                    <p class="language" v-if="language === 'th'">คำเชิญเข้าร่วม</p>
                                    <p class="language" v-else>Invitations</p>
                                </h4>
                            </div>
                            <div class="d-flex justify-content-center">
                                <button data-target="#invite" data-toggle="modal" v-if="info.meta.own_membership_position > 0" class="btn btn-success mb-2">
                                    <p class="language" v-if="language === 'th'">เชิญ</p>
                                    <p class="language" v-else>Invite</p>
                                </button>
                            </div>
                        </div>
                        <!-- Modal -->
                        <div
                        class="modal fade"
                        id="invite"
                        tabindex="-1"
                        aria-labelledby="exampleModalLabel"
                        aria-hidden="true"
                        >
                            <div class="modal-dialog">
                                <div class="modal-content">
                                    <div class="modal-header">
                                        <h5 class="modal-title" id="exampleModalLabel">
                                            <p class="language" v-if="language === 'th'">ส่งคำเชิญเข้าร่วม</p>
                                            <p class="language" v-else>Send Invitation</p>
                                        </h5>
                                        <button
                                        type="button"
                                        class="btn-close"
                                        data-dismiss="modal"
                                        aria-label="Close"
                                        ></button>
                                    </div>
                                    <div class="modal-body">
                                        <div class="row">
                                            <div class="form-outline col-9">
                                                <input type="text" id="friend" class="form-control" v-model="searchName" placeholder="Insert name, nickname or username here" />
                                                <label class="form-label mt-2" for="friend">
                                                    <p class="language" v-if="language === 'th'">เลือกคนที่จะเชิญ</p>
                                                    <p class="language" v-else>Choose to invite</p>
                                                </label>
                                            </div>
                                            <div class="col-2">
                                                <button
                                                    class="btn btn-primary"
                                                    type="button"
                                                    @click="searchInvite(info.id, searchName)"
                                                >
                                                    <i class="fas fa-search"></i>
                                                </button>
                                            </div>
                                        </div>
                                        <div>
                                            <button class="btn btn-outline-secondary" @click="selectUser(data)" v-for="data in searchData[0]" :key="data.id">{{ data.username }}&nbsp;{{ data.name }}</button>
                                        </div>
                                        <div>
                                            <p class="language" v-if="language === 'th'">ที่ถูกเลือก:</p>
                                            <p class="language" v-else>Selected:</p>
                                            <button class="btn btn-outline-secondary" @click="cancelUser(selected)" v-for="selected in selectedInvite" :key="selected.id">{{ selected.username }}{{ selected.name }}</button>
                                        </div>
                                    </div>
                                    <div class="modal-footer">
                                        <button @click="selectAll" class="btn btn-success" >
                                            <p class="language" v-if="language === 'th'">เลือกทั้งหมด</p>
                                            <p class="language" v-else>Select all</p>
                                        </button>
                                        <button @click="sendInvite(info.id)" type="button" class="btn btn-success">
                                            <p class="language" v-if="language === 'th'">เชิญ</p>
                                            <p class="language" v-else>Invite</p>
                                        </button>
                                        <button type="button" class="btn btn-light" data-dismiss="modal">
                                            <p class="language" v-if="language === 'th'">ปิด</p>
                                            <p class="language" v-else>Close</p>
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div v-if="inviteRequest.length !== 0" class="mt-2">
                        <!-- PC -->
                        <div class="d-none d-lg-block">
                            <div class="row not-bg member mt-2" v-for="invite in inviteRequest[0]" :key="invite.id">
                                <div class="col-md">
                                    <router-link :to="getProfileLink(invite.invitee)">
                                        <div class="d-flex">
                                            <div class="picture">
                                                <img v-if="getInviteProfilePhoto(invite.invitee)" class="mr-2" :src="getInviteProfilePhoto(invite.invitee)">
                                                <img v-else src="../../../assets/user_placeholder.jpg" class="mr-2">
                                            </div>
                                            <p class="name">{{ getInvitedName(invite.invitee) }}</p>
                                        </div>
                                    </router-link>
                                </div>
                                <div class="col-md d-flex justify-content-end mr-2 mt-1">
                                    <div class="button">
                                        <button v-if="invite.meta.is_able_to_cancel === true" @click="cancelInvite(invite.id)" class="btn btn-danger">
                                            <p class="language" v-if="language === 'th'">ยกเลิกคำเชิญ</p>
                                            <p class="language" v-else>Cancel invitation</p>
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <!-- Mobile -->
                        <div class="d-lg-none">
                            <div class="not-bg" v-for="invite in inviteRequest[0]" :key="invite.id">
                                <div class="mt-2 d-flex justify-content-center">
                                    <router-link :to="getProfileLink(invite.invitee)">
                                        <div class="d-flex">
                                            <div class="picture">
                                                <img v-if="getInviteProfilePhoto(invite.invitee)" class="mr-2" :src="getInviteProfilePhoto(invite.invitee)">
                                                <img v-else src="../../../assets/user_placeholder.jpg" class="mr-2">
                                            </div>
                                            <p class="name">{{ getInvitedName(invite.invitee) }}</p>
                                        </div>
                                    </router-link>
                                </div>
                                <div class="d-flex justify-content-center">
                                    <div class="button mb-2">
                                        <button v-if="invite.meta.is_able_to_cancel === true" @click="cancelInvite(invite.id)" class="btn btn-danger">
                                            <p class="language" v-if="language === 'th'">ยกเลิกคำเชิญ</p>
                                            <p class="language" v-else>Cancel invitation</p>
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div v-else-if="inviteLoading" class="ml-2">
                        <div class="row">
                            <div class="d-flex justify-content-center">
                                <div class="spinner-border m-5" role="status">
                                    <span class="sr-only">Loading...</span>
                                </div>
                            </div>
                            <div class="d-flex justify-content-center mb-4">
                                <h3>Loading...</h3>
                            </div>
                        </div>
                    </div>
                    <div v-else class="mt-2">
                        <div class="row not-bg">
                            <div class="d-none d-lg-block ml-2 mt-2">
                                <p class="language" v-if="language === 'th'">ไม่มีคำเชิญเข้าร่วม</p>
                                <p class="language" v-else>No pending invitation.</p>
                            </div>
                            <div class="d-lg-none d-flex justify-content-center mt-2">
                                <p class="language" v-if="language === 'th'">ไม่มีคำเชิญเข้าร่วม</p>
                                <p class="language" v-else>No pending invitation.</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="col-md"></div>
            <!-- Modal -->
            <the-alert :id="'alert'" :closetype="modalCloseType" :success="modalSuccess" :loading="modalLoading" :text="modalText">
                <template v-slot:header></template>
            </the-alert>

            <the-alert :id="'inviting'" :closetype="modalCloseType" :success="modalSuccess" :loading="modalLoading" :text="modalText">
                <template v-slot:picture>
                    <slot name="picture">
                        <div v-if="modalLoading" class="spinner-border m-5 d-flex justify-content-center" role="status">
                            <span class="sr-only">Loading...</span>
                        </div>
                        <img v-else-if="modalSuccess" src="../../../assets/mail.svg" style="width: 100px;">
                        <img v-else src="../../../assets/Cancel.svg" style="width: 100px;">
                    </slot>
                </template>
                <h4 v-if="!modalLoading" class="d-flex justify-content-center">
                    <p class="language" v-if="language === 'th'">คำเชิญชวนถูกส่งให้กับ</p>
                    <p class="language" v-else>Invitation send to</p>&nbsp;
                </h4>
                <h4 v-else class="d-flex justify-content-center">
                    <p class="language">Loading...</p>
                </h4>
                <h4 class="d-flex justify-content-center" v-for="invite in inviting" :key="invite">{{ invite }}</h4>
                <template v-slot:header></template>
            </the-alert>
        </div>
</template>
<script>
import axios from 'axios';
import { $http } from '../../../assets/variables.js';
import TheAlert from '../../modal/TheAlert.vue';

export default {
    components: {
        TheAlert,
    },
    props: ['token', 'info', 'language'],
    data() {
        return {
            invitename: [],
            inviteRequest: [],
            selectedInvite: [],
            searchData: [],
            searchName: '',
            request: '',
            requestUser: [],
            joinLoading: true,
            inviteLoading: true,
            joinError: '',
            inviteError: '',
            modalCloseType: '',
            modalSuccess: false,
            modalLoading: false,
            modalText: '',
            inviting: [],
        }
    },
    methods: {
        getInviteProfilePhoto(id){
            var member = this.invitename.find((mem) => mem.id === id);
            if (member !== undefined) {
                return member.profile_picture;
            }
        },
        getRequestProfilePhoto(id){
            var member = this.requestUser.find((mem) => mem.id === id);
            if (member !== undefined) {
                return member.profile_picture;
            }
        },
        cancelInvite(id){
            this.modalCloseType = '';
            this.modalSuccess = false;
            this.modalLoading = true;
            this.modalText = 'Loading...'
            window.$('#alert').modal('show');
            axios.delete($http + 'api/membership/invitation/' + id + '/', {
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                this.modalCloseType = '';
                this.modalSuccess = true;
                this.modalLoading = false;
                if (this.language === 'th') {
                    this.modalText = 'คำเชิญถูกยกเลิกเรียบร้อย'
                } else {
                    this.modalText = 'Invitation cancelled.'
                }
                this.getInvitationData(this.info.id);
            }).catch(() => {
                this.modalCloseType = '';
                this.modalSuccess = false;
                this.modalLoading = false;
                if (this.language === 'th') {
                    this.modalText = 'เกิดข้อผิดพลาดระหว่างยกเลิกคำเชิญ'
                } else {
                    this.modalText = 'Failed to cancel invitation.'
                }
            })
        },
        getInvitedName(id){
            var invited = this.invitename.find((inv) => inv.id === id);
            if(invited !== undefined){
                return invited.name
            }
        },
        getInvitationData(id) {
            this.inviteLoading = true;
            this.inviteRequest.shift();
            axios.get($http + 'api/membership/invitation?community=' + id + '&status=W', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                if(response.data.length !== 0){
                    this.inviteRequest.push(response.data)
                }
                if(this.inviteRequest[0].length > 0){
                    var invite
                    var save = this.inviteRequest[0]
                    for(invite in save){
                        axios.get($http + 'api/user/user/' + save[invite].invitee, {
                            headers: {
                                Authorization: this.token
                            }
                        }).then((response) => {
                            this.invitename.push(response.data)
                        })
                    }
                }
                this.inviteLoading = false;
            }).catch((err) => {
                console.log(err.response)
                this.inviteLoading = false;
            });
        },
        sendInvite(id) {
            var select;
            var theErrorOne = '';
            this.inviting = [];
            this.modalCloseType = '';
            this.modalSuccess = false;
            this.modalLoading = true;
            this.modalText = 'Loading...'
            window.$('#inviting').modal('show');
            for (select in this.selectedInvite) {
                axios.post($http + 'api/membership/invitation/', {
                    community: id,
                    invitee: this.selectedInvite[select].id
                }, {
                    headers: {
                        Authorization: this.token
                    }
                }).then((response) => {
                    var save = this.selectedInvite.find((sel) => sel.id === response.data.invitee)
                    this.inviting.push(save.name);
                    this.modalCloseType = '';
                    this.modalSuccess = true;
                    this.modalLoading = false;
                    var pos = this.selectedInvite.indexOf(save);
                    this.selectedInvite.splice(pos, 1);
                    this.getInvitationData(this.info.id);
                }).catch((error) => {
                    console.log(error)
                    theErrorOne = theErrorOne + ' ' + this.selectedInvite[select].name + ' ';
                    // this.modalCloseType = '';
                    // this.modalSuccess = false;
                    // this.modalLoading = false;
                    // this.modalText = 'Invitation that send to ' + theErrorOne + 'is failed.'
                    var save = this.selectedInvite.find((sel) => sel.id === this.selectedInvite[select].id)
                    var pos = this.selectedInvite.indexOf(save);
                    this.selectedInvite.splice(pos, 1);
                    this.getInvitationData(this.info.id);
                });
            }
        },
        cancelUser(user) {
            let use = this.selectedInvite.indexOf(user)
            this.selectedInvite.splice(use, 1);
        },
        selectUser(member) {
            this.selectedInvite.push(member);
            let use = this.searchData[0].indexOf(member);
            this.searchData[0].splice(use, 1);
        },
        selectAll(){
            var member;
            var all = this.searchData[0]
            for(member in all){
                this.selectedInvite.push(all[member]);
            }
            this.searchData[0] = []
        },
        searchInvite(id, name) {
            this.searchData.shift();
            axios.get($http + 'api/user/user?is_applicable_for=' + id + '&search=' + name, {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.searchData.push(response.data)
            });
        },
        getRequestUserName(userId) {
            var user = this.requestUser.find((req) => req.id === userId);
            if(user !== undefined){
                return user.name;
            }
        },
        getProfileLink(id) {
            return '/profile/' + id;
        },
        acceptRequest(id) {
            this.modalCloseType = '';
            this.modalSuccess = false;
            this.modalLoading = true;
            this.modalText = 'Loading..'
            window.$('#alert').modal('show');
            var data = {
                status: "A"
            };
            axios.put($http + 'api/membership/request/' + id +'/', data, {
                headers:{
                    Authorization: this.token
                },
            }).then(() => {
                this.modalCloseType = '';
                this.modalSuccess = true;
                this.modalLoading = false;
                if (this.language === 'th') {
                    this.modalText = 'คำขอถูกยอมรับเรียบร้อย'
                } else {
                    this.modalText = 'Request Accepted.'
                }
                this.getRequestData(this.info.id);
            }).catch((err) => {
                console.log(err);
                this.modalCloseType = '';
                this.modalSuccess = false;
                this.modalLoading = false;
                if (this.language === 'th') {
                    this.modalText = 'เกิดข้อผิดพลาดระหว่างยอมรับคำขอ'
                } else {
                    this.modalText = 'Failed to accept request.'
                }
            });
        },
        declineRequest(id) {
            this.modalCloseType = '';
            this.modalSuccess = false;
            this.modalLoading = true;
            this.modalText = 'Loading..'
            window.$('#alert').modal('show');
            var data = {
                status: "D"
            };
            axios.put($http + 'api/membership/request/' + id +'/', data, {
                headers:{
                    Authorization: this.token
                },
            }).then(() => {
                this.modalCloseType = '';
                this.modalSuccess = true;
                this.modalLoading = false;
                if (this.language === 'th') {
                    this.modalText = 'คำขอถูกปฏิเสธเรียบร้อย'
                } else {
                    this.modalText = 'Request Declinded.'
                }
                this.getRequestData(this.info.id);
            }).catch((err) => {
                console.log(err);
                this.modalCloseType = '';
                this.modalSuccess = true;
                this.modalLoading = false;
                if (this.language === 'th') {
                    this.modalText = 'เกิดข้อผิดพลาดระหว่างปฏิเสธคำขอ'
                } else {
                    this.modalText = 'Failed to decline request.'
                }
            });
        },
        getRequestData(id){
            this.joinLoading = true;
            this.requestUser = [];
            this.request = '';
            axios.get($http + 'api/membership/request?community=' + id + '&status=W', {
                headers: {
                    Authorization: this.token
                }
            })
            .then((response) => {
                this.request = response.data
                var i
                for(i in this.request) {
                    axios.get($http + 'api/user/user/' + this.request[i].user, {
                        headers: {
                            Authorization: this.token
                        }
                    }).then((response) => {
                        this.requestUser.push(response.data)
                    })
                }
                this.joinLoading = false;
            }).catch((err) => {
                console.log(err.response)
                if (this.language === 'th') {
                    this.joinError = 'เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่'
                } else {
                    this.joinError = "Data fetching error, please try again."
                }
                this.joinLoading = false;
            });
            
        },
    },
    async mounted() {
        this.getRequestData(this.info.id);
        this.getInvitationData(this.info.id);
    },
}
</script>
<style scoped src="../../../assets/main.css"></style>
<style scoped>
a{
    text-decoration: none;
    color: black;
}
a:hover{
    color: black;
}
img{
    width: 100%;
}
.picture{
    width: 50px;
    height: 50px;
