<template>
    <div class="row">
        <div class="col-md"></div>
        <div class="col-md mt-4">
            <div class="d-flex justify-content-center">
                <h2>{{ text }}</h2>
            </div>
        </div>
        <div class="col-md"></div>
    </div>
</template>
<script>
import axios from 'axios';
import { $http } from '../../assets/variables.js';

export default {
    data () {
        return {
            token: '',
            language: '',
            text: ''
        }
    },
    methods: {
        unsubscribe(){
            if(this.language === 'th'){
                this.text = 'คุณได้ทำการยกเลิกการรับข่าวสารจากอีเมลแล้ว'
            } else {
                this.text = 'You are now unsubscribed from email notifications.'
            }
            this.token = window.$cookies.get('token');
            if(this.token === null){
                this.token = window.$cookies.get("itkmitlcommunitytokenbrowser");
            }
            axios.post($http + 'api/user/unsubscribe/', {
                key: this.$route.query.key,
                username: this.$route.query.username
            }, {
                headers: {
                    Authorization: this.token
                }
            }).catch((err) => {
                if(this.language === 'th') {
                    this.text = 'เกิดผิดพลาดระหว่างยกเลิกการรับข่าวสารผ่านอีเมล'
                } else {
                    this.text = 'Unsubscribe error.'
                }
                if(err.response.data.detail){
                    this.text = err.response.data.detail;
                }
            })
        }
    },
    async mounted() {
        this.language = window.$cookies.get('itkmitlcommunitylanguage');
        if(this.$route.query.key === undefined || this.$route.query.key === null || this.$route.query.key === ''){
            if(this.language === 'th') {
                this.text = 'เกิดผิดพลาดระหว่างยกเลิกการรับข่าวสารผ่านอีเมล'
            } else {
                this.text = 'Unsubscribe error.'
            }
        } else {
            this.unsubscribe();
        }
    }
}
</script>