<template>
    <div class="row background">
        <div class="col-md-3"></div>
        <div class="col-md-6 mt-4 mb-4 not-bg">
            <div v-if="qrcode.length !== 0">
                <div class="d-flex justify-content-center">
                    <p v-if="qrcode.text !== null || qrcode.text !== undefined">{{ qrcode[0].text }}</p>
                    <img :src="qrcode[0].image" style="max-width: 100%;">
                </div>
                <div v-if="info.meta.own_membership_position > 1">
                    <div class="d-flex justify-content-center">
                        <div class="d-none d-lg-block mb-4">
                            <button @click="regenerateQRCode(qrcode[0].id, info.id)" class="btn btn-primary mr-2">
                                <p class="language" v-if="language === 'th'">สร้างรหัสคิวอาร์ใหม่</p>
                                <p class="language" v-else>Regenerate QR Code</p>
                            </button>
                            <button @click="deleteQRCode(qrcode[0].id, info.id)" class="btn btn-danger">
                                <p class="language" v-if="language === 'th'">ลบรหัสคิวอาร์</p>
                                <p class="language" v-else>Delete QR Code</p>
                            </button>
                        </div>
                    </div>
                    <div class="d-lg-none">
                        <div class="d-flex justify-content-center mb-2">
                            <button @click="regenerateQRCode(qrcode[0].id, info.id)" class="btn btn-primary mr-2">
                                <p class="language" v-if="language === 'th'">สร้างรหัสคิวอาร์ใหม่</p>
                                <p class="language" v-else>Regenerate QR Code</p>
                            </button>
                        </div>
                        <div class="d-flex justify-content-center">
                            <button @click="deleteQRCode(qrcode[0].id, info.id)" class="btn btn-danger">
                                <p class="language" v-if="language === 'th'">ลบรหัสคิวอาร์</p>
                                <p class="language" v-else>Delete QR Code</p>
                            </button>
                        </div>
                    </div>
                </div>
            </div>
            <div class="mt-4 mb-4" v-else>
                <div class="d-flex justify-content-center">
                    <h4>
                        <p class="language" v-if="language === 'th'">ยังไม่มีรหัสคิวอาร์</p>
                        <p class="language" v-else>No QR code yet</p>
                    </h4>
                </div>
                <div class="d-flex justify-content-center">
                    <button
                    v-if="info.meta.own_membership_position > 1"
                    class="btn btn-warning"
                    @click="createQRCode(info.id)">
                        <p class="language" v-if="language === 'th'">สร้างรหัสคิวอาร์</p>
                        <p class="language" v-else>Create QR Code</p>
                    </button>
                </div>
            </div>
            
        </div>
        <div class="col-md"></div>
        <!-- Modal -->
        <the-alert :id="'alert'" :closetype="modalCloseType" :success="modalSuccess" :loading="modalLoading" :text="modalText">
            <template v-slot:header></template>
        </the-alert>
    </div>
</template>
<script>
import axios from 'axios'
import { $http } from '../../../assets/variables.js';
import TheAlert from '../../modal/TheAlert.vue';

export default {
    components: {
        TheAlert,
    },
    props: ['token', 'info', 'language'],
    data() {
        return {
            qrcode: [],
            createQRButton: 'Create QR Code',
            regenQRButton: 'Regenerate QR Code',
            deleteQRButton: 'Delete QR Code',
            modalCloseType: '',
            modalSuccess: false,
            modalLoading: false,
            modalText: '',
        }
    },
    methods: {
        regenerateQRCode(qrid, eventid){
            this.regenQRButton = "Loading....."
            this.qrcode.shift();
            this.qrcode.push({ text: 'Loading.....' });
            axios.get($http + 'api/generator/join-key?event=' + eventid, {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.modalCloseType = '';
                this.modalSuccess = false;
                this.modalLoading = true;
                this.modalText = 'Loading...'
                window.$('#alert').modal('show');
                var joinkeyid = response.data[0].id
                axios.delete($http + 'api/generator/qr-code/' + qrid, {
                    headers: {
                        Authorization: this.token
                    }
                }).then(() => {
                    axios.delete($http + 'api/generator/join-key/' + joinkeyid, {
                        headers: {
                            Authorization: this.token
                        }
                    }).then(() => {
                        axios.get($http + 'api/generator/join-key/generate', {
                            headers: {
                                Authorization: this.token
                            }
                        }).then((response) => {
                            var qrkey = response.data.key;
                            axios.post($http + 'api/generator/join-key/', {
                                key: qrkey,
                                event: eventid
                            }, {
                                headers: {
                                    Authorization: this.token
                                }
                            }).then((response) => {
                                var qrurl = 'http://127.0.0.1:8080/join/' + qrkey;
                                axios.post($http + 'api/generator/qr-code/', {
                                    url: qrurl,
                                    event: eventid
                                },{
                                    headers: {
                                        Authorization: this.token
                                    }
                                }).then(() => {
                                    this.modalCloseType = 'refresh';
                                    this.modalSuccess = true;
                                    this.modalLoading = false;
                                    if(this.language === 'th') {
                                        this.modalText = 'รหัสคิวอาร์ถูกสร้างขึ้นแล้ว'
                                    } else {
                                        this.modalText = 'QR code generated.';
                                    }
                                }).catch(() => {
                                    axios.delete($http + 'api/generator/join-key/' + response.data.id, {
                                        headers: {
                                            Authorization: this.token
                                        }
                                    })
                                })
                            }).catch(() => {
                                this.modalCloseType = '';
                                this.modalSuccess = false;
                                this.modalLoading = false;
                                if(this.language === 'th') {
                                    this.modalText = 'เกิดข้อผิดพลาดระหว่างสร้างรหัส'
                                } else {
                                    this.modalText = 'Failed to generate key.';
                                }
                            });
                        })
                    });
                });
            });
        },
        deleteQRCode(qrid, eventid){
            this.deleteQRButton = "Loading....."
            this.modalCloseType = '';
            this.modalSuccess = false;
            this.modalLoading = true;
            this.modalText = 'Loading...'
            window.$('#alert').modal('show');
            axios.get($http + 'api/generator/join-key?event=' + eventid, {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                var joinkeyid = response.data[0].id
                axios.delete($http + 'api/generator/qr-code/' + qrid, {
                    headers: {
                        Authorization: this.token
                    }
                }).then(() => {
                    axios.delete($http + 'api/generator/join-key/' + joinkeyid, {
                        headers: {
                            Authorization: this.token
                        }
                    }).then(() => {
                        this.modalCloseType = 'refresh';
                        this.modalSuccess = true;
                        this.modalLoading = false;
                        if(this.language === 'th') {
                            this.modalText = 'รหัสคิวอาร์ถูกลบแล้ว'
                        } else {
                            this.modalText = 'QR code has been deleted.'
                        }
                    });
                });
            }).catch(() => {
                this.modalCloseType = '';
                this.modalSuccess = false;
                this.modalLoading = false;
                if(this.language === 'th') {
                    this.modalText = 'เกิดข้อผิดพลาดระหว่างลบรหัสคิวอาร์'
                } else {
                    this.modalText = 'Failed to delete QR code.'
                }
            });
        },
        createQRCode(id){
            this.createQRButton = "Loading....."
            this.modalCloseType = '';
            this.modalSuccess = false;
            this.modalLoading = true;
            this.modalText = 'Loading...'
            window.$('#alert').modal('show');
            axios.get($http + 'api/generator/join-key/generate', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                var qrkey = response.data.key;
                axios.post($http + 'api/generator/join-key/', {
                    key: qrkey,
                    event: id
                }, {
                    headers: {
                        Authorization: this.token
                    }
                }).then((response) => {
                        var qrurl = 'http://127.0.0.1:8080/join/' + qrkey;
                        axios.post($http + 'api/generator/qr-code/', {
                            url: qrurl,
                            event: id
                        },{
                            headers: {
                                Authorization: this.token
                            }
                        }).then(() => {
                            this.modalCloseType = 'refresh';
                            this.modalSuccess = true;
                            this.modalLoading = false;
                            if(this.language === 'th') {
                                this.modalText = 'รหัสคิวอาร์ถูกสร้างขึ้นแล้ว'
                            } else {
                                this.modalText = 'QR code generated.';
                            }
                        }).catch(() => {
                            axios.delete($http + 'api/generator/join-key/' + response.data.id, {
                                headers: {
                                    Authorization: this.token
                                }
                            })
                        })
                }).catch(() => {
                    this.modalCloseType = '';
                    this.modalSuccess = false;
                    this.modalLoading = false;
                    if(this.language === 'th') {
                        this.modalText = 'เกิดข้อผิดพลาดระหว่างสร้างรหัส'
                    } else {
                        this.modalText = 'Failed to generate key.';
                    }
                });
            })
        },
        getQRCode(id){
            axios.get($http + 'api/generator/qr-code?event=' + id, {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.qrcode = response.data;
            });
        },
    },
    async mounted() {
        this.getQRCode(this.info.id);
    },
}
</script>
<style scoped src="../../../assets/main.css"></style>