<template>
    <div class="col-lg-3 ml-2 mr-2 mb-2 album-border">
        <router-link :to="'/album/' + comid + '/' + albumid">
        <div class="box" style="margin-left: -5px; margin-top: 5px;"></div>
        <div class="box" style="margin-left: -10px; margin-top: 10px;"></div>
        <div v-if="picture === null">
            <div class="preview">
                <p class="d-flex justify-content-center" style="margin-top: 45%;">{{ error }}</p>
            </div>
            <p class="d-flex justify-content-center mt-4 album-name">{{ name }}</p>
        </div>
        <div v-else>
            <div class="preview">
                <img :src="picture"/>
            </div>
            <p class="d-flex justify-content-center mt-4 album-name">{{ name }}</p>
        </div>
        </router-link>
    </div>
</template>
<script>
import axios from 'axios';
import { $http } from '../../assets/variables.js';

export default {
    props: ['albumid', 'name', 'comid', 'token', 'language'],
    data() {
        return {
            picture: null,
            error: 'Loading.....'
        }
    },
    methods: {
        albumPreview(id){
            axios.get($http + 'api/asset/album/image?album='+ id +'&limit=1', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                if(response.data.length === 0){
                    if(this.language === 'th') {
                        this.error = 'ยังไม่มีรูปภาพ'
                    } else {
                        this.error = 'No picture yet.'
                    }
                } else {
                    this.picture = response.data[0].image;
                }
            }).catch((err) => {
                console.log(err)
                if(this.language === 'th') {
                    this.error = "เกิดข้อผิดพลาดระหว่างดึงรูปภาพ"
                } else {
                    this.error = "Error can't get picture."
                }
            })
        },
    },
    async mounted() {
        this.albumPreview(this.albumid);
    },
}
</script>
<style scoped src="../../assets/main.css"></style>
