<template>
    <div class="row mb-4">
        <div class="col-md">
            <img src="../../assets/back.svg" @click="this.$router.back()" class="mt-4 ml-3 d-none d-lg-block back-button">
        </div>
        <div class="col-md not-bg mt-4 form-padding">
            <h2 class="mb-2 d-none d-lg-block">
                <p class="language" v-if="language === 'th'">แก้ไขข้อมูลส่วนตัว</p>
                <p class="language" v-else>Edit Profile</p>
            </h2>

            <div class="d-lg-none row">
                <h2 class="mb-2 col-9">
                    <p class="language" v-if="language === 'th'">แก้ไขข้อมูลส่วนตัว</p>
                    <p class="language" v-else>Edit Profile</p>
                </h2>
                <i @click="this.$router.back()" style="cursor: pointer;" class="fas fa-times-circle fa-2x col d-flex justify-content-end mt-1"></i>
            </div>
            
            <label class="custom-file-label mb-4" for="inputGroupFile01">
                <p class="language" v-if="language === 'th'">รูปประจำตัว</p>
                <p class="language" v-else>Profile Picture</p>
            </label><br>
            <div class="form-image mb-4" v-if="current_profile !== null">
                <img class="image" ref="image" :src="current_profile">
            </div>
            <div v-if="preview !== null" class="mb-4">
                <p class="language" v-if="language === 'th'">ตัวอย่าง</p>
                <p class="language" v-else>Preview</p>
                <img class="mb-4" :src="preview" style="width: 175px; height: 175px;"><br>
                <button @click="cancelPicture">
                    <p class="language" v-if="language === 'th'">เอารูปออก</p>
                    <p class="language" v-else>Remove Image</p>
                </button><br>
            </div>
            <div v-if="preview === null" class="input-group mb-4">
                <div class="custom-file">
                    <input type="file" class="custom-file-input" id="inputGroupFile01" ref="ProfilePic" @change="ProfilePic"
                    aria-describedby="inputGroupFileAddon01" accept="image/*">
                </div>
                <p class="error" v-if="errorMessage.profile_picture">{{ errorMessage.profile_picture[0] }}</p>
            </div>
            
            <!-- <label class="custom-file-label" for="inputGroupFile01">Cover Picture</label><br>
            <img class="banner" :src="current_cover">
            <div class="input-group mb-4">
                <div class="custom-file">
                    <input type="file" class="custom-file-input" id="inputGroupFile02" ref="coverPic" @change="coverPic"
                    aria-describedby="inputGroupFileAddon02" accept="image/*">
                </div>
                <p class="error" v-if="errorMessage.cover_photo">{{ errorMessage.cover_photo[0] }}</p>
            </div> -->

            <div class="form-outline mb-4">
                <label class="form-label" for="username">
                    <p class="language" v-if="language === 'th'">ชื่อผู้ใช้</p>
                    <p class="language" v-else>Username</p>
                </label>
                <input type="text" class="form-control" id="username" disabled :value="myinfo.username"/>
            </div>

            <div class="form-outline mb-4">
                <label class="form-label" for="name">
                    <p class="language" v-if="language === 'th'">ชื่อ</p>
                    <p class="language" v-else>Name</p>
                </label>
                <input type="text" class="form-control" id="name" disabled :value="myinfo.name"/>
            </div>

            <div class="form-outline mb-4">
                <label class="form-label" for="nickname">
                    <p class="language" v-if="language === 'th'">ชื่อเล่น</p>
                    <p class="language" v-else>Nickname</p>
                </label>
                <input type="text" class="form-control" id="nickname" v-model="nickname" />
                <p class="error" v-if="errorMessage.nickname">{{ errorMessage.nickname[0] }}</p>
            </div>

            <div class="form-outline mb-4">
                <label class="form-label" for="bio">
                    <p class="language" v-if="language === 'th'">ประวัติ</p>
                    <p class="language" v-else>Bio</p>
                </label>
                <textarea class="form-control" id="bio" rows="4" v-model="bio"></textarea>
                <p class="error" v-if="errorMessage.bio">{{ errorMessage.bio[0] }}</p>
            </div>

            <div class="form-outline mb-4 mt-4">
                <label class="form-label" for="birthdate">
                    <p class="language" v-if="language === 'th'">วันเกิด</p>
                    <p class="language" v-else>Birth Date</p>
                </label>
                <input type="date" id="birthdate" class="form-control" v-model="birthdate" placeholder="dd-mmm-yyyy" />
                <p class="error" v-if="errorMessage.birthdate">{{ errorMessage.birthdate[0] }}</p>
            </div>

            <button @click="sendData" type="submit" class="btn btn-primary btn-block">
                <p class="language" v-if="language === 'th'">บันทึก</p>
                <p class="language" v-else>Save</p>
            </button>
        </div>
        <div class="col-md"></div>
        <!-- Modal -->
        <the-alert :id="'alert'" :closetype="modalCloseType" :success="modalSuccess" :loading="modalLoading" :text="modalText" :url="modalUrl">
            <template v-slot:header></template>
        </the-alert>
    </div>
</template>
<script>
import Cropper from 'cropperjs';
import axios from 'axios';
import { $http } from '../../assets/variables.js';
import TheAlert from '../modal/TheAlert.vue';

export default {
    components: {
        TheAlert,
    },
    props: ['profileid'],
    data () {
        return {
            saveButtonText: 'Save',
            cropper: {},
            image: {},
            preview: null,
            myinfo: '',
            nickname: '',
            bio: '',
            current_profile: null,
            profile_picture: null,
            current_cover: null,
            cover_photo: null,
            birthdate: '',
            errorMessage: '',
            errorTexted: [],
            modalCloseType: '',
            modalLoading: false,
            modalSuccess: false,
            modalText: '',
            modalUrl: '',
            language: '',
        }
    },
    methods: {
        errorText(){
            var error;
            for(error in this.errorMessage){
                console.log(error);
                var element = document.getElementById(error);
                element.classList.toggle('error-box');
            }
            this.errorTexted = this.errorMessage;
        },
        cancelError(){
            var error;
            for(error in this.errorTexted){
                console.log(error);
                var element = document.getElementById(error);
                element.classList.toggle('error-box');
            }
            this.errorMessage = [];
        },
        cancelPicture(){
            this.profile_picture = null;
            this.current_profile = null;
            this.preview = null;
        },
        ProfilePic(event){
            this.profile_picture = event.target.files[0];
            var reader = new FileReader();
            reader.onload = (e) => {
                this.current_profile = e.target.result;
            }
            reader.readAsDataURL(this.profile_picture);
            setTimeout(() => {
                this.image = this.$refs.image;
                this.cropper = new Cropper(this.image, {
                    zoomable: false,
                    scalable: false,
                    aspectRatio: 1,
                    crop: () => {
                        const canvas = this.cropper.getCroppedCanvas();
                        this.preview = canvas.toDataURL("image/png");
                        canvas.toBlob((blob) => {
                            this.profile_picture = blob
                        })
                        // this.profile_picture = canvas.toDataURL("image/png");
                    }
                })
            }, 1000);
        },
        coverPic(event){
            this.cover_photo = event.target.files[0];
            var reader = new FileReader();
            reader.onload = (e) => {
                this.current_cover = e.target.result;
            }
            reader.readAsDataURL(this.cover_photo)
        },
        sendData() {
            this.modalCloseType = '';
            this.modalLoading = true;
            this.modalSuccess = false;
            this.modalText = 'Loading...';
            this.modalUrl = '';
            window.$('#alert').modal('show');
            this.cancelError();
            this.saveButtonText = 'Loading.....'
            var data = {
                nickname: this.nickname,
                bio: this.bio,
                birthdate: this.birthdate
            };
            axios.patch($http + 'api/user/user/' + this.profileid + '/', data, {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                if(response.status === 200){
                    const picturedata = new FormData();
                    var check = false
                    if(this.profile_picture !== null){
                        picturedata.append('profile_picture', this.profile_picture, 'profile.png');
                        check = true
                    }
                    if(this.cover_photo !== null){
                        picturedata.append('cover_photo', this.cover_photo);
                        check = true
                    }
                    if(check) {
                        axios.patch($http + 'api/user/user/' + this.profileid + '/', picturedata, {
                            headers: {
                                Authorization: this.token
                            }
                        }).then(() => {
                            this.modalCloseType = 'redirect';
                            this.modalLoading = false;
                            this.modalSuccess = true;
                            this.modalText = 'Profile edited.';
                            this.modalUrl = '/profile/' + this.profileid;
                        })
                    } else {
                        this.modalCloseType = 'redirect';
                        this.modalLoading = false;
                        this.modalSuccess = true;
                        this.modalText = 'Profile edited.';
                        this.modalUrl = '/profile/' + this.profileid;
                    }
                }
            }).catch((err) => {
                console.log(err);
                this.modalCloseType = '';
                this.modalLoading = false;
                this.modalSuccess = false;
                this.modalText = 'Failed to edit profile.';
                this.modalUrl = '';
                this.errorMessage = err.response.data;
                this.errorText();
            });
        },
        getData() {
            this.token = window.$cookies.get('token');
            if(this.token === null){
                this.token = window.$cookies.get("itkmitlcommunitytokenbrowser");
            }
            this.language = window.$cookies.get('itkmitlcommunitylanguage');
            axios.get($http + 'api/user/user/' + this.profileid, {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.myinfo = response.data
                this.nickname = this.myinfo.nickname
                this.bio = this.myinfo.bio
                this.birthdate = this.myinfo.birthdate
                this.current_profile = this.myinfo.profile_picture
                this.current_cover = this.myinfo.cover_photo
                axios.get($http + 'api/user/user/me', {
                    headers: {
                        Authorization: this.token
                    }
                }).then((response) => {
                    if (response.data.id !== this.myinfo.id) {
                        this.$router.push('/yaytanoshiiseewhatyoudidthere')
                    }
                });
            });
        },
    },
    async mounted() {
        this.getData();
    },
}
</script>
<style scoped>
img{
    max-width: 100%;
}
</style>
<style scoped src="../../assets/main.css"></style>
<style scoped src="../../assets/form.css"></style>