<template>
    <div class="row mb-4">
        <div class="col-md">
            <img src="../../assets/back.svg" @click="this.$router.back()" class="mt-4 ml-3 d-none d-lg-block back-button">
        </div>
        <div class="col-md mt-4 not-bg">
            <div v-if="error !== ''">
                {{ error }}
            </div>
            <div v-else-if="info === ''">
                <div class="d-flex justify-content-center">
                    <div class="spinner-border m-5" role="status">
                        <span class="sr-only">Loading...</span>
                    </div>
                </div>
                <div class="d-flex justify-content-center">
                    <h1>Loading...</h1>
                </div>
            </div>
            <div class="row form-padding" v-else>
                <div class="container">
                    <div class="row d-flex justify-content-center">
                        
                        <h2 class="d-none d-lg-block mb-4" v-if="create">
                            <p class="language" v-if="language === 'th'">สร้างอัลบั้ม</p>
                            <p class="language" v-else>Create Album</p>
                        </h2>
                        <h2 class="d-none d-lg-block mb-4" v-else >
                            <p class="language" v-if="language === 'th'">แก้ไขอัลบั้ม</p>
                            <p class="language" v-else>Edit Album</p>
                        </h2>

                        <div class="d-lg-none row" style="padding: 0;">
                            <h2 class="mt-2 mb-2 col-9" v-if="create">
                                <p class="language" v-if="language === 'th'">สร้างอัลบั้ม</p>
                                <p class="language" v-else>Create Album</p>
                            </h2>
                            <h2 class="mt-2 mb-2 col-9" v-else >
                                <p class="language" v-if="language === 'th'">แก้ไขอัลบั้ม</p>
                                <p class="language" v-else>Edit Album</p>
                            </h2>
                            <i @click="this.$router.back()" style="cursor: pointer;" class="fas fa-times-circle fa-2x col d-flex justify-content-end mt-1"></i>
                        </div>

                        <div class="form-outline mb-4">
                            <label class="form-label" for="name">
                                <p class="language" v-if="language === 'th'">ชื่อ</p>
                                <p class="language" v-else>Name</p>
                            </label>
                            <input type="text" id="name" class="form-control" placeholder="The Album Name" v-model="name" />
                            <p class="error" v-if="formError.name">{{ formError.name[0] }}</p>
                        </div>
                        
                        <div v-if="event === 'no'" class="form-outline mb-4">
                            <label class="form-label">
                                <p class="language" v-if="language === 'th'">กิจกรรมชุมนุม / ห้องปฏิบัติการ</p>
                                <p class="language" v-else>Community Event</p>
                            </label>
                            <select id="community_event" class="form-control select" v-model="community_event" >
                                <option selected v-if="com_event_list.length === 0" value='none'>
                                    <p class="language" v-if="language === 'th'">ข้อความนี้จะหายไปเมื่อข็อมูลโหลดเสร็จ</p>
                                    <p class="language" v-else>This text will disappear when the data is loaded</p>
                                </option>
                                <option selected v-else value=''>-</option>
                                <option v-for="comevent in com_event_list" :key="comevent.id" :value="comevent.id">
                                    <p class="language" v-if="language === 'th'">{{ comevent.name_th }}</p>
                                    <p class="language" v-else>{{ comevent.name_en }}</p>
                                </option>
                            </select>
                            <p class="error" v-if="formError.community_event">{{ formError.community_event[0] }}</p>
                        </div>
                        
                        <button v-if="!create" @click="saveDetail(albumid)" class="btn primary btn-block">
                            <p class="language" v-if="language === 'th'">บันทึกข้อมูลด้านบน</p>
                            <p class="language" v-else>Save above data</p>
                        </button>
                    </div>
                </div>

                <div v-if="pictures.length === 0 && !emptyAlbum">
                    <div class="d-flex justify-content-center">
                        <div class="spinner-border m-5" role="status">
                            <span class="sr-only">Loading...</span>
                        </div>
                    </div>
                    <div class="d-flex justify-content-center">
                        <h1>Loading...</h1>
                    </div>
                </div>
                <div v-else-if="error_picture !== ''">{{ error_picture }}</div>
                <div v-else-if="emptyAlbum"></div>
                <div class="row mt-4 ml-1" style="padding: 0; margin: 0;" v-else>
                    <div v-for="picture in pictures" :key="picture.id" class="col-6 col-xl-4 mt-2 album-border" style="min-height:10rem; max-height:15rem; margin-left: -2px;">
                        <div class="d-flex justify-content-end m-1">
                            <i @click="deletePicture(picture.id)" style="cursor: pointer;" class="fas fa-times-circle fa-2x col d-flex justify-content-end mt-1 delete-picture"></i>
                        </div>
                        <div class="preview">
                            <img :src="picture.image">
                        </div>
                    </div>
                </div>

                <div class="container">
                    <!-- <div v-if="current_new_image.length !== 0" class="row mt-4">
                        <div v-for="image in current_new_image" :key='image' class="col-md-4 album-border">
                            <div class="d-flex justify-content-end m-1">
                                <i @click="cancelNewPicture(image)" style="cursor: pointer;" class="fas fa-times-circle fa-2x col d-flex justify-content-end mt-1 delete-picture"></i>
                            </div>
                            <div class="preview">
                                <img :src="image" style="width: 100%;">
                            </div>
                        </div>
                    </div> -->
                    <div class="input-group mt-4 mb-4">
                        <div class="custom-file">
                            <input type="file" class="custom-file-input" id="inputGroupFile01" ref="newPic" @change="newPic"
                            aria-describedby="inputGroupFileAddon01" accept="image/*" multiple>
                        </div>
                        <label class="custom-file-label mb-2" for="inputGroupFile01">
                            <p class="language" v-if="language === 'th'">*คุณสามารถอัพโหลดหลาย ๆ รูปได้พร้อมกัน</p>
                            <p class="language" v-else>*You can upload multiple photos at once.</p>
                        </label>
                    </div>
                    <div class="mb-4">
                        <!-- <button v-if="!create" @click="uploadPicture(albumid)" class="btn primary btn-block mb-4">
                            <p class="language" v-if="language === 'th'">อัพโหลดรูปภาพ</p>
                            <p class="language" v-else>Upload Pictures</p>
                        </button> -->
                        <button v-if="!create" data-target="#deleteAlbum" data-toggle="modal" class="btn btn-danger btn-block mb-4">
                            <p class="language" v-if="language === 'th'">ลบอัลบั้ม</p>
                            <p class="language" v-else>Delete Album</p>
                        </button>
                        <button v-if="create" @click="createAlbum" class="btn primary btn-block mb-4">
                            <p class="language" v-if="language === 'th'">สร้างอัลบั้ม</p>
                            <p class="language" v-else>Create Album</p>
                        </button>
                    </div>
                </div>
            </div>
        </div>
        <div class="col-md"></div>
        <!-- Modal -->
        <the-alert :id="'alert'" :closetype="modalCloseType" :success="modalSuccess" :loading="modalLoading" :text="modalText" :url="modalUrl">
            <template v-slot:header></template>
        </the-alert>
        <the-confirm :id="'deleteAlbum'">
            <h4 class="d-flex justify-content-center">
                <p class="language" v-if="language === 'th'">ต้องการลบอัลบั้ม?</p>
                <p class="language" v-else>Delete this album?</p>
            </h4>
            <template v-slot:footer>
                <button data-dismiss="modal" class="btn btn-danger mr-4" style="width: 75px;" @click="deleteAlbum(albumid)">
                    <p class="language" v-if="language === 'th'">ใช่</p>
                    <p class="language" v-else>Yes</p>
                </button>
                <button data-dismiss="modal" class="btn btn-outline-secondary ml-4" style="width: 75px;">
                    <p class="language" v-if="language === 'th'">ไม่</p>
                    <p class="language" v-else>No</p>
                </button>
            </template>
        </the-confirm>
    </div>
</template>
<script>
import axios from 'axios';
import { $http } from '../../assets/variables.js';
import TheAlert from '../modal/TheAlert.vue';
import TheConfirm from '../modal/TheConfirm.vue';

export default {
    components: {
        TheAlert,
        TheConfirm,
    },
    props: ['albumid', 'comid', 'event', 'comevent'],
    data () {
        return {
            token: '',
            info: '',
            pictures: [],
            name: '',
            community_event: this.comevent,
            com_event_list: [],
            current_new_image: [],
            new_image: null,
            error: '',
            error_picture: '',
            emptyAlbum: false,
            create: false,
            formError: [],
            errorTexted: [],
            language: '',
            modalCloseType: '',
            modalLoading: false,
            modalSuccess: false,
            modalUrl: '',
            modalText: ''
        }
    },
    methods: {
        cancelNewPicture(image){
            console.log(image);
        },
        errorText(){
            var error;
            for(error in this.formError){
                console.log(error);
                var element = document.getElementById('error');
                element.classList.toggle('error-box');
            }
            this.errorTexted = this.formError;
        },
        cancelError(){
            var error;
            for(error in this.errorTexted){
                console.log(error);
                var element = document.getElementById('error');
                element.classList.toggle('error-box');
            }
            this.formError = [];
        },
        deleteAlbum(id){
            axios.delete($http + 'api/asset/album/' + id + '/', {
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                axios.get($http + 'api/community/community/' + this.comid, {
                    headers: {
                        Authorization: this.token
                    }
                }).then((response) => {
                    var url;
                    if(response.data.meta.community_type === 'club'){
                        url = '/community/club/' + response.data.id + '/album'
                    } else if (response.data.meta.community_type === 'lab'){
                        url = '/community/lab/' + response.data.url_id + '/album'
                    } else if (response.data.meta.community_type === 'community_event'){
                        url = '/community/communityevent/' + response.data.id + '/album'
                    } else {
                        url = '/community/event/' + response.data.id + '/album'
                    }
                    this.modalCloseType = 'redirect';
                    this.modalLoading = false;
                    this.modalSuccess = true;
                    if (this.language === 'th') {
                        this.modalText = 'ลบอัลบั้มเรียบร้อย';
                    } else {
                        this.modalText = 'Album deleted.'
                    }

                    this.modalUrl = url;
                    window.$('#alert').modal('show');
                }).catch(() => {
                    this.modalCloseType = 'redirect';
                    this.modalLoading = false;
                    this.modalSuccess = true;
                    if (this.language === 'th') {
                        this.modalText = 'เกิดข้อผิดพลาดระหว่างพาไปยังหน้าอื่น'
                    } else {
                        this.modalText = 'Failed to redirect.'
                    }
                    this.modalUrl = '/clubs/';
                    window.$('#alert').modal('show');
                });
            }).catch(() => {
                this.modalCloseType = '';
                this.modalLoading = false;
                this.modalSuccess = true;
                if (this.language === 'th') {
                    this.modalText = 'เกิดข้อผิดพลาดระหว่างลบอัลบั้ม'
                } else {
                    this.modalText = 'Failed to delete album.'
                }
                window.$('#alert').modal('show');
            });
        },
        createAlbum(){
            this.cancelError();
            this.modalCloseType = '';
            this.modalLoading = true;
            this.modalSuccess = false;
            this.modalText = 'Loading...';
            window.$('#alert').modal('show');
            if(this.event === 'yes'){
                this.community_event = '';
            }
            var data = {
                name: this.name,
                community: this.comid,
                community_event: this.community_event
            }
            axios.post($http + 'api/asset/album/', data, {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                var i = 0;
                if(this.new_image === null){
                    this.modalCloseType = 'close';
                    this.modalLoading = false;
                    this.modalSuccess = true;
                    this.modalText = 'Album created.';
                } else {
                    for(i in this.new_image){
                        if(i < this.new_image.length){
                            const picdata = new FormData();
                            picdata.append('image', this.new_image[i]);
                            picdata.append('album', response.data.id);
                            axios.post($http + 'api/asset/album/image/', picdata, {
                                headers: {
                                    Authorization: this.token
                                }
                            }).then(() => {
                                console.log('picture uploaded')
                            }).catch((err) => {
                                console.log(err)
                                i = i+1;
                                this.modalCloseType = '';
                                this.modalLoading = false;
                                this.modalSuccess = false;
                                if (this.language === 'th') {
                                    this.modalText = 'เกิดข้อผิดพลาดระหว่างอัพโหลดรูปที่ ' + i;
                                } else {
                                    this.modalText = 'Fail to upload picture number ' + i;
                                }
                            })
                        } else {
                            this.modalCloseType = 'close';
                            this.modalLoading = false;
                            this.modalSuccess = true;
                            if (this.language === 'th') {
                                this.modalText = 'สร้างอัลบั้มเรียบร้อย'
                            } else {
                                this.modalText = 'Album created.';
                            }
                            break;
                        }
                    }
                }
            }).catch((err) => {
                console.log(err.response)
                this.modalCloseType = '';
                this.modalLoading = false;
                this.modalSuccess = false;
                if (this.language === 'th') {
                    this.modalText = 'เกิดข้อผิดพลาดระหว่างสร้างอัลบั้ม';
                } else {
                    this.modalText = 'Failed to create album.';
                }
                this.formError = err.response.data;
                this.errorText();
            })
        },
        deletePicture(id){
            axios.delete($http + 'api/asset/album/image/' + id,{
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                var save = this.pictures.find((pic) => pic.id === id);
                var pos = this.pictures.indexOf(save);
                this.pictures.splice(pos, 1);
            }).catch(() => {
                this.modalCloseType = '';
                this.modalLoading = false;
                this.modalSuccess = false;
                if (this.language === 'th') {
                    this.modalText = 'เกิดข้อผิดพลาดระหว่างลบรูป';
                } else {
                    this.modalText = 'Failed to detele photo.';
                }
                window.$('#alert').modal('show');
            })
        },
        uploadPicture(id){
            this.modalCloseType = '';
            this.modalLoading = true;
            this.modalSuccess = false;
            this.modalText = 'Loading...';
            window.$('#alert').modal('show');
            var i;
            for(i in this.new_image){
                if(i < this.new_image.length){
                    const data = new FormData();
                    data.append('image', this.new_image[i]);
                    data.append('album', id);
                    axios.post($http + 'api/asset/album/image/', data, {
                        headers: {
                            Authorization: this.token
                        }
                    }).catch((err) => {
                        this.modalCloseType = '';
                        this.modalLoading = false;
                        this.modalSuccess = false;
                        this.modalText = err.response.data;
                    })
                } else {
                    this.modalCloseType = '';
                    this.modalLoading = false;
                    this.modalSuccess = true;
                    if (this.language === 'th') {
                        this.modalText = 'อัพโหลดเรียบร้อยแล้ว';
                    } else {
                        this.modalText = 'Upload complete.';
                    }
                    this.getdata();
                    break;
                }
            }
        },
        saveDetail(id){
            this.modalCloseType = '';
            this.modalLoading = true;
            this.modalSuccess = false;
            this.modalText = 'Loading...';
            window.$('#alert').modal('show');
            this.cancelError();
            axios.patch($http + 'api/asset/album/' + id + '/', {
                name: this.name,
                community_event:  this.community_event
            },{
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                this.modalCloseType = '';
                this.modalLoading = false;
                this.modalSuccess = true;
                if (this.language === 'th') {
                    this.modalText = "แก้ไขข้อมูลอัลบั้มเรียบร้อย";
                } else {
                    this.modalText = "Album's detail edited.";
                }
            }).catch((err) => {
                this.modalCloseType = '';
                this.modalLoading = false;
                this.modalSuccess = false;
                if (this.language === 'th') {
                    this.modalText = "เกิดข้อผิดพลาดระหว่างแก้ไขข้อมูลอัลบั้ม";
                } else {
                    this.modalText = "Failed to edit album's detail.";
                }
                this.formError = err.response.data;
                this.errorText();
            })
        },
        newPic(event){
            var i;
            this.current_new_image = [];
            this.new_image = event.target.files
            console.log('test');
            if(this.albumid !== 'create'){
                this.uploadPicture(this.albumid);
            }
            for(i in event.target.files){
                var reader = new FileReader();
                reader.onload = (e) => {
                    this.current_new_image.push(e.target.result);
                }
                reader.readAsDataURL(this.new_image[i])
            }
        },
        getdata(){
            this.pictures = [];
            this.language = window.$cookies.get('itkmitlcommunitylanguage');
            this.token = window.$cookies.get('token');
            if(this.token === null){
                this.token = window.$cookies.get("itkmitlcommunitytokenbrowser");
            }
            axios.get($http + 'api/community/event/community?created_under=' + this.comid, {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                if(response.data.length !== 0){
                    this.com_event_list = response.data;
                } else {
                    this.com_event_list = false;
                    this.community_event = -1;
                }
            }).catch((err) => {
                this.error = err.response.data
            })
            if(this.albumid !== 'create'){
                axios.get($http + 'api/asset/album/' + this.albumid, {
                    headers: {
                        Authorization: this.token
                    }
                }).then((response) => {
                    this.info = response.data;
                    this.checkEdit();
                    this.name = this.info.name;
                    this.community_event = this.info.community_event;
                    axios.get($http + 'api/asset/album/image?album=' + this.albumid, {
                        headers: {
                            Authorization: this.token
                        }
                    }).then((response) => {
                        if(response.data.length !== 0){
                            var i;
                            for(i in response.data){
                                this.pictures.push(response.data[i]);
                            }
                        } else {
                            this.emptyAlbum = true;
                        }
                    }).catch((err) => {
                        this.error_picture = err.response.data
                    })
                }).catch((err) => {
                    this.error = err.response.data
                })
            } else {
                this.create = true;
                this.info = 'create';
                this.emptyAlbum = true;
            }
        },
        checkEdit(){
            if(this.info.meta !== undefined){
                if(!this.info.meta.is_able_to_edit){
                    this.$router.back();
                }
            }
        },
    },
    async mounted(){
        this.getdata();
        if(this.event === 'yes'){
            this.community_event = '';
        }
        if(this.comevent === 'none' || this.community_event === 'none'){
            this.community_event = '';
        }
    },
}
</script>
<style scoped src="../../assets/form.css"></style>
<style scoped src="../../assets/main.css"></style>
<style scoped>
