        height: 34vw;
        width: 34vw;
        overflow: hidden;
        margin: 0;
    }
    .preview{
        margin: 0;
        width: 34vw;
        height: 34vw;
        overflow: hidden;
    }
    .box{
        border-bottom: solid;
        border-left: solid;
        border-width: 1px;
        border-color: silver;
        position: absolute;
        height: 34vw;
        width: 34vw;
    }
}
