@media only screen and (min-width: 768px) and (max-width: 992px){
    .album-border{
        position: relative;
        width: 18vw;
        padding: 0;
    }
    .album{
        height: 18vw;
        width: 18vw;
        overflow: hidden;
        margin: 0;
    }
    .preview{
        margin: 0;
        width: 18vw;
        height: 18vw;
        overflow: hidden;
    }
    .box{
        border-bottom: solid;
        border-left: solid;
        border-width: 1px;
        border-color: silver;
        position: absolute;
        height: 18vw;
        width: 18vw;
    }
}
