@media only screen and (min-width: 993px) and (max-width: 1199px){
    .album-border{
        position: relative;
        width: 12vw;
        padding: 0;
    }
    .album{
        height: 12vw;
        overflow: hidden;
    }
    .preview{
        margin: 0;
        width: 12vw;
        height: 12vw;
        overflow: hidden;
    }
    .box{
        border-bottom: solid;
        border-left: solid;
        border-width: 1px;
        border-color: silver;
        position: absolute;
        height: 12vw;
        width: 12vw;
    }
}
/* PC very large screen */
@media only screen and (min-width: 1200px){
    .album-border{
        position: relative;
        width: 10vw;
        padding: 0;
    }
    .album{
        height: 10vw;
        width: 10vw;
        overflow: hidden;
        margin: 0;
    }
    .preview{
        margin: 0;
        width: 10vw;
        height: 10vw;
        overflow: hidden;
    }
    .box{
        border-bottom: solid;
        border-left: solid;
        border-width: 1px;
        border-color: silver;
        position: absolute;
        height: 10vw;
        width: 10vw;
    }
}
a{
    text-decoration: none;
    color: black;
}
a:hover{
    color: black;
}
img{
    object-fit: cover;
    width: 100%;
    height: 100%;
}
.album-name{
    font-weight: bold;
    margin-left: -7.5px;
}
