<template>
    <div class="row">
        <div class="col-lg">
            <img src="../../assets/back.svg" @click="this.$router.back()" class="mt-4 ml-3 d-none d-lg-block back-button">
        </div>
        <div v-if="!error" class="col-lg not-bg mt-4 form-padding">

            <h2 class="mb-3">
                <p class="language" v-if="language === 'th'">การตั้งค่ารับอีเมล</p>
                <p class="language" v-else>Email Preferences</p>
            </h2>
            
            <div class="d-flex">
                <div class="d-flex justify-content-start mb-4">
                    <div>
                        <b class="form-check-label" for="announcement">
                            <p class="language" v-if="language === 'th'">ประกาศ</p>
                            <p class="language" v-else>Announcements</p>
                        </b>
                        <div class="description">
                            <p class="language" v-if="language === 'th'">มีประกาศใหม่ในชุมนุม กิจกรรม หรือ ห้องปฏิบัติการของคุณ</p>
                            <p class="language" v-else>New announcements posted in your communities.</p>
                        </div>
                    </div>
                    <p class="error" v-if="errorMessage.receive_announcement">{{ errorMessage.receive_announcement[0] }}</p>
                </div>
                <div class="flex-fill"></div>
                <div class="d-flex justify-content-end text-right">
                    <input
                    class="form-check-input"
                    type="checkbox"
                    value=""
                    id="announcement"
                    v-model="announcement"
                    />
                </div>
            </div>

            <div class="d-flex">
                <div class="d-flex justify-content-start mb-4">
                    <div>
                        <b class="form-check-label" for="event">
                            <p class="language" v-if="language === 'th'">กิจกรรม</p>
                            <p class="language" v-else>Events</p>
                        </b>
                        <div class="description">
                            <p class="language" v-if="language === 'th'">กิจกรรมใหม่ที่เพิ่งได้รับการอนุมัติ</p>
                            <p class="language" v-else>Newly-approved upcoming events.</p>
                        </div>
                    </div>
                    <p class="error" v-if="errorMessage.receive_event">{{ errorMessage.receive_event[0] }}</p>
                </div>
                <div class="flex-fill"></div>
                <div class="d-flex justify-content-end text-right">
                    <input
                    class="form-check-input"
                    type="checkbox"
                    value=""
                    id="event"
                    v-model="event"
                    />
                </div>
            </div>

            <div class="d-flex">
                <div class="d-flex justify-content-start mb-4">
                    <div>
                        <b class="form-check-label" for="communityEvent">
                            <p class="language" v-if="language === 'th'">กิจกรรมชุมนุม / กิจกรรมห้องปฏิบัติการ</p>
                            <p class="language" v-else>Community Events</p>
                        </b>
                        <div class="description">
                            <p class="language" v-if="language === 'th'">กิจกรรมใหม่จากชุมนุมหรือห้องปฏิบัติการของคุณ</p>
                            <p class="language" v-else>New upcoming events from your communities.</p>
                        </div>
                    </div>
                    <p class="error" v-if="errorMessage.receive_community_event">{{ errorMessage.receive_community_event[0] }}</p>
                </div>
                <div class="flex-fill"></div>
                <div class="d-flex justify-content-end text-right">
                    <input
                    class="form-check-input"
                    type="checkbox"
                    value=""
                    id="communityEvent"
                    v-model="communityEvent"
                    />
                </div>
            </div>

            <div class="d-flex">
                <div class="d-flex justify-content-start mb-4">
                    <div>
                        <b class="form-check-label" for="request">
                            <p class="language" v-if="language === 'th'">คำขอเข้าร่วม</p>
                            <p class="language" v-else>Join Requests</p>
                        </b>
                        <div class="description">
                            <p class="language" v-if="language === 'th'">แจ้งเตือนเวลาที่มีคนขอเข้าร่วมชุมนุม กิจกรรม หรือ ห้องปฏิบัติการที่คุณจัดการ</p>
                            <p class="language" v-else>Notifications when someone wants to join the communities you manage.</p>
                        </div>
                    </div>
                    <p class="error" v-if="errorMessage.receive_request">{{ errorMessage.receive_request[0] }}</p>
                </div>
                <div class="flex-fill"></div>
                <div class="d-flex justify-content-end text-right">
                    <input
                    class="form-check-input"
                    type="checkbox"
                    value=""
                    id="request"
                    v-model="request"
                    />
                </div>
            </div>

            <div class="d-flex">
                <div class="d-flex justify-content-start mb-4">
                    <div>
                        <b class="form-check-label" for="invitation">
                            <p class="language" v-if="language === 'th'">คำเชิญชวน</p>
                            <p class="language" v-else>Invitations</p>
                        </b>
                        <div class="description">
                            <p class="language" v-if="language === 'th'">แจ้งเตือนเวลาที่มีคนส่งคำเชิญชวนให้กับคุณ</p>
                            <p class="language" v-else>Notifications when someone invites you to join their communities.</p>
                        </div>
                    </div>
                    <p class="error" v-if="errorMessage.receive_invitation">{{ errorMessage.receive_invitation[0] }}</p>
                </div>
                <div class="flex-fill"></div>
                <div class="d-flex justify-content-end text-right">
                    <input
                    class="form-check-input"
                    type="checkbox"
                    value=""
                    id="invitation"
                    v-model="invitation"
                    />
                </div>
            </div>
            
            <button v-if="!checkChange" @click="sendData" type="submit" class="btn btn-primary btn-block">{{ saveButtonText }}</button>
            <button v-else @click="sendData" type="submit" class="btn btn-outline-secondary btn-block" disabled>{{ saveButtonText }}</button>
        </div>
        <div v-else class="col-lg mt-4">
            <h2>Data fetching error, please try again.</h2>
        </div>
        <div class="col-lg"></div>
        <!-- Modal -->
        <the-alert :id="'alert'" :closetype="modalCloseType" :success="modalSuccess" :loading="modalLoading" :text="modalText">
            <template v-slot:header></template>
        </the-alert>
    </div>
</template>
<script>
import axios from 'axios';
import { $http } from '../../assets/variables.js';
import TheAlert from '../modal/TheAlert.vue';

export default {
    data () {
        return {
            request: false,
            announcement: false,
            communityEvent: false,
            event: false,
            invitation: false,
            checkrequest: false,
            checkannouncement: false,
            checkcommunityEvent: false,
            checkevent: false,
            checkinvitation: false,
            saveButtonText: '',
            errorMessage: [],
            token: '',
            language: '',
            error: false,
            me: '',
            modalClosetype: '',
            modalSuccess: false,
            modalLoading: false,
            modalText: '',
        }
    },
    components: {
        TheAlert,
    },
    methods: {
        getData(){
            this.token = window.$cookies.get('token');
            if(this.token === null){
                this.token = window.$cookies.get("itkmitlcommunitytokenbrowser");
            }
            this.language = window.$cookies.get('itkmitlcommunitylanguage');
            axios.get($http + 'api/user/email-preference/me/', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.request = response.data.receive_request;
                this.announcement = response.data.receive_announcement;
                this.communityEvent = response.data.receive_community_event;
                this.event = response.data.receive_event;
                this.invitation = response.data.receive_invitation;
                this.checkrequest = this.request;
                this.checkannouncement = this.announcement;
                this.checkcommunityEvent = this.communityEvent;
                this.checkevent = this.event;
                this.checkinvitation = this.invitation;
                this.me = response.data.id;
            }).catch(() => {
                this.error = true;
            });
        },
        sendData(){
            this.modalSuccess = false;
            this.modalClosetype = '';
            this.modalText = 'Loading...';
            this.modalLoading = true;
            window.$('#alert').modal('show');
            this.errorMessage = [];
            const data = new FormData();
            data.append('receive_request', this.request);
            data.append('receive_announcement', this.announcement);
            data.append('receive_community_event', this.communityEvent);
            data.append('receive_event', this.event);
            data.append('receive_invitation', this.invitation);
            axios.patch($http + 'api/user/email-preference/' + this.me + '/', data, {
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                this.modalSuccess = true;
                this.modalClosetype = '';
                if(this.language === 'th'){
                    this.modalText = 'บรรทึกการตั้งค่ารับอีเมลเรียบร้อย';
                } else {
                    this.modalText = 'Email preferences saved.';
                }
                this.modalLoading = false;
                this.checkrequest = this.request;
                this.checkannouncement = this.announcement;
                this.checkcommunityEvent = this.communityEvent;
                this.checkevent = this.event;
                this.checkinvitation = this.invitation;
            }).catch((err) => {
                this.errorMessage = err.response.data;
                this.modalSuccess = false;
                this.modalClosetype = '';
                if(this.language === 'th'){
                    this.modalText = 'เกิดข้อผิดพลาดในการบรรทึกการตั้งค่ารับอีเมล กรุณาลองใหม่ภายหลัง';
                } else {
                    this.modalText = 'Failed to save email preferences.\nPlease try again later.';
                }
                this.modalLoading = false;
                console.log(err);
            })
        },
    },
    async mounted(){
        this.getData();
        if(this.language === 'th'){
            this.saveButtonText = 'บรรทึกการเปลี่ยนแปลง';
        } else {
            this.saveButtonText = 'Save Changes';
        }
    },
    computed: {
        checkChange(){
            if(this.checkrequest === this.request && this.checkannouncement === this.announcement && this.checkcommunityEvent === this.communityEvent && this.checkinvitation === this.invitation && this.checkevent === this.event){
                return true;
            } else {
                return false;
            }
        }
    },
}
</script>
<style scoped src="../../assets/main.css"></style>
<style scoped src="../../assets/form.css"></style>
<style scoped>
.description{
    color: gray;
}
</style>