        var invita = this.invitation[0];
        var com;
        var count = 0;
        for(com in invita){
          if(invita[com].status === 'W'){
            count += 1;
          }
        }
        this.inviCount = count;
        for(com in invita){
          axios.get($http + 'api/community/community/' + invita[com].community, {
            headers: {
              Authorization: this.checktoken
            }
          }).then((response) => {
            this.community.push(response.data)
          });
        }
        this.inviLoad = false
      });
    },
    checkStudentCommittee() {
      if (this.myinfos.data !== undefined) {
        if (this.myinfos.data.meta.is_student_committee){
          return true;
        } else {
          return false;
        }
      } else {
        return false;
      }
    },
    getData() {
      this.checktoken = window.$cookies.get('token');
      if(this.checktoken === null){
        this.checktoken = window.$cookies.get("itkmitlcommunitytokenbrowser");
      }
      this.language = window.$cookies.get('itkmitlcommunitylanguage');
      if (this.checktoken !== null){
        try {
          axios.get($http + 'api/user/user/me', {
            headers: {
              Authorization: this.checktoken
            }
          }).then((response) => {
            this.myinfos = response,
            this.username = this.myinfos.data.username
            this.getInvitation(response.data.id)
            this.getNotificationData()
          }
          );
        } catch (err) {
          (console.log(err))
          this.myinfos = '';
        }
      }
    },
    selectLanguage(language){
      if(language === 'th'){
        window.$cookies.set("itkmitlcommunitylanguage", language, -1);
        this.$router.go();
      } else {
        window.$cookies.remove("itkmitlcommunitylanguage");
        this.$router.go();
      }
    },
    searchData() {
      if(this.searchText !== ''){
        this.$router.push('/search?text=' + this.searchText);
      } else {
        this.$router.push('/clubs/');
      }
    },
    navbarCollapse() {
      window.$('#navbarNavDropdown').collapse('toggle');
    },
  },
  computed: {
    profileIdLink() {
      if (this.myinfos.data !== undefined) {
        return '/profile/' + this.myinfos.data.id;
      } else {
        return '/';
      }
    },
  }
}
</script>
