                            <router-link class="col text-center test-tab" :to="'/community/' + comtype + '/' + comurl + '/event'"><p class="tab-mobile"><i class="far fa-calendar"></i></p></router-link>
                            <router-link class="col text-center test-tab" :to="'/community/' + comtype + '/' + comurl + '/member'"><p class="tab-mobile"><i class="fas fa-user"></i></p></router-link>
                            <router-link v-if="this.info.meta.own_membership_id !== null" class="col text-center test-tab" :to="'/community/' + comtype + '/' + comurl + '/request'"><p class="tab-mobile"><i class="fas fa-exclamation-circle"></i></p></router-link>
