                <div class="d-flex justify-content-center">
                    <div class="spinner-border m-5" role="status">
                        <span class="sr-only">Loading...</span>
                    </div>
                </div>
                <div class="d-flex justify-content-center">
                    <h3>Loading...</h3>
                </div>
            </div>
            <div class="col-lg"></div>
        </div>
        <!-- Modal -->
        <the-alert :id="'communityalert'" :closetype="alertCloseType" :success="alertSuccess" :loading="alertLoading" :text="alertText">
            <template v-slot:header></template>
        </the-alert>
        <the-confirm :id="'cancelJoin'">
            <h4 class="d-flex justify-content-center">
                <p class="language" v-if="language === 'th'">ยกเลิกคำขอเข้าร่วม?</p>
                <p class="language" v-else>Cancel the join request?</p>
            </h4>
            <template v-slot:footer>
                <button data-dismiss="modal" class="btn btn-danger mr-4" style="width: 75px;" @click="cancelRequest()">
                    <p class="language" v-if="language === 'th'">ใช่</p>
                    <p class="language" v-else>Yes</p>
                </button>
                <button data-dismiss="modal" class="btn btn-outline-secondary ml-4" style="width: 75px;">
                    <p class="language" v-if="language === 'th'">ไม่</p>
                    <p class="language" v-else>No</p>
                </button>
            </template>
        </the-confirm>
        <the-confirm :id="'leaveCommunity'">
            <h4 class="d-flex justify-content-center">
                <p class="language" v-if="language === 'th'">ต้องการออกจากการเป็นสมาชิก?</p>
                <p class="language" v-else>Leave the community?</p>
            </h4>
            <template v-slot:footer>
                <button data-dismiss="modal" class="btn btn-danger mr-4" style="width: 75px;" @click="leaveClub(this.info.meta.own_membership_id)">
                    <p class="language" v-if="language === 'th'">ใช่</p>
                    <p class="language" v-else>Yes</p>
                </button>
                <button data-dismiss="modal" class="btn btn-outline-secondary ml-4" style="width: 75px;">
                    <p class="language" v-if="language === 'th'">ไม่</p>
                    <p class="language" v-else>No</p>
                </button>
            </template>
        </the-confirm>
        <the-confirm :id="'retireCommunity'">
            <h4 class="d-flex justify-content-center">
                <p class="language" v-if="language === 'th'">ต้องการถอนตัว?</p>
                <p class="language" v-else>Retire from the community?</p>
            </h4>
            <h5 class="d-flex justify-content-center">
                <p class="language" v-if="language === 'th'">(สามารถกดกลับมาเข้าร่วมทีหลังได้)</p>
                <p class="language" v-else>(You can rejoin later)</p>
            </h5>
            <template v-slot:footer>
                <button data-dismiss="modal" class="btn btn-danger mr-4" style="width: 75px;" @click="retireFromClub(this.info.meta.own_membership_id)">
                    <p class="language" v-if="language === 'th'">ใช่</p>
                    <p class="language" v-else>Yes</p>
                </button>
                <button data-dismiss="modal" class="btn btn-outline-secondary ml-4" style="width: 75px;">
                    <p class="language" v-if="language === 'th'">ไม่</p>
                    <p class="language" v-else>No</p>
                </button>
            </template>
        </the-confirm>
    </div>
</template>
<script>
import axios from 'axios';
import { $http } from '../../assets/variables.js';
import TheAlert from '../modal/TheAlert.vue';
import TheConfirm from '../modal/TheConfirm.vue';

export default {
    props: ['comtype','comurl'],
    data () {
        return {
            token: '',
            language: '',
            info: '',
            message: '',
            pending: '',
            myinfo: '',
            clubtype: '',
            eventtype: '',
            eventseries: '',
            invite: '',
            date: '',
            advisor: [],
            nologo: '',
            nobanner: '',
            alertText: '',
            alertSuccess: false,
            alertLoading: false,
            alertCloseType: '',
        };
    },
    components: {
        TheAlert,
        TheConfirm,
    },
    methods: {
        checkStatus(){
            this.date = new Date().toJSON().slice(0,10);
            if(this.info.is_cancelled){
                return 'cancel';
            } else if (!this.info.is_approved){
                return 'unapprove';
            } else if (this.date > this.info.end_date){
                return 'end';
            } else {
                return 'recruiting';
            }
        },
        getAdvisor(id){
            this.advisor = [];
            axios.get($http + 'api/membership/advisory?community=' + id, {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                var date = new Date().toJSON().slice(0,10);
                var i;
                for(i in response.data){
                    if(response.data[i].end_date >= date){
                        this.advisor.push(response.data[i].meta.advisor_name);
                    }
                }
            }).catch((err) => {
                console.log(err);
            })
        },
        declineInvitation(id){
            window.$("#communityalert").modal('show');
            this.alertLoading = true;
            this.alertText = "Loading...";
            this.alertSuccess = false;
            this.alertCloseType = '';
            axios.patch($http + 'api/membership/invitation/' + id + '/', {
                status: "D"
            }, {
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                this.alertLoading = false;
                if(this.language === 'th'){
                    this.alertText = "ปฏิเสธคำเชิญเรียบร้อยแล้ว";
                } else {
                    this.alertText = "Invitation declined.";
                }
                this.alertSuccess = true;
                this.alertCloseType = 'refresh';
            }).catch(() => {
                this.alertLoading = false;
                if(this.language === 'th'){
                    this.alertText = "เกิดข้อผิดพลาดระหว่างปฏิเสธคำเชิญ";
                } else {
                    this.alertText = "Failed to decline invitation.";
                }

                this.alertSuccess = false;
                this.alertCloseType = '';
            });
        },
        acceptInvitation(id){
            window.$("#communityalert").modal('show');
            this.alertLoading = true;
            this.alertText = "Loading...";
            this.alertSuccess = false;
            this.alertCloseType = '';
            axios.patch($http + 'api/membership/invitation/' + id + '/', {
                status: "A"
            }, {
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                this.alertLoading = false;
                if(this.language === 'th'){
                    this.alertText = "ยอมรับคำเชิญเรียบร้อยแล้ว";
                } else {
                    this.alertText = "Invitation accepted.";
                }
                this.alertSuccess = true;
                this.alertCloseType = 'refresh';
            }).catch(() => {
                this.alertLoading = false;
                if(this.language === 'th'){
                    this.alertText = "เกิดข้อผิดพลาดระหว่างยอมรับคำเชิญ"
                } else {
                    this.alertText = "Failed to accept invitation.";
                }
                this.alertSuccess = false;
                this.alertCloseType = '';
            });
        },
        getInvitation(id){
            axios.get($http + 'api/user/user/me', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.myinfo = response.data
                axios.get($http + 'api/membership/invitation?invitee='+ this.myinfo.id +'&community=' + id +'&status=W',{
                    headers: {
                        Authorization: this.token
                    }
                }).then((response) => {
                    this.invite = response.data[0];
                })
            })
        },
        getPending(id){
            axios.get($http + 'api/user/user/me', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.myinfo = response.data
                axios.get($http + 'api/membership/request?user=' + this.myinfo.id + '&community=' + id + '&status=W', {
                    headers: {
                        Authorization: this.token
                    }
                })
                .then((response) => {
                    this.pending = response.data
                });
            })
            
        },
        getClubTypeName(id){
            axios.get($http + 'api/category/club-type/' + id)
            .then((response) => {
                if(this.language === 'th') {
                    this.clubtype = response.data.title_th;
                } else {
                    this.clubtype = response.data.title_en; 
                }
            }).catch(() => {
                if(this.language === 'th'){
                    this.clubtype = "ประเภทชุมนุมที่ไม่ถูกบรรทึก"
                } else {
                    this.clubtype = "Unknown club type"
                }
            })
        },
        getEventSeriesName(id){
            axios.get($http + 'api/category/event-series/' + id)
            .then((response) => {
                if(this.language === 'th') {
                    this.eventseries = response.data.title_th;
                } else {
                    this.eventseries = response.data.title_en;
                }
            }).catch(() => {
                this.eventseries = "-"
            })
        },
        getEventTypeName(id){
            axios.get($http + 'api/category/event-type/' + id)
            .then((response) => {
                if(this.language === 'th') {
                    this.eventtype = response.data.title_th;
                } else {
                    this.eventtype = response.data.title_en;
                }
            }).catch(() => {
                this.eventtype = "-"
            })
        },
        cancelApproval(id){
            this.alertLoading = true;
            this.alertText = "Loading...";
            this.alertSuccess = false;
            this.alertCloseType = '';
            window.$("#communityalert").modal('show');
            axios.get($http + 'api/membership/approval-request?community=' + id, {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                axios.delete($http + 'api/membership/approval-request/' + response.data[0].id + '/', {
                    headers: {
                        Authorization: this.token
                    }
                }).then(() => {
                    this.alertLoading = false;
                    if(this.language === 'th'){
                        this.alertText = "ยกเลิกคำขออนุมัติเรียบร้อยแล้ว"
                    } else {
                        this.alertText = "Approval request is cancelled successfully.";
                    }
                    this.alertSuccess = true;
                    this.alertCloseType = 'refresh';
                }).catch(() => {
                    this.alertLoading = false;
                    if(this.language === 'th'){
                        this.alertText = "เกิดข้อผิดพลาดระหว่างยกเลิกคำขออนุมัติ กรุณาลองใหม่"
                    } else {
                        this.alertText = "Error cancelling approval request, please try again.";
                    }
                    this.alertSuccess = false;
                    this.alertCloseType = '';
                })
            }).catch(() => {
                this.alertLoading = false;
                if(this.language === 'th'){
                    this.alertText = "เกิดข้อผิดพลาดระหว่างยกเลิกคำขออนุมัติ"
                } else {
                    this.alertText = "Error cancelling approval request data, please try again.";
                }
                this.alertSuccess = false;
                this.alertCloseType = '';
            })
        },
        goSendApproval(id){
            this.$router.push('/approve/' + id);
        },
        goClubDoc(id){
            this.$router.push('/clubdocument/' + id);
        },
        getdata() {
            this.language = window.$cookies.get('itkmitlcommunitylanguage');
            this.token = window.$cookies.get('token');
            if(this.token === null){
                this.token = window.$cookies.get("itkmitlcommunitytokenbrowser");
            }
            if(this.comtype === 'club'){
                axios.get($http + 'api/community/club/?url_id=' + this.comurl, {
                    headers: {
                        Authorization: this.token
                    }
                })
                .then((response) => {
                this.info = response.data;
                this.getClubTypeName(response.data.club_type);
                this.getPending(response.data.id);
                this.getAdvisor(response.data.id);
                if(response.data.meta.request_ability.code === 'pending_invitation'){
                    this.getInvitation(response.data.id);
                }
                if(this.info === undefined){
                    if(this.language === 'th'){
                        this.message = 'เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่';
                    } else {
                        this.message = 'Data fetching error, please try again.';
                    }
                }
                }).catch((err) => {
                    if(err.response.status === 404){
                        axios.get($http + 'api/community/club/' + this.comurl, {
                            headers: {
                                Authorization: this.token
                            }
                        })
                        .then((response) => {
                        this.info = response.data;
                        this.getClubTypeName(response.data.club_type);
                        this.getPending(response.data.id);
                        this.getAdvisor(response.data.id);
                        if(response.data.meta.request_ability.code === 'pending_invitation'){
                            this.getInvitation(response.data.id);
                        }
                        if(this.info === undefined){
                            if(this.language === 'th'){
                                this.message = 'เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่';
                            } else {
                                this.message = 'Data fetching error, please try again.';
                            }
                        }
                        }).catch((err) => {
                            console.log(err.response)
                            this.info = '';
                            if(this.language === 'th'){
                                this.message = 'เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่';
                            } else {
                                this.message = 'Data fetching error, please try again.';
                            }
                        });
                    } else {
                        (console.log(err.response))
                        this.info = '';
                        if(this.language === 'th'){
                            this.message = 'เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่';
                        } else {
                            this.message = 'Data fetching error, please try again.';
                        }
                    }
                });
            } else if(this.comtype === 'event'){
                axios.get($http + 'api/community/event/?url_id=' + this.comurl, {
                    headers: {
                        Authorization: this.token
                    }
                })
                .then((response) => {
                    this.info = response.data;
                    this.getEventTypeName(response.data.event_type);
                    this.getEventSeriesName(response.data.event_series);
                    this.getPending(response.data.id);
                    this.getAdvisor(response.data.id);
                    this.checkStatus();
                    if(response.data.meta.request_ability.code === 'pending_invitation'){
                        this.getInvitation(response.data.id);
                    }
                    if(this.info === undefined){
                        if(this.language === 'th'){
                            this.message = 'เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่';
                        } else {
                            this.message = 'Data fetching error, please try again.';
                        }
                    }
                }).catch((err) => {
                    if(err.response.status === 404){
                        axios.get($http + 'api/community/event/' + this.comurl, {
                            headers: {
                                Authorization: this.token
                            }
                        })
                        .then((response) => {
                            this.info = response.data;
                            this.getEventTypeName(response.data.event_type);
                            this.getEventSeriesName(response.data.event_series);
                            this.getPending(response.data.id);
                            this.getAdvisor(response.data.id);
                            this.checkStatus();
                            if(response.data.meta.request_ability.code === 'pending_invitation'){
                                this.getInvitation(response.data.id);
                            }
                            if(this.info === undefined){
                                if(this.language === 'th'){
                                    this.message = 'เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่';
                                } else {
                                    this.message = 'Data fetching error, please try again.';
                                }
                            }
                        }).catch((err) => {
                            (console.log(err))
                            this.info = '';
                            if(this.language === 'th'){
                                this.message = 'เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่';
                            } else {
                                this.message = 'Data fetching error, please try again.';
                            }
                        });
                    }
                });
            } else if(this.comtype === 'communityevent'){
                axios.get($http + 'api/community/event/community/?url_id=' + this.comurl, {
                    headers: {
                        Authorization: this.token
                    }
                })
                .then((response) => {
                    this.info = response.data;
                    this.getEventTypeName(response.data.event_type);
                    this.getEventSeriesName(response.data.event_series);
                    this.getPending(response.data.id);
                    this.getAdvisor(response.data.id);
                    this.checkStatus();
                    if(response.data.meta.request_ability.code === 'pending_invitation'){
                        this.getInvitation(response.data.id);
                    }
                    if(this.info === undefined){
                        if(this.language === 'th'){
                            this.message = 'เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่';
                        } else {
                            this.message = 'Data fetching error, please try again.';
                        }
                    }
                }).catch((err) => {
                    if(err.response.status === 404){
                        axios.get($http + 'api/community/event/community/' + this.comurl, {
                            headers: {
                                Authorization: this.token
                            }
                        })
                        .then((response) => {
                            this.info = response.data;
                            this.getEventTypeName(response.data.event_type);
                            this.getEventSeriesName(response.data.event_series);
                            this.getPending(response.data.id);
                            this.getAdvisor(response.data.id);
                            this.checkStatus();
                            if(response.data.meta.request_ability.code === 'pending_invitation'){
                                this.getInvitation(response.data.id);
                            }
                            if(this.info === undefined){
                                if(this.language === 'th'){
                                    this.message = 'เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่';
                                } else {
                                    this.message = 'Data fetching error, please try again.';
                                }
                            }
                        }).catch((err) => {
                            (console.log(err))
                            this.info = '';
                            if(this.language === 'th'){
                                this.message = 'เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่';
                            } else {
                                this.message = 'Data fetching error, please try again.';
                            }
                        });
                    }
                });
            } else if(this.comtype === 'lab'){
                axios.get($http + 'api/community/lab/?url_id=' + this.comurl, {
                    headers: {
                        Authorization: this.token
                    }
                })
                .then((response) => {
                    this.info = response.data;
                    this.getPending(response.data.id);
                        if(response.data.meta.request_ability.code === 'pending_invitation'){
                            this.getInvitation(response.data.id);
                        }
                    if(this.info === undefined){
                        if(this.language === 'th'){
                            this.message = 'เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่';
                        } else {
                            this.message = 'Data fetching error, please try again.';
                        }
                    }
                }).catch((err) => {
                    (console.log(err))
                    this.info = '';
                    if(this.language === 'th'){
                        this.message = 'เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่';
                    } else {
                        this.message = 'Data fetching error, please try again.';
                    }
                });
            } else {
                this.$router.push('/wrongurl/');
            }
        },
        goEditForm(id) {
            if(this.comtype === 'club'){
                this.$router.push('/clubform/' + id);
            } else if (this.comtype === 'event'){
                this.$router.push('/eventform/' + id);
            } else if (this.comtype === 'communityevent'){
                this.$router.push('/comeventform/' + id);
            } else if (this.comtype === 'lab'){
                this.$router.push('/labform/' + id);
            }
        },
        joinCommunity() {
            this.alertLoading = true;
            this.alertText = 'Loading...';
            var data = {
                community: this.info.id
            };
            axios.post($http + 'api/membership/request/', data, {
                headers: {
                    Authorization: this.token
                },
            }).then(() => {
                this.alertCloseType = 'refresh';
                this.alertLoading = false;
                this.alertSuccess = true;
                if(this.language === 'th'){
                    this.alertText = "ส่งคำขอเข้าร่วมเรียบร้อยแล้ว";
                } else {
                    this.alertText = "Successfully sent join request.";
                }
            }).catch((err) => {
                console.log(err);
                this.alertCloseType = '';
                this.alertLoading = false;
                this.alertSuccess = false;
                if(this.language === 'th'){
                    this.alertText = "เกิดข้อผิดพลาดระหว่างส่งคำขอเข้าร่วม";
                } else {
                    this.alertText = "Failed to sent join request.";
                }
            });
        },
        cancelRequest() {
            axios.delete($http + 'api/membership/request/' + this.pending[0].id + '/', {
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                this.$router.go();
            }).catch(() => {
                this.alertCloseType = '';
                this.alertLoading = false;
                this.alertSuccess = false;
                if(this.language === 'th'){
                    this.alertText = "เกิดข้อผิดพลาดระหว่างยกเลิกคำขอเข้าร่วม";
                } else {
                    this.alertText = "Failed to cancel join request.";
                }
                window.$('#communityalert').modal('show')
            });
        },
        leaveClub(id) {
            axios.get($http + 'api/membership/membership/' + id + '/', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                axios.put($http + 'api/membership/membership/' + id + '/', {
                    position: response.data.position,
                    status: "L"
                }, {
                    headers: {
                        Authorization: this.token
                    }
                }).then(() => {
                    this.$router.go()
                }).catch(() => {
                    this.alertCloseType = '';
                    this.alertLoading = false;
                    this.alertSuccess = false;
                    if(this.language === 'th'){
                        this.alertText = "เกิดข้อผิดพลาดระหว่างออกจากการเป็นสมาชิก";
                    } else {
                        this.alertText = "Failed to leave the community.";
                    }
                    window.$('#communityalert').modal('show');
                })
            }).catch(() => {
                this.alertCloseType = '';
                this.alertLoading = false;
                this.alertSuccess = false;
                if(this.language === 'th'){
                    this.alertText = "เกิดข้อผิดพลาดระหว่างออกจากการเป็นสมาชิก";
                } else {
                    this.alertText = "Failed to leave the community.";
                }
                window.$('#communityalert').modal('show');
            });
        },
        retireFromClub(id) {
            axios.get($http + 'api/membership/membership/' + id + '/', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                axios.put($http + 'api/membership/membership/' + id + '/', {
                    position: response.data.position,
                    status: "R"
                }, {
                    headers: {
                        Authorization: this.token
                    }
                }).then(() => {
                    this.$router.go()
                }).catch(() => {
                    this.alertCloseType = '';
                    this.alertLoading = false;
                    this.alertSuccess = false;
                    if(this.language === 'th'){
                        this.alertText = "เกิดข้อผิดพลาดระหว่างถอนตัว";
                    } else {
                        this.alertText = "Failed to retire from the community.";
                    }
                    window.$('#communityalert').modal('show');
                });
            }).catch(() => {
                this.alertCloseType = '';
                this.alertLoading = false;
                this.alertSuccess = false;
                if(this.language === 'th'){
                    this.alertText = "เกิดข้อผิดพลาดระหว่างถอนตัว";
                } else {
                    this.alertText = "Failed to retire from the community.";
                }
                window.$('#communityalert').modal('show');
            });
        },
        backToDuty(id) {
            this.alertCloseType = '';
            this.alertLoading = true;
            this.alertSuccess = false;
            this.alertText = "Loading...";
            window.$("#communityalert").modal('show');
            axios.get($http + 'api/membership/membership/' + id + '/', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                axios.put($http + 'api/membership/membership/' + id + '/', {
                    position: response.data.position,
                    status: "A"
                }, {
                    headers: {
                        Authorization: this.token
                    }
                }).then(() => {
                    this.alertCloseType = 'refresh';
                    this.alertLoading = false;
                    this.alertSuccess = true;
                    if (this.language === 'th') {
                        this.alertText = "ยินดีต้อนรับกลับมา";
                    } else {
                        this.alertText = "Welcome back to the community.";
                    }
                }).catch(() => {
                    this.alertCloseType = '';
                    this.alertLoading = false;
                    this.alertSuccess = false;
                    if (this.language === 'th') {
                        this.alertText = "เกิดข้อผิดพลาดระหว่างเอาคุณกลับมาเป็นสมาชิก";
                    } else {
                        this.alertText = "Failed to put you back into the community.";
                    }
                })
            }).catch(() => {
                this.alertCloseType = '';
                this.alertLoading = false;
                this.alertSuccess = false;
                if (this.language === 'th') {
                    this.alertText = "เกิดข้อผิดพลาดระหว่างเอาคุณกลับมาเป็นสมาชิก";
                } else {
                    this.alertText = "Failed to put you back into the community.";
                }
            });
        },
    },
    async mounted() {
        this.getdata();
    },
    watch: {
        $route() {
            this.getdata();
        },
    },
}
</script>
