<template>
    <div class="row background">
        <div class="col-md-3"></div>
        <div class="col-md-6 mt-4 mb-4 not-bg" style="border-radius: 5px; padding: 20px 10px 20px 10px;">
            <!-- PC -->
            <div class="d-none d-lg-block">
                <div class="row mt-2">
                    <div class="col d-flex ml-4">
                        <div class="mb-2">
                            <input v-if="language === 'th'" class="form-control" id="searchAlbum" rows="4" v-model="searchAlbum" placeholder="ค้นหา..." />
                            <input v-else class="form-control" id="searchAlbum" rows="4" v-model="searchAlbum" placeholder="Search..." />
                        </div>
                        <div class="mb-2" style="margin-top: -20px;">
                            <button @click="searchAlbumData(info.id)" class="btn btn-primary"><i class="fas fa-search"></i></button>
                        </div>
                    </div>
                    <div class="col d-flex justify-content-end" style="margin-top: -30px; margin-right: 10px;">
                        <div style="margin-top: 10px;"><button @click="createAlbum" v-if="info.meta.own_membership_position > 0" class="btn btn-warning">
                            <p class="language" v-if="language === 'th'">สร้างอัลบั้ม</p>
                            <p class="language" v-else>Create Album</p>
                        </button></div>
                    </div>
                </div>
            </div>
            <!-- Mobile -->
            <div class="d-lg-none">
                <div class="d-flex justify-content-center mt-2">
                    <div class="d-flex">
                        <div>
                            <input v-if="language === 'th'" class="form-control" id="searchAlbum" rows="4" v-model="searchAlbum" placeholder="ค้นหา..." />
                            <input v-else class="form-control" id="searchAlbum" rows="4" v-model="searchAlbum" placeholder="Search..." />
                        </div>
                        <div style="margin-top: -20px;">
                            <button @click="searchAlbumData(info.id)" class="btn btn-primary"><i class="fas fa-search"></i></button>
                        </div>
                    </div>
                </div>
                <div class="d-flex justify-content-center">
                    <div class="d-flex" style="margin-top: -20px; margin-right: 10px;">
                        <button @click="createAlbum" v-if="info.meta.own_membership_position > 0" class="btn btn-warning">
                            <p class="language" v-if="language === 'th'">สร้างอัลบั้ม</p>
                            <p class="language" v-else>Create Album</p>
                        </button>
                    </div>
                </div>
            </div>
            <!-- Album list -->
            <div v-if="error !== null"><p style="color: red;">{{ error }}</p></div>
            <div class="not-bg" v-else-if="albums === null">
                <div class="d-flex justify-content-center">
                    <div class="spinner-border m-5" role="status">
                        <span class="sr-only">Loading...</span>
                    </div>
                </div>
                <div class="d-flex justify-content-center">
                    <h3>Loading...</h3>
                </div>
            </div>
            <div v-else>
                <div v-if="albums.length === 0" class="d-flex justify-content-center">
                    {{ loadingResult }}
                </div>
                <div class="row ml-4 mr-4 d-flex justify-content-center">
                    <album-preview class="album-preview"
                    v-for="album in albums" 
                    :key='album.id'
                    :albumid = album.id
                    :comid = info.id
                    :token = token
                    :name = album.name
                    :language = language
                    ></album-preview>
                </div>
            </div>
        </div>
        <div class="col-md-3"></div>
    </div>
</template>
<script>
import axios from 'axios';
import AlbumPreview from '../../album/AlbumPreview.vue';
import { $http } from '../../../assets/variables.js';

export default {
    components: {
        AlbumPreview,
    },
    props: ['info', 'token', 'language'],
    data() {
        return {
            albums: null,
            error: null,
            searchAlbum: '',
            loadingResult: '',
        }
    },
    methods: {
        createAlbum(){
            if(this.info.meta.community_type === 'event'){
                this.$router.push('/albumform/' + this.info.id + '/create/yes/none');
            } else if(this.info.meta.community_type === 'community_event'){
                this.$router.push('/albumform/' + this.info.created_under + '/create/no/' + this.info.id);
            } else {
                this.$router.push('/albumform/' + this.info.id + '/create/no/none');
            }
        },
        searchAlbumData(id){
            this.albums = null;
            if(this.info.meta.community_type !== 'community_event'){
                axios.get($http + 'api/asset/album?community=' + id + '&search=' + this.searchAlbum, {
                    headers: {
                        Authorization: this.token
                    }
                }).then((response) => {
                    this.albums = response.data;
                }).catch((err) => {
                    this.error = err.response.data;
                })
            } else if (this.info.meta.community_type === 'community_event') {
                axios.get($http + 'api/asset/album?community_event=' + id + '&search=' + this.searchAlbum, {
                    headers: {
                        Authorization: this.token
                    }
                }).then((response) => {
                    this.albums = response.data;
                }).catch((err) => {
                    this.error = err.response.data;
                })
            }
            if(this.language === 'th') {
                this.loadingResult = "ไม่มีอัลบั้มที่ตรงกับที่คุณค้นหา"
            } else {
                this.loadingResult = "No albums match your search."
            }
        },
        getAlbumData(id){
            if(this.info.meta.community_type !== 'community_event'){
                axios.get($http + 'api/asset/album?community=' + id, {
                    headers: {
                        Authorization: this.token
                    }
                }).then((response) => {
                    this.albums = response.data;
                }).catch((err) => {
                    this.error = err.response.data;
                })
            } else if (this.info.meta.community_type === 'community_event') {
                axios.get($http + 'api/asset/album?community_event=' + id, {
                    headers: {
                        Authorization: this.token
                    }
                }).then((response) => {
                    this.albums = response.data;
                }).catch((err) => {
                    this.error = err.response.data;
                })
            }
            if(this.language === 'th') {
                this.loadingResult = 'ยังไม่มีอัลบั้มถูกสร้างขึ้น'
            } else {
                this.loadingResult = 'No albums have been made yet.'
            }
        },
    },
    async mounted() {
        this.getAlbumData(this.info.id);
    }
}
</script>
<style scoped src="../../../assets/main.css"></style>
<style scoped>
button.btn{
    margin-top: 20px;
    margin-bottom: 20px;
}
button.btn-warning{
    margin-top: 20px;
    margin-bottom: 20px;
}
</style>