<template>
    <div class="row">
        <div class="col-md"></div>
        <div class="col-md mt-4">
            <div class="row not-bg" style="padding: 10px;">
                <!-- Mobile -->
                <h2 class="d-none d-lg-block">
                    <p class="language" v-if="language === 'th'">การเชิญชวนทั้งหมด</p>
                    <p class="language" v-else>All Invitations</p>
                </h2>
                <!-- PC -->
                <h2 class="d-flex d-lg-none justify-content-center">
                    <p class="language" v-if="language === 'th'">การเชิญชวนทั้งหมด</p>
                    <p class="language" v-else>All Invitations</p>
                </h2>
                <!-- End -->
            </div>
            <div v-if="invitation[0].length !== 0" >
                <div class="mt-2 not-bg row" v-for="invite in invitation[0].slice().reverse()" :key="invite.id">
                  <div class="ml-lg-2 mt-2 d-flex justify-content-center justify-content-lg-start"
                  style="padding: 10px;">{{ getInviteCommunityName(invite.community) }}</div>
                <div v-if="invite.status === 'W'" class="d-flex justify-content-center justify-content-lg-end mr-2" style="padding-bottom: 10px;">
                  <button class="btn btn-primary" @click="acceptInvitation(invite.id)">
                      <p class="language" v-if="language === 'th'">ยอมรับ</p>
                    <p class="language" v-else>Accept</p>
                  </button>
                  <button class="btn btn-danger" @click="declineInvitation(invite.id)">
                      <p class="language" v-if="language === 'th'">ปฏิเสธ</p>
                    <p class="language" v-else>Decline</p>
                  </button>
                </div>
                <div v-if="invite.status === 'A' || invite.status === 'D'" style="padding-bottom: 10px;">
                    <div v-if="invite.status === 'A'" class="d-flex justify-content-end mr-2" style="color: #2357A5;">
                        <p class="language" v-if="language === 'th'">ยอมรับแล้ว</p>
                    <p class="language" v-else>Accepted</p>
                    </div>
                    <div v-if="invite.status === 'D'" class="d-flex justify-content-end mr-2 text-danger">
                        <p class="language" v-if="language === 'th'">ปฏิเสธแล้ว</p>
                    <p class="language" v-else>Declined</p>
                    </div>
                </div>
                </div>
            </div>
            <div v-else-if="inviLoad">
                <div class="d-flex justify-content-center">
                    <div class="spinner-border m-5" role="status">
                        <span class="sr-only">Loading...</span>
                    </div>
                </div>
                <div class="d-flex justify-content-center">
                    <h3>Loading...</h3>
                </div>
            </div>
            <div v-else class="d-flex justify-content-center">
                <h3>
                    <p class="language" v-if="language === 'th'">ไม่มีการเชิญชวน</p>
                    <p class="language" v-else>No Invitation</p>
                </h3>
            </div>
        </div>
        <div class="col-md"></div>
    </div>
</template>
<script>
import axios from 'axios';
import { $http } from '../../assets/variables.js';

export default {
    props: ['id'],
    data() {
        return {
            token: '',
            language: '',
            community: [],
            invitation: [[]],
            myinfo: '',
            inviLoad: true,
        }
    },
    methods: {
        declineInvitation(id){
            axios.patch($http + 'api/membership/invitation/' + id + '/', {
                status: "D"
            }, {
                headers: {
                    Authorization: this.token
                }
            }).then(
                this.getInvitation(this.myinfo.id)
            );
        },
        acceptInvitation(id){
            axios.patch($http + 'api/membership/invitation/' + id + '/', {
                status: "A"
            }, {
                headers: {
                    Authorization: this.token
                }
            }).then(
                this.getInvitation(this.myinfo.id)
            );
        },
        getInviteCommunityName(id) {
            var com = this.community.find((communit) => communit.id === id);
            if(com !== undefined){
                if(this.language === 'th'){
                    return 'การเชิญชวนจาก ' + com.name_th;
                } else {
                    return 'Invitation from ' + com.name_en;
                }
            }
        },
        getInvitation(id) {
            this.inviLoad = true;
            this.invitation.shift()
            axios.get($http + 'api/membership/invitation?invitee=' + id, {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.invitation.push(response.data)
                var invita = this.invitation[0];
                var com;
                for(com in invita){
                    axios.get($http + 'api/community/community/' + invita[com].community, {
                        headers: {
                        Authorization: this.token
                        }
                    }).then((response) => {
                        this.community.push(response.data)
                    })
                }
                this.inviLoad = false;
            });
        },
        getdata() {
            this.language = window.$cookies.get('itkmitlcommunitylanguage');
            this.token = window.$cookies.get('token');
            if(this.token === null){
                this.token = window.$cookies.get("itkmitlcommunitytokenbrowser");
            }
            axios.get($http + 'api/user/user/me', {
                headers:{
                    Authorization: this.token
                }
            }).then((response) => {
                this.myinfo = response.data;
                if (this.myinfo.id.toString() !== this.id) {
                    this.$router.back();
                } else {
                    this.getInvitation(this.myinfo.id);
                }
            }).catch((err) => {
                console.log(err);
                this.$router.back();
            });
        }
    },
    async mounted() {
        this.getdata();
    },
    watch:{
        $route() {
            this.getdata();
        }
    }
}
</script>
<style scoped src="../../assets/main.css"></style>
<style scoped>
h2{
    margin: 0;
}
</style>