<template>
    <div class="row">
        <div class="col-md"></div>
        <div class="col-md-8">
            <div class="row">
                <div class="col-xl mr-md-4">
                    <div class="mt-4">
                        <div class="row not-bg">
                            <div class="col-lg">
                                <!-- PC -->
                                <div class="d-none d-lg-block">
                                <h2 class="m-2 toggle d-flex" @click="assignToggle"
                                aria-expanded="false" aria-controls="assign">
                                    <p class="language" v-if="language === 'th'">อาจารย์ที่ปรึกษา</p>
                                    <p class="language" v-else>Advisories</p>&nbsp;
                                    <i v-if="assignTog" class="fas fa-caret-down"></i>
                                    <i v-else class="fas fa-caret-right"></i>
                                </h2>
                                </div>
                                <!-- Mobile -->
                                <div class="d-lg-none">
                                    <h2 class="d-flex justify-content-center mt-2 toggle" @click="assignToggle"
                                    aria-expanded="false" aria-controls="assign">
                                        <p class="language" v-if="language === 'th'">อาจารย์ที่ปรึกษา</p>
                                        <p class="language" v-else>Advisories</p>&nbsp;
                                        <i v-if="assignTog" class="fas fa-caret-down"></i>
                                        <i v-else class="fas fa-caret-right"></i>
                                    </h2>
                                </div>
                            </div>
                            <!-- PC -->
                            <div class="col-lg d-none d-lg-block">
                                <div class="m-2 d-flex justify-content-end">
                                    <button @click="getAddingData()" data-target="#adding" data-toggle="modal" class="btn btn-primary mr-2">
                                        <p class="language" v-if="language === 'th'">เพิ่มอาจารย์ที่ปรึกษา</p>
                                        <p class="language" v-else>Add Advisory</p>
                                    </button>
                                </div>
                            </div>
                            <!-- Mobile -->
                            <div class="col-lg d-flex justify-content-center d-lg-none">
                                <div class="mt-2 mb-2">
                                    <button @click="getAddingData()" data-target="#adding" data-toggle="modal" class="btn btn-primary mr-2">
                                        <p class="language" v-if="language === 'th'">เพิ่มอาจารย์ที่ปรึกษา</p>
                                        <p class="language" v-else>Add Advisory</p>
                                    </button>
                                </div>
                            </div>
                            <!-- Modal -->
                            <div
                            class="modal fade"
                            id="adding"
                            ref="adding"
                            tabindex="-1"
                            aria-labelledby="exampleModalLabel"
                            aria-hidden="true"
                            >
                                <div class="modal-dialog">
                                    <div class="modal-content">
                                        <div class="modal-header">
                                            <h5 class="modal-title" id="exampleModalLabel">
                                                <p class="language" v-if="language === 'th'">เพิ่มอาจารย์ที่ปรึกษาให้กับชุมนุมหรือกิจกรรม</p>
                                                <p class="language" v-else>Add Advisor to the Club or Event</p></h5>
                                            <button
                                            type="button"
                                            class="btn-close"
                                            data-dismiss="modal"
                                            aria-label="Close"
                                            ></button>
                                        </div>
                                        <div class="modal-body">
                                            <div class="row">
                                                <div class="form-outline mb-4">
                                                    <label class="form-label">
                                                        <p class="language" v-if="language === 'th'">อาจารย์ที่ปรึกษา</p>
                                                        <p class="language" v-else>Advisor</p>
                                                    </label>
                                                    <select class="form-control select" v-model="selectAdvisor">
                                                        <option v-if="advisoryList.length === 0" value="none">
                                                            <p class="language" v-if="language === 'th'">ข้อความนี้จะหายไปเมื่อโหลดข้อมูลเสร็จ</p>
                                                            <p class="language" v-else>This text will disappear when data is loaded.</p>
                                                        </option>
                                                        <option v-for="option in advisoryList" :key="option.id" :value="option.id">{{ option.name }}</option>
                                                    </select>
                                                    <p class="error" v-if="errorFormMessage.advisor">{{ errorFormMessage.advisor[0] }}</p>
                                                </div>
                                                <div class="form-outline mb-4">
                                                    <label class="form-label">
                                                        <p class="language" v-if="language === 'th'">ชุมนุม / กิจกรรม</p>
                                                        <p class="language" v-else>Community</p>
                                                    </label>
                                                    <select class="form-control select" v-model="selectCommunity">
                                                        <option v-if="communityList.length === 0" value="none">
                                                            <p class="language" v-if="language === 'th'">ข้อความนี้จะหายไปเมื่อโหลดข้อมูลเสร็จ</p>
                                                            <p class="language" v-else>This text will disappear when data is loaded.</p>
                                                        </option>
                                                        <option v-if="communityList[0]" value="non">
                                                            <p class="language" v-if="language === 'th'">-----ชุมนุม-----</p>
                                                        <p class="language" v-else>-----Clubs-----</p>
                                                        </option>
                                                        <option v-for="option in communityList[0]" :key="option.id" :value="option.id">
                                                            <p class="language" v-if="language === 'th'">{{ option.name_th }}</p>
                                                            <p class="language" v-else>{{ option.name_en }}</p>
                                                        </option>
                                                        <option v-if="communityList[1]" value="no">
                                                            <p class="language" v-if="language === 'th'">----กิจกรรม----</p>
                                                            <p class="language" v-else>-----Events-----</p>
                                                        </option>
                                                        <option v-for="option in communityList[1]" :key="option.id" :value="option.id">
                                                            <p class="language" v-if="language === 'th'">{{ option.name_th }}</p>
                                                            <p class="language" v-else>{{ option.name_en }}</p>
                                                        </option>
                                                    </select>
                                                    <p class="error" v-if="errorFormMessage.community">{{ errorFormMessage.community[0] }}</p>
                                                </div>
                                                <div class="form-outline mb-4">
                                                    <label class="form-label d-flex" for="startdate">
                                                        <p class="language" v-if="language === 'th'">วันที่เริ่ม</p>
                                                        <p class="language" v-else>Start Date</p>
                                                        &nbsp;*31-Jan-2020
                                                    </label>
                                                    <input type="date" id="startdate" class="form-control" v-model="start_date" placeholder="dd-mmm-yyyy" />
                                                    <p class="error" v-if="errorFormMessage.start_date">{{ errorFormMessage.start_date[0] }}</p>
                                                </div>
                                                <div class="form-outline mb-4">
                                                    <label class="form-label d-flex" for="enddate">
                                                        <p class="language" v-if="language === 'th'">วันที่สิ้นสุด</p>
                                                        <p class="language" v-else>End Date</p>
                                                        &nbsp;*31-Jan-2020
                                                    </label>
                                                    <input type="date" id="enddate" class="form-control" v-model="end_date" placeholder="dd-mmm-yyyy" />
                                                    <p class="error" v-if="errorFormMessage.end_date">{{ errorFormMessage.end_date[0] }}</p>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="modal-footer">
                                            <button @click="addAdvisor()" type="button" class="btn btn-primary">
                                            <p class="language" v-if="language === 'th'">เพิ่ม</p>
                                            <p class="language" v-else>Add</p>
                                            </button>
                                            <button type="button" class="btn btn-light" data-dismiss="modal">
                                            <p class="language" v-if="language === 'th'">ปิด</p>
                                            <p class="language" v-else>Close</p>
                                            </button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="not-bg" v-if="advisorError !== null">{{ advisorError }}</div>
                        <div v-else-if="advisorLoading">
                            <div class="d-flex justify-content-center">
                                <div class="spinner-border m-5" role="status">
                                    <span class="sr-only">Loading...</span>
                                </div>
                            </div>
                            <div class="d-flex justify-content-center">
                                <h3>Loading...</h3>
                            </div>
                        </div>
                        <div v-else-if="advisorActiveList.length === 0 && advisorNotActiveList.length === 0">
                            <p class="language" v-if="language === 'th'">ไม่มีข้อมูลอาจารย์ที่ปรึกษา</p>
                            <p class="language" v-else>No advisories data.</p>
                        </div>
                        <div v-else class="row collapse show justify-content-center" id="assign">
                            <div class="not-bg mt-4">
                                <h3 class="mt-2 mb-2 ml-2 toggle d-flex" @click="activeToggle"
                                    aria-expanded="false" aria-controls="activeAdvisor">
                                    <p class="language" v-if="language === 'th'">อาจารย์ที่ปรึกษาที่ทำงานอยู่</p>
                                    <p class="language" v-else>Active Advisories</p>&nbsp;
                                    <i v-if="activeTog" class="fas fa-caret-down"></i>
                                    <i v-else class="fas fa-caret-right"></i></h3>
                            </div>
                            <div class="m-2 mt-4 collapse show" id="activeAdvisor" style="padding-left: 20px;" v-if="advisorActiveList[0].length === 0">
                                <p class="language" v-if="language === 'th'">ไม่มีอาจารย์ที่ปรึกษาที่กำลังทำงานอยู่</p>
                                <p class="language" v-else>No active advisories.</p>
                            </div>
                            <div class="collapse show" id="activeAdvisor">
                                <div class="mt-2 mb-2 not-bg" v-for="advisor in advisorActiveList[0]" :key="advisor.id">
                                    <div class="ml-3 mr-2 d-flex" style="padding-top: 10px;">
                                    <p class="language" v-if="language === 'th'"><router-link :to="'/profile/' + advisor.advisor">{{ advisor.meta.advisor_name }}</router-link>
                                    &nbsp;เป็นที่ปรึกษาให้กับ&nbsp;<router-link :to="'/tocommunity/' + advisor.community">{{ advisor.meta.community_name }}</router-link></p>
                                    <p class="language" v-else><router-link :to="'/profile/' + advisor.advisor">{{ advisor.meta.advisor_name }}</router-link>
                                    &nbsp;is an advisor of&nbsp;<router-link :to="'/tocommunity/' + advisor.community">{{ advisor.meta.community_name }}</router-link>.</p>
                                    </div>
                                    <div class="d-flex justify-content-end">
                                        <button @click="selectDeleteAdvisor(advisor.id)" class="btn btn-danger  mt-4">Delete</button>
                                    </div>
                                </div>
                            </div>
                            <div class="not-bg mt-4">
                                <h3 class="mt-2 mb-2 ml-2 toggle d-flex" @click="nonActiveToggle"
                                    aria-expanded="false" aria-controls="nonActiveAdvisor">
                                    <p class="language" v-if="language === 'th'">อาจารย์ที่ปรึกษาที่ไม่ทำงานแล้ว</p>
                                    <p class="language" v-else>Non-active Advisories</p>&nbsp;
                                    <i v-if="nonActiveTog" class="fas fa-caret-down"></i>
                                    <i v-else class="fas fa-caret-right"></i></h3>
                            </div>
                            <div class="m-2 mt-4 collapse show" style="padding-left: 20px;" id="nonActiveAdvisor" v-if="advisorNotActiveList[0].length === 0">
                                <p class="language" v-if="language === 'th'">ไม่มีอาจารย์ที่ปรึกษาที่ไม่ได้ทำงานอยู่</p>
                                <p class="language" v-else>No non-active advisories.</p>
                            </div>
                            <div class="collapse show" id="nonActiveAdvisor" v-else>
                                <div class="mt-2 not-bg" v-for="advisor in advisorNotActiveList[0]" :key="advisor.id">
                                    <div class="d-flex" style="padding: 10px;">
                                    <p class="language" v-if="language === 'th'"><router-link :to="'/profile/' + advisor.advisor">{{ advisor.meta.advisor_name }}</router-link>
                                    &nbsp;เคยเป็นที่ปรึกษาให้กับ&nbsp;<router-link :to="'/tocommunity/' + advisor.community">{{ advisor.meta.community_name }}</router-link></p>
                                    <p class="language" v-else><router-link :to="'/profile/' + advisor.advisor">{{ advisor.meta.advisor_name }}</router-link>
                                    &nbsp;was an advisor of&nbsp;<router-link :to="'/tocommunity/' + advisor.community">{{ advisor.meta.community_name }}</router-link>.</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <!-- <div class="d-lg-none line mt-4"></div> -->
                <div class="col-xl ml-xl-2">
                    <div class="mt-4">
                        <div class="row not-bg">
                            <h2 class="m-2 toggle d-flex" @click="pendingToggle"
                            aria-expanded="false" aria-controls="pending">
                            <p class="language" v-if="language === 'th'">คำขออนุมัติที่ยังไม่ได้ตอบรับ</p>
                            <p class="language" v-else>Pending Approval Requests</p>&nbsp;
                            <i v-if="pendingTog" class="fas fa-caret-down"></i>
                            <i v-else class="fas fa-caret-right"></i></h2>
                        </div>
                        <div class="m-2" v-if="requestWaitError !== null">{{ requestWaitError }}</div>
                        <div v-else-if="requestWait[0] === 'Loading.....'">
                            <div class="d-flex justify-content-center">
                                <div class="spinner-border m-5" role="status">
                                    <span class="sr-only">Loading...</span>
                                </div>
                            </div>
                            <div class="d-flex justify-content-center">
                                <h3>Loading...</h3>
                            </div>
                        </div>
                        <div v-else-if="requestWait[0].length === 0">
                            <div class="m-2 mt-4 collapse show" id="pending">
                                <p class="language" v-if="language === 'th'">ไม่มีคำขออนุมัติที่ยังไม่ได้ตอบรับ</p>
                                <p class="language" v-else>No pending requests.</p>
                            </div>
                        </div>
                        <div v-else class="collapse show" id="pending" style="max-width: 100%;">
                            <div class="mt-2 not-bg" v-for="request in requestWait[0].slice().reverse()" :key="request">
                                <div class="m-2">
                                    <div class="d-flex requests">
                                        <p v-if="language === 'th'"><router-link :to="'/tocommunity/' + request.community">{{ getCommunityName(request.community) }}</router-link>&nbsp;ได้ส่งคำขออนุมมัติ</p>
                                        <p v-else><router-link :to="'/tocommunity/' + request.community">{{ getCommunityName(request.community) }}</router-link>&nbsp;has submitted an approval request.</p>
                                    </div>
                                    <p class="requests message">{{ request.message }}</p>
                                    <a v-if="request.attached_file !== null" :href="request.attached_file">
                                        <p class="language" v-if="language === 'th'">ไฟล์ที่แนบมา</p>
                                        <p class="language" v-else>Attached File</p>
                                    </a>
                                    <div class="d-flex justify-content-center justify-content-lg-end">
                                        <button @click="selectAcceptRequest(request.id)" class="btn btn-primary mb-2">
                                            <p class="language" v-if="language === 'th'">ยอมรับ</p>
                                            <p class="language" v-else>Accept</p>
                                        </button>
                                        <button @click="selectDeclineRequest(request.id)" class="btn btn-danger mb-2">
                                            <p class="language" v-if="language === 'th'">ปฏิเสธ</p>
                                            <p class="language" v-else>Decline</p>
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="mt-4">
                        <div class="row not-bg">
                            <h2 class="m-2 toggle d-flex" @click="acceptToggle"
                            aria-expanded="false" aria-controls="accept">
                            <p class="language" v-if="language === 'th'">คำขออนุมัติที่ถูกยอมรับ</p>
                            <p class="language" v-else>Accepted Approval Requests</p>&nbsp;
                            <i v-if="acceptTog" class="fas fa-caret-down"></i>
                            <i v-else class="fas fa-caret-right"></i></h2>
                        </div>
                        <div class="m-2" v-if="requestAcceptError !== null">{{ requestAcceptError }}</div>
                        <div v-else-if="requestAccept[0] === 'Loading.....'">
                            <div class="d-flex justify-content-center">
                                <div class="spinner-border m-5" role="status">
                                    <span class="sr-only">Loading...</span>
                                </div>
                            </div>
                            <div class="d-flex justify-content-center">
                                <h3>Loading...</h3>
                            </div>
                        </div>
                        <div v-else-if="requestAccept[0].length === 0">
                            <div class="m-2 collapse show" id="accept">
                                <p class="language" v-if="language === 'th'">ไม่มีคำขออนุมัติที่ถูกยอมรับ</p>
                                <p class="language" v-else>No accepted approval requests.</p>
                            </div>
                        </div>
                        <div v-else class="collapse show" id="accept" style="max-width: 100%;">
                            <div class="not-bg mt-2" v-for="request in requestAccept[0].slice().reverse()" :key="request">
                                <div class="m-2">
                                    <div class="d-flex requests">
                                        <p v-if="language === 'th'">คำขอของ <router-link :to="'/tocommunity/' + request.community">{{ getAcceptedCommunityName(request.community) }}</router-link> ได้ถูกยอมรับแล้ว</p>
                                        <p v-else><router-link :to="'/tocommunity/' + request.community">{{ getAcceptedCommunityName(request.community) }}</router-link>'s approval request is accepted.</p>
                                    </div>
                                    <p class="requests message" style="padding-bottom: 10px;">{{ request.message }}</p>
                                    <a v-if="request.attached_file !== null" :href="request.attached_file">
                                        <p class="language" v-if="language === 'th'">ไฟล์ที่แนบมา</p>
                                        <p class="language" v-else>Attached File</p>
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="mt-4">
                        <div class="row not-bg">
                            <h2 class="m-2 toggle d-flex" @click="declineToggle"
                            aria-expanded="false" aria-controls="decline">
                            <p class="language" v-if="language === 'th'">คำขออนุมัติที่ถูกปฏิเสธ</p>
                            <p class="language" v-else>Declined Approval Requests</p>&nbsp;
                            <i v-if="declineTog" class="fas fa-caret-down"></i>
                            <i v-else class="fas fa-caret-right"></i>
                            </h2>
                        </div>
                        <div class="m-2" v-if="requestDeclineError !== null">{{ requestDeclineError }}</div>
                        <div v-else-if="requestDecline[0] === 'Loading.....'">
                            <div class="d-flex justify-content-center">
                                <div class="spinner-border m-5" role="status">
                                    <span class="sr-only">Loading...</span>
                                </div>
                            </div>
                            <div class="d-flex justify-content-center">
                                <h3>Loading...</h3>
                            </div>
                        </div>
                        <div v-else-if="requestDecline[0].length === 0">
                            <div class="m-2 mt-4 collapse show" id="decline">
                                <p class="language" v-if="language === 'th'">ไม่มีคำขออนุมัติที่ถูกปฏิเสธ</p>
                                <p class="language" v-else>No declined approval requests.</p>
                            </div>
                        </div>
                        <div v-else class="collapse show" id="decline" style="max-width: 100%;">
                            <div class="not-bg mt-2" v-for="request in requestDecline[0].slice().reverse()" :key="request">
                                <div class="m-2">
                                    <div class="d-flex requests">
                                        <p v-if="language === 'th'">คำขอของ <router-link :to="'/tocommunity/' + request.community">{{ getDeclinedCommunityName(request.community) }}</router-link> ได้ถูกปฏิเสธแล้ว</p>
                                        <p v-else><router-link :to="'/tocommunity/' + request.community">{{ getDeclinedCommunityName(request.community) }}</router-link>'s approval request is declined.</p>
                                    </div>
                                    <p class="requests message" style="padding-bottom: 10px;">{{ request.message }}</p>
                                    <a v-if="request.attached_file !== null" :href="request.attached_file">
                                        <p class="language" v-if="language === 'th'">ไฟล์ที่แนบมา</p>
                                        <p class="language" v-else>Attached File</p>
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="col-md"></div>
        <!-- Modal -->
        <the-alert :id="'alert'" :closetype="modalCloseType" :success="modalSuccess" :loading="modalLoading" :text="modalText">
            <template v-slot:header></template>
        </the-alert>
        <the-confirm :id="'deleteAdvisor'">
            <h4 class="d-flex justify-content-center">
                <p class="language" v-if="language === 'th'">ยกเลิกการปรึกษานี้?</p>
                <p class="language" v-else>Delete this advisory?</p>
            </h4>
            <template v-slot:footer>
                <button data-dismiss="modal" class="btn btn-danger mr-4" style="width: 75px;" @click="deleteAdvisor(selectDelAdvisor)">
                    <p class="language" v-if="language === 'th'">ใช่</p>
                    <p class="language" v-else>Yes</p>
                </button>
                <button data-dismiss="modal" class="btn btn-outline-secondary ml-4" style="width: 75px;">
                    <p class="language" v-if="language === 'th'">ไม่</p>
                    <p class="language" v-else>No</p>
                </button>
            </template>
        </the-confirm>
        <the-confirm :id="'acceptRequest'">
            <h4 class="d-flex justify-content-center">
                <p class="language" v-if="language === 'th'">ยอมรับคำขออนุมัตินี้?</p>
                <p class="language" v-else>Accept this request?</p>
            </h4>
            <template v-slot:footer>
                <button data-dismiss="modal" class="btn btn-primary mr-4" style="width: 75px;" @click="acceptRequest(selectaccept)">
                    <p class="language" v-if="language === 'th'">ใช่</p>
                    <p class="language" v-else>Yes</p>
                </button>
                <button data-dismiss="modal" class="btn btn-outline-secondary ml-4" style="width: 75px;">
                    <p class="language" v-if="language === 'th'">ไม่</p>
                    <p class="language" v-else>No</p>
                </button>
            </template>
        </the-confirm>
        <the-confirm :id="'declineRequest'">
            <h4 class="d-flex justify-content-center">
                <p class="language" v-if="language === 'th'">ปฏิเสธคำขออนุมัตินี้?</p>
                <p class="language" v-else>Decline this request?</p>
            </h4>
            <template v-slot:footer>
                <button data-dismiss="modal" class="btn btn-danger mr-4" style="width: 75px;" @click="declineRequest(selectdecline)">
                    <p class="language" v-if="language === 'th'">ใช่</p>
                    <p class="language" v-else>Yes</p>
                </button>
                <button data-dismiss="modal" class="btn btn-outline-secondary ml-4" style="width: 75px;">
                    <p class="language" v-if="language === 'th'">ไม่</p>
                    <p class="language" v-else>No</p>
                </button>
            </template>
        </the-confirm>
    </div>
</template>
<script>
import axios from 'axios';
import { $http } from '../../assets/variables.js';
import TheAlert from '../modal/TheAlert.vue';
import TheConfirm from '../modal/TheConfirm.vue';

export default {
    data() {
        return {
            token: '',
            language: '',
            myinfo: '',
            requestWait: ['Loading.....'],
            requestWaitError: null,
            requestWaitCommunity: [],
            requestWaitLoading: true,
            requestAcceptError: null,
            requestAcceptCommunity: [],
            requestAccept: ['Loading.....'],
            requestAcceptLoading: true,
            requestDeclineError: null,
            requestDeclineCommunity: [],
            requestDecline: ['Loading.....'],
            requestDeclineLoading: true,
            advisorActiveList: [],
            advisorNotActiveList: [],
            advisorError: null,
            advisorLoading: true,
            selectAdvisor: 'none',
            selectCommunity: 'none',
            advisoryList: [],
            communityList: [],
            start_date: null,
            end_date: null,
            errorFormMessage: '',
            assignTog: false,
            activeTog: false,
            nonActiveTog: false,
            pendingTog: false,
            acceptTog: false,
            declineTog: false,
            modalCloseType: '',
            modalSuccess: false,
            modalLoading: false,
            modalText: '',
            selectDelAdvisor: '',
            selectaccept: '',
            selectdelete: '',
        }
    },
    components: {
        TheAlert,
        TheConfirm,
    },
    methods: {
        selectDeclineRequest(id){
            this.selectdecline = id;
            window.$('#declineRequest').modal('show');
        },
        selectAcceptRequest(id){
            this.selectaccept = id;
            window.$('#acceptRequest').modal('show');
        },
        selectDeleteAdvisor(id){
            this.selectDelAdvisor = id;
            window.$('#deleteAdvisor').modal('show');
        },
        pendingToggle(){
            this.pendingTog = !this.pendingTog;
            window.$('#pending').collapse('toggle');
        },
        acceptToggle(){
            this.acceptTog = !this.acceptTog;
            window.$('#accept').collapse('toggle');
        },
        declineToggle(){
            this.declineTog = !this.declineTog;
            window.$('#decline').collapse('toggle');
        },
        nonActiveToggle(){
            this.nonActiveTog = !this.nonActiveTog;
            window.$('#nonActiveAdvisor').collapse('toggle');
        },
        activeToggle(){
            this.activeTog = !this.activeTog;
            window.$('#activeAdvisor').collapse('toggle');
        },
        assignToggle(){
            this.assignTog = !this.assignTog;
            window.$('#assign').collapse('toggle');
        },
        deleteAdvisor(id){
            this.modalCloseType = '';
            this.modalSuccess = false;
            this.modalLoading = true;
            this.modalText = 'Loading...';
            window.$('#alert').modal('show');
            axios.delete($http + 'api/membership/advisory/' + id + '/', {
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                this.modalCloseType = '';
                this.modalSuccess = true;
                this.modalLoading = false;
                if(this.language === 'th') {
                    this.modalText = 'ยกเลิกการปรึกษานี้แล้ว';
                } else {
                    this.modalText = 'Advisory deleted.';
                }
                this.getAdvisorData();
            }).catch(() => {
                this.modalCloseType = '';
                this.modalSuccess = false;
                this.modalLoading = false;
                if(this.language === 'th') {
                    this.modalText = 'เกิดข้อผิดพลาดระหว่างยกเลิกการปรึกษา';
                } else {
                    this.modalText = 'Failed to delete advisory.';
                }
            })
        },
        addAdvisor(){
            this.modalCloseType = '';
            this.modalSuccess = false;
            this.modalLoading = true;
            this.modalText = 'Loading...';
            window.$('#alert').modal('show');
            axios.post($http + 'api/membership/advisory/', {
                advisor: this.selectAdvisor,
                community: this.selectCommunity,
                start_date: this.start_date,
                end_date: this.end_date
            }, {
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                this.modalCloseType = '';
                this.modalSuccess = true;
                this.modalLoading = false;
                if(this.language === 'th') {
                    this.modalText = 'เพิ่มที่ปรึกษาแล้ว'
                } else {
                    this.modalText = 'Advisor added.';
                }
                this.selectAdvisor = 'none';
                this.selectCommunity = 'none';
                this.start_date = null;
                this.end_date = null;
                this.errorFormMessage = '';
                this.getAdvisorData();
            }).catch((err) => {
                this.errorFormMessage = err.response.data
            });
        },
        getAddingData(){
            axios.get($http + 'api/user/user?user_group=lecturer', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.advisoryList = response.data;
            }).catch(() => {
                this.modalCloseType = '';
                this.modalSuccess = false;
                this.modalLoading = false;
                if(this.language === 'th') {
                    this.modalText = 'เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่'
                } else {
                    this.modalText = 'Data fetching error, please try again.';
                }
                window.$('#alert').modal('show');
            });
            axios.get($http + 'api/community/club', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.communityList[0] = response.data
            }).catch(() => {
                this.modalCloseType = '';
                this.modalSuccess = false;
                this.modalLoading = false;
                if(this.language === 'th') {
                    this.modalText = 'เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่'
                } else {
                    this.modalText = 'Data fetching error, please try again.';
                }
                window.$('#alert').modal('show');
            })
            axios.get($http + 'api/community/event', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.communityList[1] = response.data
            }).catch(() => {
                this.modalCloseType = '';
                this.modalSuccess = false;
                this.modalLoading = false;
                if(this.language === 'th') {
                    this.modalText = 'เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่'
                } else {
                    this.modalText = 'Data fetching error, please try again.';
                }
                window.$('#alert').modal('show');
            })
        },
        getAdvisorData(){
            this.advisorActiveList = [];
            this.advisorNotActiveList = [];
            this.advisorLoading = true;
            axios.get($http + 'api/membership/advisory?is_active=1', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.advisorActiveList.push(response.data);
                this.advisorLoading = false;
            }).catch(() => {
                if(this.language === 'th') {
                    this.advisorError = "เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่"
                } else {
                    this.advisorError = "Data fetching error, please try again."
                }
                this.advisorLoading = false;
            });
            axios.get($http + 'api/membership/advisory?is_active=0', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.advisorNotActiveList.push(response.data);
                this.advisorLoading = false;
            }).catch(() => {
                if(this.language === 'th') {
                    this.advisorError = "เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่"
                } else {
                    this.advisorError = "Data fetching error, please try again."
                }
                this.advisorLoading = false;
            });
        },
        declineRequest(id){
            axios.patch($http + 'api/membership/approval-request/' + id + '/', {
                status: 'D'
            }, {
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                this.getWaitingApprove();
                this.getDeclinedApprove();
            }).catch((err) => {
                console.log(err);
                this.modalCloseType = '';
                this.modalSuccess = false;
                this.modalLoading = false;
                if(this.language === 'th') {
                    this.modalText = "เกิดข้อผิดพลาดระหว่างปฏิเสธคำขออนุมัติ"
                } else {
                    this.modalText = "Failed to change approval status.";
                }
                window.$('#alert').modal('show');
            });
        },
        acceptRequest(id){
            axios.patch($http + 'api/membership/approval-request/' + id + '/', {
                status: 'A'
            }, {
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                this.getWaitingApprove();
                this.getAcceptedApprove();
            }).catch(() => {
                this.modalCloseType = '';
                this.modalSuccess = false;
                this.modalLoading = false;
                if(this.language === 'th') {
                    this.modalText = "เกิดข้อผิดพลาดระหว่างยอมรับคำขออนุมัติ"
                } else {
                    this.modalText = "Failed to change approval status.";
                }
                window.$('#alert').modal('show');
            });
        },
        getDeclinedCommunityName(id){
            var com = this.requestDeclineCommunity.find((community) => community.id === id);
            if(com !== undefined){
                return com.name_en;
            }
        },
        getAcceptedCommunityName(id){
            var com = this.requestAcceptCommunity.find((community) => community.id === id);
            if(com !== undefined){
                return com.name_en;
            }
        },
        getCommunityName(id){
            var com = this.requestWaitCommunity.find((community) => community.id === id);
            if(com !== undefined){
                return com.name_en;
            }
        },
        getDeclinedApprove(){
            this.requestDecline.shift();
            this.requestDecline.push('Loading.....')
            axios.get($http + 'api/membership/approval-request?status=D', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.requestDecline.shift();
                this.requestDecline.push(response.data);
                var i;
                for(i in response.data){
                    axios.get($http + 'api/community/community/' + response.data[i].community, {
                        headers: {
                            Authorization: this.token
                        }
                    }).then((response) => {
                        this.requestDeclineCommunity.push(response.data)
                    }).catch(() => {
                        this.modalCloseType = '';
                        this.modalSuccess = false;
                        this.modalLoading = false;
                        if(this.language === 'th') {
                            this.modalText = "เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่";
                        } else {
                            this.modalText = "Data fetching error, please try again.";
                        }
                        window.$('#alert').modal('show');
                    })
                }
            }).catch(() => {
                if(this.language === 'th') {
                    this.requestDeclineError = "เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่"
                } else {
                    this.requestDeclineError = 'Data fetching error, please try again.';
                }
            });
        },
        getAcceptedApprove(){
            this.requestAccept.shift();
            this.requestAccept.push('Loading.....')
            axios.get($http + 'api/membership/approval-request?status=A', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.requestAccept.shift();
                this.requestAccept.push(response.data);
                var i;
                for(i in response.data){
                    axios.get($http + 'api/community/community/' + response.data[i].community, {
                        headers: {
                            Authorization: this.token
                        }
                    }).then((response) => {
                        this.requestAcceptCommunity.push(response.data)
                    }).catch((err) => {
                        console.log(err)
                        this.modalCloseType = '';
                        this.modalSuccess = false;
                        this.modalLoading = false;
                        if(this.language === 'th') {
                            this.modalText = "เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่";
                        } else {
                            this.modalText = "Data fetching error, please try again.";
                        }
                        window.$('#alert').modal('show');
                    })
                }
            }).catch(() => {
                if(this.language === 'th') {
                    this.requestAcceptError = "เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่"
                } else {
                    this.requestAcceptError = 'Data fetching error, please try again.';
                }
            });
        },
        getWaitingApprove(){
            this.requestWait.shift();
            this.requestWait.push('Loading.....')
            axios.get($http + 'api/membership/approval-request?status=W', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.requestWait.shift();
                this.requestWait.push(response.data);
                var i;
                for(i in response.data){
                    axios.get($http + 'api/community/community/' + response.data[i].community, {
                        headers: {
                            Authorization: this.token
                        }
                    }).then((response) => {
                        this.requestWaitCommunity.push(response.data)
                    }).catch(() => {
                        this.modalCloseType = '';
                        this.modalSuccess = false;
                        this.modalLoading = false;
                        if(this.language === 'th') {
                            this.modalText = "เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่";
                        } else {
                            this.modalText = "Data fetching error, please try again.";
                        }
                        window.$('#alert').modal('show');
                    })
                }
            }).catch(() => {
                if(this.language === 'th') {
                    this.requestWaitError = "เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่"
                } else {
                    this.requestWaitError = 'Data fetching error, please try again.';
                }
            });
        },
        getData() {
            this.token = window.$cookies.get('token');
            if(this.token === null){
                this.token = window.$cookies.get("itkmitlcommunitytokenbrowser");
            }
            this.language = window.$cookies.get('itkmitlcommunitylanguage');
            axios.get($http + 'api/user/user/me', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.myinfo = response.data;
                if(!this.myinfo.meta.is_student_committee) {
                    this.$router.back()
                }
                this.getWaitingApprove();
                this.getAcceptedApprove();
                this.getDeclinedApprove();
                this.getAdvisorData();
            }).catch(() => {
                this.modalCloseType = 'close';
                this.modalSuccess = false;
                this.modalLoading = false;
                if(this.language === 'th') {
                    this.modalText = "เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่";
                } else {
                    this.modalText = "Data fetching error, please try again.";
                }
                window.$('#alert').modal('show');
            });
        },
    },
    async mounted() {
        this.getData();
    },
    watch: {
        $route(){
            this.getData();
        }
    }
}
</script>
<style scoped>
p.error{
    color: red;
}
a{
    font-weight: bold;
}
.line{
    width: 100vh;
    height: 1px;
    border-top: solid;
    border-width: 3px;
}
p{
    margin: 0;
}
.requests{
    margin: 10px;
    padding-top: 10px;
}
p.requests{
    padding-top: 0;
}
.message{
    color: rgb(97, 97, 97);
}
</style>
<style scoped src="../../assets/main.css"></style>