<template>
    <div class='row'>
        <div class="col-lg"></div>
        <div class="col-lg">
            <form class="mt-5 border not-bg">
                <!-- ID input -->
                <div class="mt-4 ml-4 mr-4">
                    <h2>
                        <p class="language" v-if="language === 'th'">เข้าสู่ระบบ</p>
                        <p class="language" v-else>Log In</p>
                    </h2>
                </div>

                <div class="form-outline mb-4 mt-4 ml-4 mr-4">
                    <label class="form-label" for="form1Example1">
                        <p class="language" v-if="language === 'th'">ชื่อผู้ใช้</p>
                        <p class="language" v-else>Username</p>
                    </label>
                    <input name='id' type="text" id="form1Example1" class="form-control" @keyup.enter="loggingIn" placeholder="itxxxxxxxx" v-model="user">
                </div>

                <!-- Password input -->
                <div class="form-outline mb-4 mt-2 ml-4 mr-4">
                    <label class="form-label" for="form1Example2">
                        <p class="language" v-if="language === 'th'">รหัสผ่าน</p>
                        <p class="language" v-else>Password</p>
                    </label>
                    <input name='id' type="password" id="form1Example2" class="form-control" @keyup.enter="loggingIn" placeholder="********" v-model="password">
                </div>
                <div class="d-flex justify-content-center">
                    <p class="error">{{ errorMessage }}</p>
                </div>

                <!-- 2 column grid layout for inline styling -->
                <!-- <div class="row mb-4">
                    <div class="col d-flex justify-content-center"> -->
                    <!-- Checkbox -->
                    <!-- <div class="form-check">
                        <input
                        class="form-check-input"
                        type="checkbox"
                        value=""
                        id="form1Example3"
                        checked
                        >
                        <label class="form-check-label" for="form1Example3">
                        Remember me
                        </label>
                    </div>
                    </div>

                    <div class="col"> -->
                    <!-- Simple link -->
                    <!-- <a href="#!">Forgot password?</a>
                    </div>
                </div> -->

                <!-- Submit button -->
                <div class="mb-4 ml-4 mr-4">
                    <button @click.prevent="loggingIn" type="submit" class="btn primary btn-block">{{ loginButtonText }}
                    <div v-if="loginButtonText === 'Logging In' || loginButtonText === 'กำลังเข้าสู่ระบบ'" class="spinner-border spinner-border-sm" role="status">
                        <span class="sr-only">Loading...</span>
                    </div></button>
                </div>
            </form>
        </div>
        <div class="col-lg"></div>
        </div>
</template>

<script>
import axios from 'axios';
import { $http } from '../../assets/variables.js';

export default {
    props: ['qr'],
    data() {
        return{
            user: '',
            password: '',
            token1: '',
            token: '',
            loginButtonText: '',
            errorMessage: null,
        }
    },
    methods: {
        loggingIn() {
            this.errorMessage = null;
            if(this.language === 'th'){
                this.loginButtonText = 'กำลังเข้าสู่ระบบ'
            } else {
                this.loginButtonText = 'Logging In';
            }
            axios.post($http + 'api/user/login/', {
              username: this.user,
              password: this.password
            })
            .then((response) => {
                this.password = '';
                const data = response.data;
                this.token1 = "Token " + data.token;
                window.$cookies.set("token", this.token1, "3h");
                window.$cookies.set("itkmitlcommunitytokenbrowser", this.token1, 0);
                if(this.qr === 'qr'){
                    this.$router.back();
                }
                this.$router.push('/clubs');
            }).catch(() => {
                if(this.language === 'th'){
                    this.loginButtonText = 'เข้าสู่ระบบ'
                    this.errorMessage = 'ข้อมูลไม่ถูกต้อง'
                } else {
                    this.loginButtonText = 'Log In';
                    this.errorMessage = 'Invalid credentials';
                }
                this.password = '';
            })
        }
    },
    async mounted() {
        this.token = window.$cookies.get('token');
        if(this.token === null){
            this.token = window.$cookies.get("itkmitlcommunitytokenbrowser");
        }
        this.language = window.$cookies.get('itkmitlcommunitylanguage');
        if(this.language === 'th'){
            this.loginButtonText = 'เข้าสู่ระบบ'
        } else {
            this.loginButtonText = 'Log In';
        }
        if(this.token !== null){
            this.$router.back();
        }
    }
}
</script>
<style scoped src="../../assets/main.css"></style>
