<template>
    <div class="row">
        <div class="col-md"></div>
        <div class="col-md">
            <h2>
                <p class="language" v-if="language === 'th'">ขอบคุณที่เข้าร่วมกิจกรรม</p>
                <p class="language" v-else>Thanks for joining the Event</p>
            </h2>
        </div>
        <div class="col-md"></div>
    </div>
</template>
<script>
import axios from 'axios';
import { $http } from '../../assets/variables.js';

export default {
    props: ['keys'],
    data() {
        return {
            token: '',
            language: '',
        }
    },
    methods: {
        joinEvent(){
            this.token = window.$cookies.get('token');
            if(this.token === null){
                this.token = window.$cookies.get("itkmitlcommunitytokenbrowser");
            }
            this.language = window.$cookies.get('itkmitlcommunitylanguage');
            if(this.token === null || this.token === undefined || this.token === ''){
                this.$router.push('/qrlogin/qr');
            }
            const data = new FormData();
            data.append('key', this.keys);
            axios.post($http + 'api/generator/join-key/use/', data, {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                if(response.status > 199 && response.status < 300){
                    setTimeout(() => {
                        this.$router.push('/events');
                    }, 5000);
                }
            });
        },
    },
    async mounted() {
        this.joinEvent();
    },
}
</script>
<style scoped src="../../assets/main.css"></style>