<template>
    <div
        @click="goToUrl()"
        @keydown.esc="goToUrl()"
        class="modal fade"
        :id="id"
        tabindex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
        >
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header" style="border: none;">
                        <button
                        type="button"
                        class="btn-close"
                        data-dismiss="modal"
                        aria-label="Close"
                        ></button>
                    </div>
                    <div class="modal-body">
                        <div class="d-flex justify-content-center mb-4">
                            <slot name="picture">
                                <div v-if="loading" class="spinner-border m-5 d-flex justify-content-center" role="status">
                                    <span class="sr-only">Loading...</span>
                                </div>
                                <img v-else-if="success" src="../../assets/Checked.svg" style="width: 100px;">
                                <img v-else src="../../assets/Cancel.svg" style="width: 100px;">
                            </slot>
                        </div>
                        <slot>
                            <h4 v-if="loading" class="d-flex justify-content-center">Loading...</h4>
                            <h4 v-else class="d-flex justify-content-center">{{ text }}</h4>
                        </slot>
                    </div>
                    <div class="modal-footer d-flex justify-content-center" style="border: none;">
                        <slot name="footer"></slot>
                    </div>
                </div>
            </div>
        </div>
</template>
<script>
export default {
    props: ['id', 'closetype', 'url', 'success', 'loading', 'text'],
    methods: {
        goToUrl(){
            if (this.closetype === 'refresh'){
                this.$router.go();
            } else if (this.closetype === 'redirect'){
                this.$router.push(this.url);
            } else if (this.closetype === 'close'){
                this.$router.back()
            }
        }
    },
}
</script>
<style scoped src="../../assets/main.css"></style>