<template>
    <div class="background" style="min-height: 100vh; overflow: hidden;" v-if="myinfo.data">
        <div class="row not-bg-community">
            <div class="col-md-3"></div>
            <div class="col-md-6">
                <!-- <div class="d-none d-lg-block">
                    <h1 class="mt-4">Profile</h1>
                </div>
                <div class="d-lg-none">
                    <h1 class="mt-4 d-flex justify-content-center">Profile</h1>
                </div> -->
                <div class="container mb-4">
                </div>
                <!-- PC -->
                <div class="container d-none d-lg-block">
                    <div class="row" style="min-height: 300px;">
                        <div class="col-md mb-4">
                            <div class="container">
                                <div class="logo">
                                    <img v-if="myinfo.data.profile_picture" class="logo" :src="myinfo.data.profile_picture">
                                    <img v-else class="logo" src="../../assets/user_placeholder.jpg">
                                </div>
                                <div v-if="checkEdit">
                                    <button v-if="language === 'th'" style="margin-left: 30px;" class="btn btn-primary edit-button" @click="goProfileForm(myinfo.data.id)">
                                        แก้ไขข้อมูลส่วนตัว
                                    </button>
                                    <button v-else class="btn btn-primary edit-button" @click="goProfileForm(myinfo.data.id)">
                                        Edit Profile
                                    </button>
                                </div>
                            </div>
                            <h2 class="info d-flex">{{ myinfo.data.name }}<p class="d-none d-xl-block" v-if="myinfo.data.nickname" style="margin: 0;">&nbsp;({{ myinfo.data.nickname }})</p></h2>
                            <h2 class="info d-xl-none" v-if="myinfo.data.nickname" style="margin-top: -10px; margin-left: 240px;">&nbsp;({{ myinfo.data.nickname }})</h2>
                            <div class="d-flex info detail" style="margin-top: 0;">
                                <img src="../../assets/id-card.svg" class="icon">
                                <p v-if="myinfo.data.username" style="color: grey; margin: 0;">&nbsp;{{ myinfo.data.username }}</p>
                                <div v-if="myinfo.data.birthdate" style="margin: 0; color: grey; margin-left: 10px;" class="d-flex">
                                    <img src="../../assets/cake.svg" class="icon" style="height: 18px;">&nbsp;{{ myinfo.data.birthdate }}
                                </div>
                                <div class="d-none d-xl-block">
                                    <div class="d-flex">
                                        <img src="../../assets/group.svg" class="icon" style="margin-left: 10px;">
                                        <p style="color: grey; margin: 0;">&nbsp;{{ checkUserGroup }}</p>
                                    </div>
                                </div>
                            </div>
                            <div class="d-xl-none info detail">
                                <div class="d-flex">
                                    <img src="../../assets/group.svg" class="icon">
                                    <p style="color: grey; margin: 0;">&nbsp;{{ checkUserGroup }}</p>
                                </div>
                            </div>
                            <p v-if="myinfo.data.bio" class="info detail">{{ myinfo.data.bio }}</p>
                            <div v-if="checkEdit">
                                <router-link to="/votes" class="info detail d-flex">
