                                    <p class="language" v-else>Votes</p>: {{ myinfo.data.meta.votes }}
                                </router-link>
                            </div>
                            <div v-else class="info detail d-flex">
                                <p class="language" v-if="language === 'th'">โหวต</p>
                                <p class="language" v-else>Votes</p>: {{ myinfo.data.meta.votes }}
                            </div>
                        </div>
                    </div>
                </div>
                <!-- Mobile -->
                <div class="container d-lg-none">
                    <div class="mb-4">
                        <div class="container d-flex justify-content-center">
                            <div class="logo">
                                <img v-if="myinfo.data.profile_picture" class="logo" :src="myinfo.data.profile_picture">
                                <img v-else class="logo" src="../../assets/user_placeholder.jpg">
                            </div>
                        </div>
                        <div>
                            <h2 class="info d-flex justify-content-center mt-1">{{ myinfo.data.name }}</h2>
                            <h2 class="info d-flex justify-content-center" v-if="myinfo.data.nickname">({{ myinfo.data.nickname }})</h2>
                            <div class="d-flex justify-content-center info detail">
                                <img src="../../assets/id-card.svg" class="icon">
                                &nbsp;{{ myinfo.data.username }}
                                <img v-if="myinfo.data.birthdate" src="../../assets/cake.svg" class="icon" style="height: 18px; margin-left: 10px;">
                                &nbsp;{{ myinfo.data.birthdate }}
                                <div class="d-none d-sm-block">
                                    <img src="../../assets/group.svg" class="icon" style="margin-left: 10px; height: 20px; margin-top: -5px;">
                                    &nbsp;{{ checkUserGroup }}
                                </div>
                            </div>
                            <div class="d-flex d-sm-none justify-content-center info detail">
                                <img src="../../assets/group.svg" class="icon" style="height: 20px; margin-top: 0px;">
                                &nbsp;{{ checkUserGroup }}
                            </div>
                            <div class="d-flex justify-content-center mb-2 mt-2">
                                <p class="info detail text-center">{{ myinfo.data.bio }}</p>
                            </div>
                            <div class="d-flex justify-content-center">
                                <div v-if="checkEdit">
                                    <router-link to="/votes" class="info detail d-flex">
                                        <p class="language" v-if="language === 'th'">โหวต</p>
                                        <p class="language" v-else>Votes</p>: {{ myinfo.data.meta.votes }}
                                    </router-link>
                                </div>
                                <div v-else class="info detail d-flex">
                                    <p class="language" v-if="language === 'th'">โหวต</p>
                                    <p class="language" v-else>Votes</p>: {{ myinfo.data.meta.votes }}
                                </div>
                            </div>
                            <div class="d-flex justify-content-center mt-3">
                                <button v-if="checkEdit" class="btn btn-primary edit-button" @click="goProfileForm(myinfo.data.id)">
                                    <p class="language" v-if="language === 'th'">แก้ไขข้อมูลส่วนตัว</p>
