                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="col-md-3"></div>
        </div>
        <div class="row">
            <div class="col-md-3"></div>
            <div class="col-md-6">
                <div class="mt-4 container not-bg" style="margin-top: 10px; padding: 20px;">
                    <h3>
                        <p class="language" v-if="language === 'th'">การเป็นสมาชิกในปัจจุบัน</p>
                        <p class="language" v-else>Current Memberships</p>
                    </h3>
                    <div v-if="clubActivity.length !== 0 || eventActivity.length !== 0 || communityEventActivity.length !== 0 || labActivity.length !== 0">
                        <h4 class="toggle mt-4 d-flex" @click="clubToggle"
                        aria-expanded="false" aria-controls="club" v-if="clubActivity.length !== 0">
                            <p class="language" v-if="language === 'th'">ชุมนุม</p>
                            <p class="language" v-else>Clubs</p>&nbsp;
                        <i v-if="clubTog" class="fas fa-caret-down"></i>
                        <i v-else class="fas fa-caret-right"></i></h4>
                        <div v-if="clubActivity.length !== 0" class="collapse show" id="club">
                            <!-- <i class="fas fa-angle-left fa-3x d-none d-lg-block" @mousedown="startScroll(-20, 'club')" @mouseup="stopScroll" style="margin-top: 130px;"></i> -->
                            <!-- <div class="scrollmenu club d-flex mb-4"> -->
                            <div class="row d-flex justify-content-start">
                                <simple-card
                                v-for="club in clubActivity"
