                                :name_en="club.name_en"
                                :url_id="club.url_id"
                                :position="findPosition(club.id)"
                                :language="language"
                                :comType="'club'">
                                </simple-card>
                            </div>
                            <!-- </div> -->
                            <!-- <i class="fas fa-angle-right fa-3x d-none d-lg-block" @mousedown="startScroll(20, 'club')" @mouseup="stopScroll" style="margin-top: 130px;"></i> -->
                        </div>
                        <h4 class="toggle mt-4 d-flex" @click="eventToggle"
                        aria-expanded="false" aria-controls="event" v-if="eventActivity.length !== 0">
                        <p class="language" v-if="language === 'th'">กิจกรรม</p>
                        <p class="language" v-else>Events</p>&nbsp;
                        <i v-if="eventTog" class="fas fa-caret-down"></i>
                        <i v-else class="fas fa-caret-right"></i></h4>
                        <div class="collapse show" id="event" v-if="eventActivity.length !== 0">
                            <!-- <i class="fas fa-angle-left fa-3x d-none d-lg-block" @mousedown="startScroll(-20, 'event')" @mouseup="stopScroll" style="margin-top: 130px;"></i> -->
                            <!-- <div class="scrollmenu event d-flex  mb-4"> -->
                                <div class="row d-flex justify-content-start">
                                    <simple-card
                                    v-for="event in eventActivity"
                                    :key="event.id"
                                    :id="event.id"
                                    :logo="event.logo"
                                    :banner="event.banner"
                                    :name_th="event.name_th"
                                    :name_en="event.name_en"
                                    :url_id="event.url_id"
                                    :position="findPosition(event.id)"
                                    :language="language"
                                    :comType="'event'"
                                    ></simple-card>
                                </div>
                            <!-- </div> -->
                            <!-- <i class="fas fa-angle-right fa-3x d-none d-lg-block" @mousedown="startScroll(20, 'event')" @mouseup="stopScroll" style="margin-top: 130px;"></i> -->
                        </div>
                        <h4 class="toggle mt-4 d-flex" @click="comEventToggle"
                        aria-expanded="false" aria-controls="comevent" v-if="communityEventActivity.length !== 0">
                        <p class="language" v-if="language === 'th'">กิจกรรมชุมนุม/กิจกรรมห้องปฏบัติการ</p>
                        <p class="language" v-else>Community Events</p>&nbsp;
                        <i v-if="comEventTog" class="fas fa-caret-down"></i>
                        <i v-else class="fas fa-caret-right"></i></h4>
                        <div class="collapse show" id="comevent" v-if="communityEventActivity.length !== 0">
                            <!-- <i class="fas fa-angle-left fa-3x d-none d-lg-block" @mousedown="startScroll(-20, 'comevent')" @mouseup="stopScroll" style="margin-top: 130px;"></i> -->
                            <!-- <div class="scrollmenu comevent d-flex mb-4"> -->
                                <div class="row d-flex justify-content-start">
                                    <simple-card
                                    v-for="event in communityEventActivity"
                                    :key="event.id"
                                    :id="event.id"
                                    :logo="event.logo"
                                    :banner="event.banner"
                                    :name_th="event.name_th"
                                    :name_en="event.name_en"
                                    :description="event.description"
                                    :url_id="event.url_id"
                                    :create_by="event.meta.created_under_name_en"
                                    :position="findPosition(event.id)"
                                    :language="language"
                                    :comType="'comevent'">
                                    </simple-card>
                                </div>
                            <!-- </div> -->
                            <!-- <i class="fas fa-angle-right fa-3x d-none d-lg-block" @mousedown="startScroll(20, 'comevent')" @mouseup="stopScroll" style="margin-top: 130px;"></i> -->
                        </div>
                        <h4 class="toggle mt-4 d-flex" @click="labToggle"
                        aria-expanded="false" aria-controls="lab" v-if="labActivity.length !== 0">
                        <p class="language" v-if="language === 'th'">ห้องปฏิบัติการ</p>
                        <p class="language" v-else>Labs</p>
                        <i v-if="labTog" class="fas fa-caret-down"></i>
                        <i v-else class="fas fa-caret-right"></i></h4>
                        <div class="collapse show" id="lab" v-if="labActivity.length !== 0">
                            <!-- <i class="fas fa-angle-left fa-3x d-none d-lg-block" @mousedown="startScroll(-20, 'lab')" @mouseup="stopScroll" style="margin-top: 130px;"></i> -->
                            <!-- <div class="scrollmenu lab d-flex mb-4"> -->
                                <div class="row d-flex justify-content-start">
                                    <simple-card
                                        v-for="info in labActivity"
                                        :key="info.id"
                                        :name_th="info.name_th"
                                        :name_en="info.name_en"
                                        :logo="info.logo"
                                        :banner="info.banner"
                                        :url_id='info.url_id'
                                        :position="findPosition(info.id)"
                                        :language="language"
                                        :comType="'lab'">
                                    </simple-card>
                                </div>
                            <!-- </div> -->
                            <!-- <i class="fas fa-angle-right fa-3x d-none d-lg-block" @mousedown="startScroll(20, 'lab')" @mouseup="stopScroll" style="margin-top: 130px;"></i> -->
                        </div>
                    </div>
                    <div class="row" v-else>
                        <div class="col"></div>
                        <div class="col" v-if="Currentloading">
                            <div class="d-flex justify-content-center">
                                <div class="spinner-border m-5" role="status">
                                    <span class="sr-only">Loading...</span>
                                </div>
                            </div>
                            <div class="d-flex justify-content-center">
                                <h3>Loading...</h3>
                            </div>
                        </div>
                        <div class="col-8" v-else>
                            <div class="d-flex justify-content-center">
                                <p class="language" v-if="language === 'th'">ยังไม่มีการเข้าร่วมเป็นสมาชิก</p>
                                <p class="language" v-else>No current memberships</p>
                            </div>
                        </div>
                        <div class="col"></div>
                    </div>
                </div>
                <div class="container mt-4 mb-4 not-bg" style="padding: 20px;">
                    <h3>
                        <p class="language" v-if="language === 'th'">การเป็นสมาชิกในอดีต</p>
                        <p class="language" v-else>Past Memberships</p>
                    </h3>
                    <div v-if="!pastLoading">
                        <div class="row">
                            <div class="d-flex justify-content-center" v-if="pastPosition.length === 0">
                                <p class="language" v-if="language === 'th'">ยังไม่มีการเป็นสมาชิกในอดีต</p>
                                <p class="language" v-else>No past memberships</p>
                            </div>
                            <router-link v-for="past in pastPosition.slice().reverse()" :key="past.community_id" :to="'/tocommunity/' + past.community_id">
                                <div class="col-xl-11 ml-4" style="min-height: 60px; border-bottom: solid 1px silver;">
                                    <div class="d-flex">
                                        <div class="image">
                                            <img v-if="findPastLogo(past.community_id)" :src="findPastLogo(past.community_id)">
                                            <img v-else src="../../assets/banner1.png">
                                        </div>
                                        <p class="past-name">{{ findPastName(past.community_id) }}</p>
                                    </div>
                                    <p class="past-position d-none d-lg-block">{{ findPastPosition(past.position, past.community_type) }} ({{ getDateTime(past.start_datetime) }} to {{ getDateTime(past.end_datetime) }})</p>
                                    <p class="past-position d-lg-none">{{ findPastPosition(past.position, past.community_type) }}</p>
                                    <p class="past-time d-lg-none">({{ getDateTime(past.start_datetime) }} to {{ getDateTime(past.end_datetime) }})</p>
                                </div>
                            </router-link>
                        </div>
                    </div>
                    <div v-else>
                        <div class="d-flex justify-content-center">
                            <div class="spinner-border m-5" role="status">
                                <span class="sr-only">Loading...</span>
                            </div>
                        </div>
                        <div class="d-flex justify-content-center">
                            <h3>Loading...</h3>
                        </div>
                    </div>
                </div>
            </div>
            <div class="col-md"></div>
        </div>
    </div>
    <div class="row" v-else>
        <div class="col"></div>
        <div class="col">
            <div class="d-flex justify-content-center">
                <div class="spinner-border m-5" role="status">
                    <span class="sr-only">Loading...</span>
                </div>
            </div>
            <div class="d-flex justify-content-center">
                <h3>Loading...</h3>
            </div>
        </div>
        <div class="col"></div>
    </div>
</template>
<script>
import axios from 'axios';
import SimpleCard from '../card/SimpleCard.vue';
import { $http } from '../../assets/variables.js';

export default {
    components: {
        SimpleCard
    },
    props: ['userId'],
    data () {
        return {
            token: '',
            language: '',
            profile_pic: '',
            myinfo: '',
            checkId: '',
            checkinfo: '',
            labActivity: [],
            clubActivity: [],
            eventActivity: [],
            communityEventActivity: [],
            Currentloading: true,
            pastLoading: true,
            pastActivity: [],
            pastError: null,
            memPosition: [],
            clubTog: false,
            eventTog: false,
            comEventTog: false,
            labTog: false,
        };
    },
    methods: {
        getDateTime(dateTime){
            var check;
            var date = '';
            for(check in dateTime){
                if(dateTime[check] !== 'T'){
                    date += dateTime[check];
                } else {
                    break;
                }
            }
            return date;
        },
        findPastPosition(position, type){
            if(this.language === 'th'){
                if(type === 'club'){
                    if (position === 0) {
                        return 'สมาชิก';
                    } else if (position === 1) {
                        return 'ทีมงาน';
                    } else if (position === 2) {
                        return 'รองประธาน'
                    } else if (position === 3) {
                        return 'ประธาน'
                    } else {
                        return 'error'
                    }
                } else if(type === 'event'){
                    if (position === 0) {
                        return 'ผู้เข้าร่วม';
                    } else if (position === 1) {
                        return 'ทีมงาน';
                    } else if (position === 2) {
                        return 'รองประธาน'
                    } else if (position === 3) {
                        return 'ประธาน'
                    } else {
                        return 'error'
                    }
                } else if(type === 'lab'){
                    if (position === 0) {
                        return 'สมาชิกห้องปฏิบัติการ';
                    } else if (position === 1) {
                        return 'ผู้ช่วยดูแลห้องปฏิบัติการ';
                    } else if (position === 2) {
                        return 'อาจารย์ผู้ร่วมดูแลห้องปฏิบัติการ'
                    } else if (position === 3) {
                        return 'อาจารย์ผู้ดูแลห้องปฏิบัติการ'
                    } else {
                        return 'error'
                    }
                } else if(type === 'community_event'){
                    if (position === 0) {
                        return 'ผู้เข้าร่วม';
                    } else if (position === 1) {
                        return 'ทีมงาน';
                    } else if (position === 2) {
                        return 'ผู้ร่วมสร้างกิจกรรม'
                    } else if (position === 3) {
                        return 'ผู้สร้างกิจกรรม'
                    } else {
                        return 'error'
                    }
                }
            } else {
                if(type === 'club'){
                    if (position === 0) {
                        return 'Member';
                    } else if (position === 1) {
                        return 'Staff';
                    } else if (position === 2) {
                        return 'Vice President'
                    } else if (position === 3) {
                        return 'President'
                    } else {
                        return 'error'
                    }
                } else if(type === 'event'){
                    if (position === 0) {
                        return 'Participator';
                    } else if (position === 1) {
                        return 'Staff';
                    } else if (position === 2) {
                        return 'Vice President'
                    } else if (position === 3) {
                        return 'President'
                    } else {
                        return 'error'
                    }
                } else if(type === 'lab'){
                    if (position === 0) {
                        return 'Lab Member';
                    } else if (position === 1) {
                        return 'Lab Helper';
                    } else if (position === 2) {
                        return 'Lab Co-Supervisor'
                    } else if (position === 3) {
                        return 'Lab Supervisor'
                    } else {
                        return 'error'
                    }
                } else if(type === 'community_event'){
                    if (position === 0) {
                        return 'Participator';
                    } else if (position === 1) {
                        return 'Staff';
                    } else if (position === 2) {
                        return 'Event Co-Creator'
                    } else if (position === 3) {
                        return 'Event Creator'
                    } else {
                        return 'error'
                    }
                }
            }
        },
        findPastName(id){
            var name = this.pastActivity.find((name) => name.id === id);
            if(name !== undefined){
                return name.name_en;
            }
        },
        findPastLogo(id){
            var logo = this.pastActivity.find((com) => com.id === id);
            if(logo !== undefined){
                return logo.logo;
            }
        },
        labToggle(){
            this.labTog = !this.labTog;
            window.$('#lab').collapse('toggle');
        },
        comEventToggle(){
            this.comEventTog = !this.comEventTog;
            window.$('#comevent').collapse('toggle');
        },
        eventToggle(){
            this.eventTog = !this.eventTog;
            window.$('#event').collapse('toggle');
        },
        clubToggle(){
            this.clubTog = !this.clubTog;
            window.$('#club').collapse('toggle');
        },
        findPosition(id){
            var position = this.memPosition.find((mem) => mem.community === id)
            if(position !== undefined){
                return position.position
            }
        },
        startScroll(num, menu) {
            if(!this.scrolling){
                this.scrolling = setInterval(() => {
                    let save = '.' + menu;
                    let content = document.querySelector(save);
                    content.scrollLeft += num;
                }, 30);
            }
        },
        stopScroll(){
            clearInterval(this.scrolling);
            this.scrolling = false;
        },
        goProfileForm(id) {
            this.$router.push('/profileform/' + id);
        },
        toClub(id) {
            this.$router.push('/club/' + id);
        },
        toEvent(id) {
            this.$router.push('/event/' + id);
        },
        toLab(url_id) {
            this.$router.push('/lab/' + url_id)
        },
        getdata() {
            this.Currentloading = true;
            this.token = window.$cookies.get('token');
            if(this.token === null){
                this.token = window.$cookies.get("itkmitlcommunitytokenbrowser");
            }
            this.language = window.$cookies.get('itkmitlcommunitylanguage');
            axios.get($http + 'api/user/user/' + this.userId, {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.myinfo = response
                // axios.get(this.myinfo.data.profile_picture, {
                //     headers: {
                //         Authorization: this.token
                //     }
                // }).then((response) => {
                //     this.profile_pic = response.data;
                //     console.log(response)
                // });
            }).catch((err) => {
                (console.log(err))
                this.myinfo = '';
            });
                
            axios.get($http + 'api/membership/membership?user=' + this.userId + '&statys=A', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.memPosition = response.data
            }).catch((err) => {
                console.log(err)
            })
            axios.get($http + 'api/membership/membership?user=' + this.userId + '&community_type=club&status=A', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                var club
                for (club in response.data) {
                    axios.get($http + 'api/community/club/' + response.data[club].community, {
                        headers: {
                            Authorization: this.token
                        }
                    }).then((response) => {
                        this.clubActivity.push(response.data)
                        this.Currentloading = false;
                    })
                }
                this.Currentloading = false;
            }).catch((err) => {
                (console.log(err))
                this.Currentloading = false;
            });
            axios.get($http + 'api/membership/membership?user=' + this.userId + '&community_type=event&status=A', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                var event
                for (event in response.data) {
                    axios.get($http + 'api/community/event/' + response.data[event].community, {
                        headers: {
                            Authorization: this.token
                        }
                    }).then((response) => {
                        if(response.data.community_events !== 1){
                            this.eventActivity.push(response.data)
                            this.Currentloading = false;
                        }
                    })
                }
                this.Currentloading = false;
            }).catch((err) => {
                (console.log(err))
                this.Currentloading = false;
            });
            axios.get($http + 'api/membership/membership?user=' + this.userId + '&community_type=community_event&status=A', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                var comevent
                for (comevent in response.data) {
                    axios.get($http + 'api/community/event/community/' + response.data[comevent].community, {
                        headers: {
                            Authorization: this.token
                        }
                    }).then((response) => {
                        this.communityEventActivity.push(response.data)
                        this.Currentloading = false;
                    })
                }
                this.Currentloading = false;
            }).catch((err) => {
                (console.log(err))
                this.Currentloading = false;
            });
            axios.get($http + 'api/membership/membership?user=' + this.userId + '&community_type=lab&status=A', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                var lab
                for (lab in response.data) {
                    axios.get($http + 'api/community/lab/' + response.data[lab].community, {
                        headers: {
                            Authorization: this.token
                        }
                    }).then((response) => {
                        this.labActivity.push(response.data)
                        this.Currentloading = false;
                    })
                }
                this.Currentloading = false;
            }).catch((err) => {
                (console.log(err))
                this.Currentloading = false;
            });
            axios.get($http + 'api/membership/membership/past/' + this.userId, {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                var commu;
                this.pastPosition = response.data;
                if(response.data.length === 0){
                    this.pastLoading = false
                }
                for(commu in response.data){
                    axios.get($http + 'api/community/community/' + response.data[commu].community_id, {
                        headers: {
                            Authorization: this.token
                        }
                    }).then((response) => {
                        this.pastActivity.push(response.data)
                        this.pastLoading = false
                    }).catch((err) => {
                        console.log(err)
                    })
                }
            }).catch((err) => {
                console.log(err)
                this.pastLoading = false
                if(this.language === 'th') {
                    this.pastError = "เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่";
                } else {
                    this.pastError = "Data fetching error, please try again.";
                }
            });
            if (this.token !== null) {
                axios.get($http + 'api/user/user/me', {
                    headers: {
                        Authorization: this.token
                    }
                }).then((response) => {
                    this.checkinfo = response,
                    this.checkId = this.checkinfo.data.id
                }).catch((err) => {
                    (console.log(err))
                    this.checkinfo = '';
                    this.checkId = '';
                });
            }
        }
    },
    async mounted() {
        // this.token = window.$cookies.get('token');
        // try {
        //     let response = await axios.get($http + 'api/user/user/' + this.userId, {
        //         headers: {
        //             Authorization: this.token
        //         }
        //     })
        //     this.myinfo = response;
        // } catch (err) {
        //     (console.log(err))
        //     this.myinfo = '';
        // }
        this.getdata();
    },
    watch: {
        $route() {
        // this.token = window.$cookies.get('token');
        //     try {
        //         let response = axios.get($http + 'api/user/user/me', {
        //         headers: {
        //             Authorization: this.token
        //         }
        //         }).then(
        //         this.myinfo = response
        //         );
        //     } catch (err) {
        //         (console.log(err))
        //         this.myinfo = '';
        //     }
            this.getdata();
        }
    },
    computed: {
        checkEdit(){
            return this.myinfo.data.id === this.checkId;
        },
        checkUserGroup(){
            if(this.language === 'th'){
                if(this.myinfo.data.user_group === 'student'){
                    return "นักศึกษา";
                }
                if(this.myinfo.data.user_group === 'lecturer'){
                    return "อาจารย์";
                }
                if(this.myinfo.data.user_group === 'support'){
                    return "เจ้าหน้าที่";
                }
            }
            else{
                if(this.myinfo.data.user_group === 'student'){
                    return "Student";
                }
                if(this.myinfo.data.user_group === 'lecturer'){
                    return "Lecturer";
                }
                if(this.myinfo.data.user_group === 'support'){
                    return "Support Staff";
                }
            }
            return null;
        }
    },
}
</script>
