<template>
    <div class="ml-2 mr-2 mb-4 the-whole-card">
        <!-- PC -->
        <div class="d-none d-lg-block">
            <router-link :to="UrlLink">
            <div class="card">
                <div class="banner">
                    <img v-if="banner" class="card-img-top cover" :src="banner">
                    <img v-else class="card-img-top cover" src="../../assets/community_banner_placeholder.jpg">
                </div>
                <div class="logo">
                    <img v-if="logo" class="card-img-top logo" :src="logo">
                    <img v-else class="card-img-top logo" src="../../assets/community_logo_placeholder.jpg">
                </div>
                <div class="card-body mt-4">
                    <h5 class="card-title">{{ name_en }}</h5>
                    <h6 class="card-title">{{ name_th }}</h6>
                    <div>
                        <p class="card-text" v-if="comType !== 'comeventcheck'">{{ positionName(position) }}</p>
                        <p v-if="comType === 'comevent' || comType === 'comeventcheck'" class="card-text">by {{ create_by }}</p>
                    </div>
                    <div v-if="comType === 'comeventcheck'">
                        <div v-if="checkStatus() === 'cancel'"><div class="badge bg-danger card-text">
                            <p class="language" v-if="language === 'th'">ยกเลิก</p>
                            <p class="language" v-else>Cancelled</p>
                        </div></div>
                        <div v-else-if="checkStatus() === 'unapprove'"><div class="badge bg-secondary card-text">
                            <p class="language" v-if="language === 'th'">ยังไม่ได้รับการอนุมัติ</p>
                            <p class="language" v-else>Unapproved</p>
                        </div></div>
                        <div v-else-if="checkStatus() === 'end'"><div class="badge bg-dark card-text">
                            <p class="language" v-if="language === 'th'">จบแล้ว</p>
                            <p class="language" v-else>Ended</p>
                        </div></div>
                        <div v-else-if="checkStatus() === 'recruiting'"><div class="badge bg-success card-text">
                            <p class="language" v-if="language === 'th'">เปิดรับ</p>
                            <p class="language" v-else>Recruiting</p>
                        </div></div>
                    </div>
                </div>
            </div>
            </router-link>
        </div>
        <!-- Mobile -->
        <div class="d-lg-none d-flex justify-content-center">
            <router-link :to="UrlLink">
            <div class="card shadow" style="width: 15rem; height: 250px;">
                <div class="banner">
                    <img v-if="banner" class="card-img-top cover" :src="banner">
                    <img v-else class="card-img-top cover" src="../../assets/community_banner_placeholder.jpg">
                </div>
                <div class="logo">
                    <img v-if="logo" class="card-img-top logo" :src="logo">
                    <img v-else class="card-img-top logo" src="../../assets/community_logo_placeholder.jpg">
                </div>
                <div class="card-body mt-4">
                    <h5 class="card-title">{{ name_en }}</h5>
                    <h6 class="card-title">{{ name_th }}</h6>
                    <div>
                        <p class="card-text" v-if="comType !== 'comeventcheck'">{{ positionName(position) }}</p>
                        <p v-if="comType === 'comevent' || comType === 'comeventcheck'" class="card-text">by {{ create_by }}</p>
                    </div>
                    <div v-if="comType === 'comeventcheck'">
                        <div v-if="checkStatus() === 'cancel'"><div class="badge badge-mobile bg-danger card-text">
                            <p class="language" v-if="language === 'th'">ยกเลิก</p>
                            <p class="language" v-else>Cancelled</p>
                        </div></div>
                        <div v-else-if="checkStatus() === 'unapprove'"><div class="badge badge-mobile bg-secondary card-text">
                            <p class="language" v-if="language === 'th'">ยังไม่ได้รับการอนุมัติ</p>
                            <p class="language" v-else>Unapproved</p>
                        </div></div>
                        <div v-else-if="checkStatus() === 'end'"><div class="badge badge-mobile bg-dark card-text">
                            <p class="language" v-if="language === 'th'">จบแล้ว</p>
                            <p class="language" v-else>Ended</p>
                        </div></div>
                        <div v-else-if="checkStatus() === 'recruiting'"><div class="badge badge-mobile bg-success card-text">
                            <p class="language" v-if="language === 'th'">เปิดรับ</p>
                            <p class="language" v-else>Recruiting</p>
                        </div></div>
                    </div>
                </div>
            </div>
            </router-link>
        </div>
    </div>
</template>

<script>
export default {
    props: ['name_th', 'name_en', 'logo', 'banner', 'url_id', 'id', 'comType', 'create_by', 'position', 'accept', 'cancel', 'approve', 'language'],
    computed: {
        UrlLink() {
            var community;
            if(this.comType === 'club'){
                community = 'club';
            } else if(this.comType === 'comclub'){
                community = 'club';
            } else if(this.comType === 'event'){
                community = 'event';
            } else if(this.comType === 'comevent'){
                community = 'communityevent';
            } else if(this.comType === 'lab'){
                community = 'lab';
            }
            if (this.comType === 'comeventcheck'){
                return { path: '/tocommunity/' + this.id}
            }
            if (this.url_id !== null && this.url_id !== undefined){
                return { path: '/community/' + community + '/' + this.url_id };
            } else {
                return { path: '/community/' + community + '/' + this.id };
            }
        },
    },
    methods: {
        checkStatus(){
            if(this.comType === 'comeventcheck'){
                this.date = new Date().toJSON().slice(0,10);
                if(this.cancel){
                    return 'cancel';
                } else if (!this.approve){
                    return 'unapprove';
                } else if (this.date > this.end_date){
                    return 'end';
                } else {
                    return 'recruiting';
                }
            }
        },
        positionName(num){
            if(this.language === 'th'){
                if(this.comType === 'club'){
                    if (num === 0) {
                        return 'สมาชิก';
                    } else if (num === 1) {
                        return 'ทีมงาน';
                    } else if (num === 2) {
                        return 'รองประธาน'
                    } else if (num === 3) {
                        return 'ประธาน'
                    } else {
                        return 'error'
                    }
                } else if(this.comType === 'event'){
                    if (num === 0) {
                        return 'ผู้เข้าร่วม';
                    } else if (num === 1) {
                        return 'ทีมงาน';
                    } else if (num === 2) {
                        return 'รองประธาน'
                    } else if (num === 3) {
                        return 'ประธาน'
                    } else {
                        return 'error'
                    }
                } else if(this.comType === 'lab'){
                    if (num === 0) {
                        return 'สมาชิกห้องปฏิบัติการ';
                    } else if (num === 1) {
                        return 'ผู้ช่วยดูแลห้องปฏิบัติการ';
                    } else if (num === 2) {
                        return 'อาจารย์ผู้ร่วมดูแลห้องปฏิบัติการ'
                    } else if (num === 3) {
                        return 'อาจารย์ผู้ดูแลห้องปฏิบัติการ'
                    } else {
                        return 'error'
                    }
                } else if(this.comType === 'comclub' || this.comType === 'comevent'){
                    if (num === 0) {
                        return 'ผู้เข้าร่วม';
                    } else if (num === 1) {
                        return 'ทีมงาน';
                    } else if (num === 2) {
                        return 'ผู้ร่วมสร้างกิจกรรม'
                    } else if (num === 3) {
                        return 'ผู้สร้างกิจกรรม'
                    } else {
                        return 'error'
                    }
                }
            } else {
                if(this.comType === 'club'){
                    if (num === 0) {
                        return 'Member';
                    } else if (num === 1) {
                        return 'Staff';
                    } else if (num === 2) {
                        return 'Vice President'
                    } else if (num === 3) {
                        return 'President'
                    } else {
                        return 'error'
                    }
                } else if(this.comType === 'event'){
                    if (num === 0) {
                        return 'Participator';
                    } else if (num === 1) {
                        return 'Staff';
                    } else if (num === 2) {
                        return 'Vice President'
                    } else if (num === 3) {
                        return 'President'
                    } else {
                        return 'error'
                    }
                } else if(this.comType === 'lab'){
                    if (num === 0) {
                        return 'Lab Member';
                    } else if (num === 1) {
                        return 'Lab Helper';
                    } else if (num === 2) {
                        return 'Lab Co-Supervisor'
                    } else if (num === 3) {
                        return 'Lab Supervisor'
                    } else {
                        return 'error'
                    }
                } else if(this.comType === 'community_event'){
                    if (num === 0) {
                        return 'Participator';
                    } else if (num === 1) {
                        return 'Staff';
                    } else if (num === 2) {
                        return 'Event Co-Creator'
                    } else if (num === 3) {
                        return 'Event Creator'
                    } else {
                        return 'error'
                    }
                }
            }
        }
    },
}
</script>
<style scoped src="../../assets/main.css">
</style>
<style scoped src="../../assets/card.css">
</style>
<style scoped>
/* Mobile */
@media only screen and (max-width: 992px){
    .the-whole-card{
        width: 15rem; height: 30%;
    }
}
/* PC */
@media only screen and (min-width: 993px){
    .the-whole-card{
        width: 18rem;
        height: 30%;
    }
}
div.badge{
    height: 22px;
}
</style>