<template>
    <div class="row" id="main" v-if="!loading">
        <div class="col-lg"></div>
        <div class="col-lg-10 mt-3">
            <div v-if="errorMessage !== ''" class="d-flex justify-content-center mt-4">
                <h2>{{ errorMessage }}</h2>
            </div>
            <div class="d-flex ml-2 mb-3">
                <div class="d-flex justify-content-start">
                    <h2 class="toggle d-flex" @click="eventToggle"
                        aria-expanded="false" aria-controls="event">
                        <p class="language" v-if="language === 'th'">กิจกรรม</p>
                        <p class="language" v-else>Events</p>&nbsp;
                        <i v-if="eventTog" class="fas fa-caret-down"></i>
                        <i v-else class="fas fa-caret-right"></i>
                    </h2>
                </div>
                <div class="flex-fill"></div>
                <div class="d-flex justify-content-end">
                    <div>
                        <button v-if="token !== '' && token !== null" class="btn btn-warning" @click="goEventForm">Create Event</button>
                    </div>
                </div>
            </div>
            <div class="collapse show" id='event'>
                <div class="d-flex justify-content-center" v-if="infos.data.length === 0">
                    <h2>
                        <p class="language" v-if="language === 'th'">ยังไม่มีกิจกรรม</p>
                        <p class="language" v-else>No Events yet</p>
                    </h2>
                </div>
                <div class="row d-flex justify-content-start">
                    <the-card
                    v-for="info in infos.data"
                    :key="info.id"
                    :id="info.id"
                    :logo="info.logo"
                    :banner="info.banner"
                    :name_th="info.name_th"
                    :name_en="info.name_en"
                    :description="info.description"
                    :url_id="info.url_id"
                    :accept="info.is_accepting_requests"
                    :cancel="info.is_cancelled"
                    :approve="info.is_approved"
                    :end_date="info.end_date"
                    :language="language"
                    :comType="'event'">
                    </the-card>
                </div>
            </div>
            <div v-if="communityeventinfos.data.length !== 0">
                <h2 class="ml-2 toggle d-flex" @click="comeventToggle"
                    aria-expanded="false" aria-controls="comevent">
                    <p class="language" v-if="language === 'th'">กิจกรรมชุมนุม / กิจกรรมห้องปฏิบัติการ</p>
                    <p class="language" v-else>Community Events</p>&nbsp;
                    <i v-if="comeventTog" class="fas fa-caret-down"></i>
                    <i v-else class="fas fa-caret-right"></i>
                </h2>
                <div class="collapse show" id="comevent">
                    <div class="row d-flex justify-content-start mt-4">
                        <the-card
                        v-for="info in communityeventinfos.data"
                        :key="info.id"
                        :id="info.id"
                        :logo="info.logo"
                        :banner="info.banner"
                        :name_th="info.name_th"
                        :name_en="info.name_en"
                        :description="info.description"
                        :url_id="info.url_id"
                        :accept="info.is_accepting_requests"
                        :create_by="info.meta.created_under_name_en"
                        :cancel="info.is_cancelled"
                        :approve="info.is_approved"
                        :end_date="info.end_date"
                        :language="language"
                        :comType="'comevent'">
                        </the-card>
                    </div>
                </div>
            </div>
        </div>
        <div class="col-lg"></div>
    </div>
    <div class="row" v-else>
        <div class="col-lg"></div>
        <div class="col-lg loading">
            <div class="d-flex justify-content-center">
                <div class="spinner-border m-5" role="status">
                    <span class="sr-only">Loading...</span>
                </div>
            </div>
            <div class="d-flex justify-content-center">
                <h3>Loading...</h3>
            </div>
        </div>
        <div class="col-lg"></div>
    </div>
</template>

<script>
import TheCard from '../card/TheCard.vue';
import axios from 'axios';
import { $http } from '../../assets/variables.js';

export default {
    data() {
        return {
            infos: '',
            communityeventinfos: '',
            token: '',
            language: '',
            errorMessage: '',
            eventTog: false,
            comeventTog: false,
            loading: true,
        }
    },
    components: {
        TheCard,
    },
    methods: {
        comeventToggle() {
            this.comeventTog = !this.comeventTog;
            window.$('#comevent').collapse('toggle');
        },
        eventToggle() {
            this.eventTog = !this.eventTog;
            window.$('#event').collapse('toggle');
        },
        goEventForm() {
            this.$router.push('/eventform/create');
        },
        getData() {
            this.token = window.$cookies.get("token");
            if(this.token === null){
                this.token = window.$cookies.get("itkmitlcommunitytokenbrowser");
            }
            this.language = window.$cookies.get('itkmitlcommunitylanguage');
            axios.get($http + 'api/community/event?exclude_community_events=1', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.infos = response
                this.loading = false;
            }).catch((err) => {
                (console.log(err))
                if(this.language === 'th'){
                    this.errorMessage = "เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่"
                } else {
                    this.errorMessage = "Data fetching error, please try again.";
                }
                this.loading = false;
            });
            axios.get($http + 'api/community/event/community/', {
                headers: {
                    Authorization: this.token
                }
            }).then((response) => {
                this.communityeventinfos = response
                this.loading = false;
            }).catch((err) => {
                (console.log(err))
                if(this.language === 'th'){
                    this.errorMessage = "เกิดข้อผิดพลาดระหว่างดึงข้อมูล กรุณาลองใหม่"
                } else {
                    this.errorMessage = "Data fetching error, please try again.";
                }
                this.loading = false;
            });
        },
    },
    async mounted() {
        this.getData();
    },
    watch: {
        $route() {
            this.getData();
        }
    },
}
</script>
<style>
.card{
    display: flex;
    justify-content: space-between;
}
</style>
<style scoped src="../../assets/main.css"></style>