<template>
    <div class="row mb-4">
        <div class="col-md">
            <img src="../../assets/back.svg" @click="this.$router.back()" class="mt-4 ml-3 d-none d-lg-block back-button">
        </div>
        <div class="col-md not-bg mt-4 form-padding">
            <!-- <div v-if="info">
                {{ info }}
            </div> -->
            <h2 class="mb-2 d-none d-lg-block" v-if="labid === 'create'">
                <p class="language" v-if="language === 'th'">สร้างห้องปฏิบัติการ</p>
                <p class="language" v-else>Create Lab</p>
            </h2>
            <h2 class="mb-2 d-none d-lg-block" v-else >
                <p class="language" v-if="language === 'th'">แก้ไขห้องปฏิบัติการ</p>
                <p class="language" v-else>Edit Lab</p>
            </h2>

            <div class="d-lg-none row">
                <h2 class="mb-2 col-9" v-if="labid === 'create'">
                    <p class="language" v-if="language === 'th'">สร้างห้องปฏิบัติการ</p>
                    <p class="language" v-else>Create Lab</p>
                </h2>
                <h2 class="mb-2 col-9" v-else >
                    <p class="language" v-if="language === 'th'">แก้ไขห้องปฏิบัติการ</p>
                    <p class="language" v-else>Edit Lab</p>
                </h2>
                <i @click="this.$router.back()" style="cursor: pointer;" class="fas fa-times-circle fa-2x col d-flex justify-content-end mt-1"></i>
            </div>

            <label class="custom-file-label mb-4" for="inputGroupFile01">
                <p class="language" v-if="language === 'th'">โลโก้</p>
                <p class="language" v-else>Logo</p>
            </label>
            <div class="form-image mb-4" v-if="current_logo !== null">
                <img class="image" ref="image" :src="current_logo">
            </div>
            <div v-if="preview !== null" class="mb-4">
                <p class="language" v-if="language === 'th'">ตัวอย่าง</p>
                <p class="language" v-else>Preview</p>
                <img class="mb-4" :src="preview" style="width: 175px; height: 175px;"><br>
                <button @click="cancelPicture">
                    <p class="language" v-if="language === 'th'">เอารูปออก</p>
                    <p class="language" v-else>Remove Image</p>
                </button><br>
            </div>
            <div v-if="preview === null" class="input-group mb-4">
                <div class="custom-file">
                    <input type="file" class="custom-file-input" id="inputGroupFile01" ref="logoPic" @change="logoPic"
                    aria-describedby="inputGroupFileAddon01" accept="image/*">
                </div>
                <p class="error" v-if="errorMessage.logo">{{ errorMessage.logo[0] }}</p>
            </div>

            <label class="custom-file-label mb-4" for="inputGroupFile02">
                <p class="language" v-if="language === 'th'">รูปปก</p>
                <p class="language" v-else>Banner</p>
            </label>
            <img v-if="current_banner !== null" class="banner mb-4" :src="current_banner">
            <button v-if="cBanner" class="mb-4" @click="cancelBanner()">
                <p class="language" v-if="language === 'th'">เอารูปออก</p>
                <p class="language" v-else>Remove Image</p>
            </button>
            <div v-else class="input-group mb-4">
                <div class="custom-file">
                    <input type="file" class="custom-file-input" id="inputGroupFile02" ref="bannerPic" @change="bannerPic"
                    aria-describedby="inputGroupFileAddon01" accept="image/*">
                </div>
                <p class="error" v-if="errorMessage.banner">{{ errorMessage.banner[0] }}</p>
            </div>

           <div class="row">
                <div class="col-xl">
                <div class="form-outline mb-4">
                    <label class="form-label" for="name_th">
                        <p class="language" v-if="language === 'th'">ชื่อห้องปฏิบัติการ (ไทย)</p>
                        <p class="language" v-else>Lab Name (Thai)</p>
                    </label>
                    <input type="text" id="name_th" class="form-control" v-model="name_th" />
                    <p class="error" v-if="errorMessage.name_th">{{ errorMessage.name_th[0] }}</p>
                </div>
                </div>
                <div class="col-xl">
                <div class="form-outline mb-4">
                    <label class="form-label" for="name_en">
                        <p class="language" v-if="language === 'th'">ชื่อห้องปฏิบัติการ (อังกฤษ)</p>
                        <p class="language" v-else>Lab Name (English)</p>
                    </label>
                    <input type="text" id="name_en" class="form-control" v-model="name_en" />
                    <p class="error" v-if="errorMessage.name_en">{{ errorMessage.name_en[0] }}</p>
                </div>
                </div>
            </div>

            <div class="form-outline mb-4">
                <label class="form-label" for="description">
                    <p class="language" v-if="language === 'th'">รายละเอียดห้องปฏิบัติการ</p>
                    <p class="language" v-else>Lab Description</p>
                </label>
                <textarea class="form-control" id="description" rows="4" v-model="description"></textarea>
                <p class="error" v-if="errorMessage.description">{{ errorMessage.description[0] }}</p>
            </div>

            <div class="form-outline mb-4">
                <label class="form-label" for="externallinks">
                    <p class="language" v-if="language === 'th'">ลิ้งค์อิ่น ๆ ที่เกี่ยวข้อง</p>
                    <p class="language" v-else>External Links</p>
                </label>
                <textarea class="form-control" id="external_links" rows="4" v-model="external_links"></textarea>
                <p class="error" v-if="errorMessage.external_links">{{ errorMessage.external_links[0] }}</p>
            </div>
            
            <div class="form-outline mb-4">
                <label class="form-label" for="url_id">URL ID</label>
                <input type="text" id="url_id" class="form-control" v-model="url_id" />
                <p class="error" v-if="errorMessage.url_id">{{ errorMessage.url_id[0] }}</p>
            </div>

            <div class="form-check d-flex justify-content-start mb-4">
                <input
                class="form-check-input mr-2"
                type="checkbox"
                value=""
                id="isAcceptingRequest"
                v-model="is_accepting_requests"
                />
                <label class="form-check-label" for="isAcceptingRequest">
                    <p class="language" v-if="language === 'th'">กำลังรับคำขอ?</p>
                    <p class="language" v-else>Is accepting request?</p>
                </label>
                <p class="error" v-if="errorMessage.is_accepting_requests">{{ errorMessage.is_accepting_requests[0] }}</p>
            </div>

            <div class="form-outline mb-4">
                <label class="form-label" for="room">
                    <p class="language" v-if="language === 'th'">ห้อง</p>
                    <p class="language" v-else>Room</p>
                </label>
                <input type="text" id="room" class="form-control" v-model="room" />
                <p class="error" v-if="errorMessage.room">{{ errorMessage.room[0] }}</p>
            </div>

            <div class="form-outline mb-4">
                <label class="form-label" for="foundeddate">
                    <p class="language" v-if="language === 'th'">วันที่ก่อตั้ง</p>
                    <p class="language" v-else>Founded Date</p>
                </label>
                <input type="date" id="founded_date" class="form-control" v-model="founded_date" placeholder="dd-mmm-yyyy" />
                <p class="error" v-if="errorMessage.founded_date">{{ errorMessage.founded_date[0] }}</p>
            </div>

            <div class="form-outline mb-4">
                <label class="form-label">
                    <p class="language" v-if="language === 'th'">สถานะห้องปฏิบัติการ</p>
                    <p class="language" v-else>Lab Status</p>
                </label>
                <select id="status" class="form-control select" v-model="status">
                    <option v-for="option in options" :key="option.id" :value="option.value">
                        <p class="language" v-if="language === 'th'">{{ option.text_th }}</p>
                        <p class="language" v-else>{{ option.text }}</p>
                    </option>
                </select>
                <p class="error" v-if="errorMessage.status">{{ errorMessage.status[0] }}</p>
            </div>

            <div class="form-outline mb-4">
                <label class="form-label" for="tags">
                    <p class="language" v-if="language === 'th'">ที่เกี่ยวข้อง</p>
                    <p class="language" v-else>Tags</p>
                </label>
                <input type="text" id="tags" class="form-control" v-model="tags" />
                <p class="error" v-if="errorMessage.tags">{{ errorMessage.tags[0] }}</p>
            </div>
            

            <!-- Submit button -->
            <div>
                <button @click="sendData" type="submit" class="btn btn-primary btn-block" v-if="checkEdit">
                    <p class="language" v-if="language === 'th'">บันทึกการแก้ไข</p>
                    <p class="language" v-else>Save Changes</p>
                </button>
                <button @click="sendData" type="submit" class="btn btn-primary btn-block" v-else>
                    <p class="language" v-if="language === 'th'">สร้างห้องปฏิบัติการ</p>
                    <p class="language" v-else>Create Lab</p>
                </button>
                <div v-if="checkEdit" class="d-flex justify-content-center seperator"></div>
                <button @click="check1deleteData" type="submit" class="btn btn-outline-danger btn-block mt-4" v-if="checkDelete">
                    <p class="language" v-if="language === 'th'">ลบห้องปฏิบัติการ</p>
                    <p class="language" v-else>Delete Lab</p>
                </button>
                <button type="submit" class="btn btn-outline-secondary btn-block mt-4" v-else-if="checkEdit" disabled>
                    <p class="language" v-if="language === 'th'">ลบห้องปฏิบัติการ</p>
                    <p class="language" v-else>Delete Lab</p>
                </button>
            </div>
        </div>
        <div class="col-md"></div>
        <!-- Modal -->
        <the-alert :id="'alert'" :closetype="modalCloseType" :success="modalSuccess" :loading="modalLoading" :text="modalText" :url="modalUrl">
            <template v-slot:header></template>
        </the-alert>

        <the-confirm :id="'check1'">
            <h4 class="d-flex justify-content-center">
                <p class="language" v-if="language === 'th'">ต้องการลบห้องปฏิบัติการ?</p>
                <p class="language" v-else>Delete the lab?</p>
            </h4>
            <template v-slot:footer>
                <button data-dismiss="modal" class="btn btn-danger mr-4" style="width: 75px;" @click="check2deleteData()">
                    <p class="language" v-if="language === 'th'">ใช่</p>
                    <p class="language" v-else>Yes</p>
                </button>
                <button data-dismiss="modal" class="btn btn-outline-secondary ml-4" style="width: 75px;">
                    <p class="language" v-if="language === 'th'">ไม่</p>
                    <p class="language" v-else>No</p>
                </button>
            </template>
        </the-confirm>
        
        <the-confirm :id="'check2'">
            <h4 class="align-self-center">
                <p class="language" v-if="language === 'th'">การลบห้องปฏิบัติการจะส่งผลให้ลบข้อมูลอื่น ๆ ที่เกี่ยวข้องกับห้องปฏิบัติการ สมาชิกห้องปฏิบัติการอาจต้องการเก็บข้อมูลนั้นไว้</p>
                <p class="language" v-else>Delete the lab will delete all the event and other things related. Your lab member might want to keep the data.</p>
            </h4>
            <template v-slot:footer>
                <button data-dismiss="modal" class="btn btn-danger mr-4" style="width: 125px;" @click="check3deleteData()">
                    <p class="language" v-if="language === 'th'">ใช่ ลบมันซะ</p>
                    <p class="language" v-else>Yes, delete it.</p>
                </button>
                <button data-dismiss="modal" class="btn btn-outline-secondary ml-4" style="width: 125px;">
                    <p class="language" v-if="language === 'th'">ไม่</p>
                    <p class="language" v-else>No</p>
                </button>
            </template>
        </the-confirm>
        
        <the-confirm :id="'check3'">
            <h4 class="d-flex justify-content-center">
                <p class="language" v-if="language === 'th'">เช็ครอบสุดท้ายคุณแน่ใจว่าต้องการลบจริง ๆ ใช่ไหม คุณตั้งสถานะเป็นปิดรับหรือยุบตัวแทนที่จะลบได้นะ</p>
                <p class="language" v-else>Last check are you sure you want to delete it? You can just set to Closed or Disbanded.</p>
            </h4>
            <template v-slot:footer>
                <button data-dismiss="modal" class="btn btn-danger mr-4" style="width: 170px;" @click="deleteData()">
                    <p class="language" v-if="language === 'th'">ใช่ ฉันแน่ใจแล้ว</p>
                    <p class="language" v-else>Yes, I'm really sure.</p>
                </button>
                <button data-dismiss="modal" class="btn btn-outline-secondary ml-4" style="width: 170px;">
                    <p class="language" v-if="language === 'th'">ไม่</p>
                    <p class="language" v-else>No</p>
                </button>
            </template>
        </the-confirm>
    </div>
</template>
<script>
import Cropper from 'cropperjs';
import axios from 'axios';
import { $http } from '../../assets/variables.js';
import TheAlert from '../modal/TheAlert.vue';
import TheConfirm from '../modal/TheConfirm.vue';

export default {
    components: {
        TheAlert,
        TheConfirm
    },
    props: ['labid'],
    data () {
        return {
            cropper: {},
            image: {},
            preview: null,
            token: '',
            info: '',
            current_logo: null,
            current_banner: null,
            name_th: '',
            name_en: '',
            url_id: '',
            description: '',
            external_links: '',
            logo: null,
            banner: null,
            is_accepting_requests: false,
            room: '',
            founded_date: '',
            status: 'R',
            tags: '',
            options: [
                { id:0, value: '', text: 'Please Select One', text_th: 'กรุณาเลือกอย่างใดอย่างหนึ่ง' },
                { id:1, value: 'R', text: 'Recruiting', text_th: 'เปิดรับ' },
                { id:2, value: 'C', text: 'Closed', text_th: 'ปิดรับ' },
                { id:3, value: 'D', text: 'Disbanded', text_th: 'ยุบตัว' }
            ],
            errorMessage: '',
            createButtonText: 'Create Lab',
            editButtonText: 'Save Changes',
            errorTexted: [],
            cBanner: false,
            modalCloseType: '',
            modalLoading: false,
            modalSuccess: false,
            modalText: '',
            modalUrl: '',
            language: '',
        }
    },
    methods: {
        errorText(){
            var error;
            for(error in this.errorMessage){
                console.log(error);
                var element = document.getElementById(error);
                element.classList.toggle('error-box');
            }
            this.errorTexted = this.errorMessage;
        },
        cancelError(){
            var error;
            for(error in this.errorTexted){
                console.log(error);
                var element = document.getElementById(error);
                element.classList.toggle('error-box');
            }
            this.errorMessage = [];
        },
        cancelPicture(){
            this.logo = null;
            this.current_logo = null;
            this.preview = null;
        },
        cancelBanner(){
            this.banner = null;
            this.current_banner = null;
            this.cBanner = false;
        },
        bannerPic(event){
            this.banner = event.target.files[0];
            var reader = new FileReader();
            reader.onload = (e) => {
                this.current_banner = e.target.result;
            }
            reader.readAsDataURL(this.banner)
            this.cBanner = true;
        },
        logoPic(event){
            this.logo = event.target.files[0];
            var reader = new FileReader();
            reader.onload = (e) => {
                this.current_logo = e.target.result;
            }
            reader.readAsDataURL(this.logo);
            setTimeout(() => {
                this.image = this.$refs.image;
                this.cropper = new Cropper(this.image, {
                    zoomable: false,
                    scalable: false,
                    aspectRatio: 1,
                    crop: () => {
                        const canvas = this.cropper.getCroppedCanvas();
                        this.preview = canvas.toDataURL("image/png");
                        canvas.toBlob((blob) => {
                            this.logo = blob
                        })
                        // this.logo = canvas.toDataURL("image/png");
                    }
                })
            }, 1000);
        },
        check1deleteData(){
            window.$('#check1').modal('show');
        },
        check2deleteData(){
            setTimeout(() => { window.$('#check2').modal('show'); }, 1000);
        },
        check3deleteData(){
            setTimeout(() => { window.$('#check3').modal('show'); }, 1000);
        },
        deleteData() {
            this.modalCloseType = '';
            this.modalLoading = true;
            this.modalSuccess = false;
            this.modalText = 'Loading...';
            this.modalUrl = '';
            window.$('#alert').modal('show');
            axios.delete($http + 'api/community/lab/' + this.labid, {
                headers: {
                    Authorization: this.token
                }
            }).then(() => {
                setTimeout(() => {
                    this.modalCloseType = 'redirect';
                    this.modalLoading = false;
                    this.modalSuccess = true;
                    if (this.language === 'th') {
                        this.modalText = 'ลบห้องปฏิบัติการเรียบร้อยและข้อมูลทุกอย่างสูญหายไปตลอดกาล';
                    } else {
                        this.modalText = 'The lab is deleted and all the data is lost forever.';
                    }
                    this.modalUrl = '/labs';
                }, 3000);
            }).catch(() => {
                this.modalCloseType = '';
                this.modalLoading = false;
                this.modalSuccess = false;
                if (this.language === 'th') {
                    this.modalText = 'เกิดข้อผิดพลาดระหว่างลบห้องปฏิบัติการ';
                } else {
                    this.modalText = "Failed to delete lab.";
                }
                this.modalUrl = '';
            });
        },
        sendData() {
            this.modalCloseType = '';
            this.modalLoading = true;
            this.modalSuccess = false;
            this.modalText = 'Loading...';
            this.modalUrl = '';
            window.$('#alert').modal('show');
            this.cancelError();
            this.token = window.$cookies.get('token');
            if(this.token === null){
                this.token = window.$cookies.get("itkmitlcommunitytokenbrowser");
            }
            const data = new FormData();
            data.append('name_th', this.name_th);
            data.append('name_en', this.name_en);
            data.append('url_id', this.url_id);
            data.append('description', this.description);
            data.append('external_links', this.external_links);
            if (this.logo !== null){
                data.append('logo', this.logo, 'logo.png');
            }
            if (this.banner !== null){
                data.append('banner', this.banner);
            }
            data.append('is_accepting_requests', this.is_accepting_requests);
            data.append('room', this.room);
            data.append('founded_date', this.founded_date);
            data.append('status', this.status);
            data.append('tags', this.tags);
            // var data = {
            //     name_th: this.name_th,
            //     name_en: this.name_en,
            //     url_id: this.url_id,
            //     description: this.description,
            //     external_links: this.external_links,
            //     logo: this.logo,
            //     banner: this.banner,
            //     is_accepting_requests: this.is_accepting_requests,
            //     room: this.room,
            //     founded_date: this.founded_date,
            //     status: this.status,
            //     tags: this.tags
            // };
            if(this.labid !== 'create'){
                this.editButtonText = 'Loading.....';
                axios.patch($http + 'api/community/lab/' + this.labid + '/', data, {
                    headers: {
                        Authorization: this.token
                    }
                }).then((response) => {
                    if (response) {
                        this.modalCloseType = 'redirect';
                        this.modalLoading = false;
                        this.modalSuccess = true;
                        if (this.language === 'th') {
                            this.modalText = 'แก้ไขห้องปฏิบัติการเรียบร้อย';
                        } else {
                            this.modalText = 'Lab edited successfully.';
                        }
                        this.modalUrl = '/community/lab/' + this.url_id;
                    }
                }).catch((err) => {
                    this.modalCloseType = '';
                    this.modalLoading = false;
                    this.modalSuccess = false;
                    if (this.language === 'th') {
                        this.modalText = 'เกิดข้อผิดพลาดระหว่างแก้ไขห้องปฏิบัติการ'
                    } else {
                        this.modalText = 'Failed to edit lab.';
                    }
                    this.modalUrl = '';
                    this.errorMessage = err.response.data;
                    this.editButtonText = 'Save Changes';
                    this.errorText();
                });
            } else {
                this.createButtonText = 'Loading.....';
                axios.post($http + 'api/community/lab/', data, {
                    headers: {
                        Authorization: this.token
                    }
                }).then(() => {
                    this.modalCloseType = 'redirect';
                    this.modalLoading = false;
                    this.modalSuccess = true;
                    if (this.language === 'th') {
                        this.modalText = 'สร้างห้องปฏิบัติการเรียบร้อย';
                    } else {
                        this.modalText = 'Lab created successfully.';
                    }
                    this.modalUrl = '/community/lab/' + this.url_id;
                }).catch((err) => {
                    this.modalCloseType = '';
                    this.modalLoading = false;
                    this.modalSuccess = false;
                     if (this.language === 'th') {
                        this.modalText = 'เกิดข้อผิดพลาดระหว่างสร้างห้องปฏิบัติ';
                    } else {
                        this.modalText = 'Failed to create lab.';
                    }
                    this.modalUrl = '';
                    this.errorMessage = err.response.data;
                    this.createButtonText = 'Create Lab';
                    this.errorText();
                });
            }
        },
        getData() {
            this.language = window.$cookies.get('itkmitlcommunitylanguage');
            this.token = window.$cookies.get('token');
            if(this.token === null){
                this.token = window.$cookies.get("itkmitlcommunitytokenbrowser");
            }
            if(this.labid !== 'create'){
                axios.get($http + 'api/community/lab/' + this.labid, {
                    headers: {
                        Authorization: this.token
                    }
                }).then((response) => {
                    this.info = response.data
                    if(this.info.meta.available_actions.find((action) => action === 'edit') !== 'edit'){
                        this.$router.push('/lab/' + this.info.id)
                    } else {
                        this.name_th = this.info.name_th
                        this.name_en = this.info.name_en
                        this.url_id = this.info.url_id
                        this.description = this.info.description
                        this.external_links = this.info.external_links
                        this.current_logo = this.info.logo
                        this.current_banner = this.info.banner
                        this.is_accepting_requests = this.info.is_accepting_requests
                        this.room = this.info.room
                        this.founded_date = this.info.founded_date
                        this.status = this.info.status
                        this.tags = this.info.tags
                    }
                }).catch(() => {
                    this.modalCloseType = 'close';
                    this.modalLoading = false;
                    this.modalSuccess = false;
                    if (this.language === 'th') {
                        this.modalText = 'เกิดข้อผิดพลาดระหว่างดึงข้อมูลห้องปฏิบัติการปัจจุบัน'
                    } else {
                        this.modalText = 'Failed to get current lab data.';
                    }
                    this.modalUrl = '';
                    window.$('#alert').modal('show');
                });
            } else if (this.token === null || this.token === undefined) {
                this.$router.back();
            } else {
                try{
                    axios.get($http + 'api/user/user/me', {
                        headers: {
                            Authorization: this.token
                        }
                    }).then((response) => {
                        if(response.data.is_lecturer === false) {
                            this.$router.back()
                        }
                    });
                } catch (err) {
                    console.log(err);
                }
            }
            try {
                axios.get($http + 'api/category/lab-type', {
                    headers: {
                        Authorization: this.token
                    }
                }).then((response) => {
                    this.labtype = response.data
                });
            } catch (err) {
                (console.log(err))
            }
        }
    },
    async mounted(){
        this.getData();
    },
    computed: {
        checkDelete() {
            if(this.info.meta === undefined){
                return null
            }
            if(this.info.meta.available_actions === undefined) {
                return false
            } else {
                if(this.info.meta.available_actions.find((action) => action === 'delete') === 'delete') {
                    return true;
                } else {
                    return false;
                }
            }
        },
        checkEdit() {
            if(this.info.meta === undefined){
                return null
            }
            if(this.info.meta.available_actions === undefined) {
                return false
            } else {
                if(this.info.meta.available_actions.find((action) => action === 'edit') === 'edit') {
                    return true;
                } else {
                    return false;
                }
            }
        },
    }
}
</script>
